export const hymnal = [
{id:1,title:"1  .- GEDEON Y SUS TRESCIENTOS ",anthem:"1.- GEDEON Y SUS TRESCIENTOS \n \n Geden y sus trescientos, \n En el Valle de Mor, \n Vencieron a las huestes, \n De enemigos de Israel, \n Geden y sus trescientos, \n Con su santa impavidez, \n Eran pocos, pero leales \n A los mandatos del Seor. \n CORO \n Tocad la trompeta! \n Quebrad las jarras! \n Las antorchas en las manos levantad, \n Gritad! \n //: La espada de Jehov y de Geden! :/// \n Vamos a vencer. \n -2- \n Geden y sus trescientos \n Todos llenos de valor, \n A la diestra del Caudillo \n Jehov, el Guiador. \n Con las armas invencibles, \n Y todo en derredor, \n Con antorchas y trompetas \n Obedientes al Seor. \n ",track:"11g-lErJVttxe0R8TwQIltsXvgNqYZUof",demo:"1m8cBCKxtn-6ru2S4gEc40zH6X2ghFkSj"},
{id:2,title:"  2  .- ACOGIDA DE JESUS  ",anthem:"2.- ACOGIDA DE JESS \n \n Al que en busca de la luz \n Vague ciego y con temor, \n Lo recibe el buen Jess \n En los brazos de su amor. \n CORO \n Volveremos a cantar \n Cristo acoge al pecador, \n Claro hacedlo resonar \n Cristo acoge al pecador. \n -2- \n A sus pies descansars \n Ejercita en l tu fe. \n De tus males sanars \n A Jess, tu amigo, ve. \n -3- \n Hazlo, porque as dirs: \n Ya no me condenar \n Ya la ley no pide ms \n En Jess vivirla se. \n -4- \n Acogerte prometi. \n Date prisa en acudir. \n Necesitas como yo, \n Vida que l te har vivir. \n ",track:"13kETaWTxHSCiQQv4SEumU9aFd_mowD_4",demo:"1LcBbnN79TSexnUxPmfuRrsNotAi93ba-"},
{id:3,title:"  3  .- A CUALQUIERA PARTE  ",anthem:" 3.- A CUALQUIERA PARTE \n \n A cualquiera parte sin temor ir \n Si Jess dirige mi inseguro pie \n Sin su compaa todo es pavor, \n Mas si l me gua, no tendr temor. \n CORO \n Con Jess, por doquier, sin temor ir \n Si Jess me gua, nada temer. \n -2- \n Con Jess por gua donde quiera voy, \n Caminando en pos de l seguro estoy, \n Y aunque padre y madre me pueden faltar, \n Jams Jesucristo me despreciar. \n -3- \n Donde quiera con Jess en tierra y mar,\n Quiero ser su fiel testigo sin cesar: \n Y si por desierto mi camino va, \n Un seguro albergue mi Jess ser. \n -4- \n Donde quiera siento yo su proteccin, \n Porque siempre oigo su benigna voz \n l de da y noche a mi lado est, \n Y en plena gloria me despertar. ",track:"1sS__F9wKxkow5_HXREkUmowPaHNLJYDq",demo:"1WchOPaiPvSH9VSdkyp17TIU2rfGeaKnk"},
{id:4,title:"  4  .- ALELUYA ",anthem:" 4.- ALELUYA! \n \n Aleluya! Aleluya! Aleluya! \n Aleluya! Aleluya! Aleluya! \n Aleluya! Aleluya! Aleluya! \n Aleluya! Aleluya! Aleluya! \n Amn, Aleluya! Amn, \n Aleluya! Amn, Aleluya! Amn, \n Aleluya! Amn, Amn, Amn. \n ",track:"1T9ZqcBVrTIwZCiOWAD8NvovrFVGM5_hb",demo:"1129T7YtDJUbhuGKFFhM8kleQ1y5emQ06"},
{id:5,title:"  5  .- ADELANTE  ",anthem:" 5.- ADELANTE \n \n Adelante, vamos a luchar, \n Huestes del Seor, a batallar, \n Y hacia el enemigo avanzar, \n Pues lucharemos hasta conquistar. \n CORO \n Con el gran poder de Jesucristo \n Lucharemos, lucharemos, \n Y al cruel enemigo pelearemos, \n Hasta conquistar. \n -2- \n Adelante, no te d temor, \n Cristo el Capitn nos da valor,\n  S valiente con fe y sin temor,\n  La lucha sigue vamos a vencer.\n -3- \n Adelante, vamos a luchar \n Por la grata luz y la verdad, \n La victoria hemos de cantar, \n Pues adelante ya sin vacilar. \n -4- \n Adelante, nos espera all \n La corona que Jess dar, \n A los que con fiel solicitud \n Sus vidas dieron por la grata luz.\n ",track:"14yK_MKfcgL8xnu-5EAlaaSkqgr7h2VlK",demo:"12LjuWkpKm02mjwoGK8m_bLe7BhbIXSP5"},
{id:6,title:"  6  .- ADELANTE GEDEONISTAS ",anthem:" 6.- ADELANTE, GEDEONISTAS \n \n Por los valles, por los montes, \n El mensaje proclamad: \n Pronto viene el Rey de gloria \n Trayendo corona de vida eternal. \n CORO \n Adelante! Adelante! \n Gedeonistas, avanzad! \n Pronto viene el Rey de gloria \n Trayendo en sus alas salud eternal.\n  -2-\n  Predicad por los collados: \n Nuestro Rey se acerca ya. \n Anunciad al pueblo ciego \n Que cerca est el da, que Cristo vendr. \n -3- \n Gedeonistas, adelante \n Trabajad por rescatar \n A las almas que en tinieblas \n Perecen por falta de luz celestial. \n ",track:"1vgEgMS6wI2nNWbNg9LwP7oG93jXCl4SX",demo:"19vZm5m8_eMS3iawtNc7e4q3KO5vxK2D_"},
{id:7,title:"  7  .- ADELANTE JUVENTUD  ",anthem:" 7.- ADELANTE JUVENTUD \n \n Oh! juventud cristiana, ve adelante \n Luchando por salvar los pecadores \n Que el galardn del cielo has de ganarte,\n  Y morars con el Seor de los seores.\n CORO \n No dejes que otro tome tu corona, \n Que Cristo ha preparado para Ti. \n Qu ser para todos esa aurora \n Cuando la trompeta suene para el fin? \n -2- \n Si la tribulacin suele alcanzarte \n Y el enemigo cruel tus pasos vela, \n Da una mirada al cielo y al instante \n El Seor Jesucristo te consuela. \n -3- \n Unidos vamos todos al combate \n Lavados por la sangre de Jess \n Que en el Calvario quiso derramarla \n Y llev nuestros pecados en la cruz. \n ",track:"19qA7oOXskcqNE9ntTIbh4PVYLmyJq2lQ",demo:"1982bMq9IkJlLyT42W8oTW2NTOINfhANh"},
{id:8,title:"  8  .- A DIOS PIADOSO ",anthem:" 8.- A DIOS PIADOSO \n \n A Dios piadoso deb el nacer, \n l me dio padres para mi bien \n Me da alimento, templa mi sed, \n Buenos seamos, que Dios nos ve!\n  -2- \n Dios hizo el cielo con su poder \n Hizo la tierra y el mar tambin \n El sol y estrellas brillan por l, \n Buenos seamos, que Dios nos ve! \n -3- \n Si el desvalido pide merced, \n Si al triste aflige suerte cruel: \n Ese que llora tu hermano es \n Buenos seamos, que Dios nos ve! \n -4- \n No al malo envidies aunque tal vez \n Impune ostente gloria y poder \n Que all en el cielo hay otro juez \n Buenos seamos, que Dios nos ve! \n ",track:"1QbesKZnVTrlrPhe46erCrFqnFFWTqrVv",demo:"1G2ywLPBNhR6V7FC3SUReEbASjAVyY1dE"},
{id:9,title:"  9  .- ALABADO EL GRAN MANANTIAL  ",anthem:" 9.- ALABADO EL GRAN MANANTIAL \n \n Alabado el gran manantial \n Que de sangre Dios nos mostr! \n Alabado el Rey que muri \n Su pasin nos libra del mal! \n Lejos del redil de mi Dueo \n Vime msero, pequeo, vil. \n El Cordero sangre verti \n Me limpia slo este raudal. \n CORO \n S que solo as \n Me emblanquecer, \n Lvame en tu sangre, Cordero Jess, \n Y nvea blancura tendr. \n  -2- \n La punzante insignia llev, \n En la cruz dej de vivir, \n Grandes males quiso sufrir \n No en vano Cristo sufri. \n Al gran manantial conducido \n Que de mi maldad ha sido fin, \n Lvame, le pude decir \n Y nvea blancura me dio. \n -3- \n Padre, de Ti lejos vagu, \n Extravise mi corazn, \n Como grana mis culpas son, \n No con agua limpio ser. \n A tu fuente magna acud, \n Tu promesa creo, Oh, Jess! \n La eficaz virtud de tu don \n La nvea blancura me d. ",track:"19pbkKTk0hd7VHxMUA4pu9qX73NqlwxbC",demo:"1WOBJf1AE3jteDvn5RNNl7t5lmhP5EahM"},
{id:10,title:"  10 .- ALELUYA EN MI ALMA  ",anthem:" 10.- ALELUYA EN MI ALMA \n \n Siento en mi alma una gloriosa bendicin, \n Don perfecto puso en mi alma \n Desde que Cristo en mis maldades me busc \n Y hallndome El me salv. \n CORO \n Del fondo de mi alma \n Surgi un aleluya, \n Aleluya, aleluya, \n Y un coro de millones \n Contest en la altura: \n Aleluya!, Gloria a Dios!. \n -2- \n Cuando en tinieblas y pecado yo vagu, \n Con tristeza en mi alma, \n Por mi salud a Jesucristo yo clam \n Y mi carga a l le entregu. \n -3- \n Si persevero hasta el fin con el Seor, \n A la gloria va mi alma, \n Y mi bandera voy alzando con valor, \n Porque en l slo hay salvacin. ",track:"1dOJHLdM5gyAy3z74jLf_X5jyUSQgyH7X",demo:"1Fqt_ImNxlt_W2dd8_RU5DdO-7vLeHU0h"},
{id:11,title:"   11 .- ALGUNA VEZ YA NO ESTARE  ",anthem:" 11.- ALGUNA VEZ YA NO ESTAR \n \n Alguna vez ya no estar \n En mi lugar, en esta grey. \n Mas cun feliz despertar \n En el palacio de mi Rey! \n CORO \n Yo le ver y en dulce amor, \n Ir a vivir con l all. \n Y le dir: Mi buen Seor, \n Por gracia yo salvado fui. \n -2- \n Alguna vez la muerte atroz \n Vendr, mas cundo no lo se, \n Pero esto se: Con mi buen Dios \n Un sitio yo feliz tendr. \n -3- \n Alguna vez yo, como el sol, \n Mi ocaso y fin tendr tambin \n Mas me dir mi buen Seor: \n Mi siervo fiel, conmigo ven. \n -4- \n El da feliz que espero yo, \n Con mi candil ardiendo ya, \n Las puertas me abrir el Seor \n Y mi alma a l con gozo ir. ",track:"14p0qpp_JxichzjHaoXOrYUnBkxlYjFSY",demo:"17mT2wT5pTspxy0nkYaO1UGfbGXAWAQ4G"},
{id:12,title:"  12 .- ALMA ATRIBULADA  ",anthem:" 12 .- ALMA ATRIBULADA \n \n Pobre mendigo, est junto al camino \n Ciego que pide al mundo, caridad, \n Cuando Jess le cambia su destino \n Al darle vista y plena claridad. \n CORO \n Cristo al llegar al alma atribulada, \n El llanto enjuga, ahuyenta su dolor. \n La tentacin del mal es rechazada, \n Y cambia todo en gozo el Salvador. \n -2- \n Inmundo soy, clamaba atormentado. \n Pobre leproso con dolor mortal \n En penas mil el sordomudo estaba, \n Cuando al cautivo Cristo fue a librar. \n -3- \n En los sepulcros, pobre endemoniado, \n Lloraba solo, triste y sin hogar. \n A su querer Satn le arrojaba, \n Viene Jess, y quita todo mal. ",track:"161OCO-wgWqZmy39-FydmkF-QbLLse0ST",demo:"1Sor4mjg8F2KQGNuADYYhB-qb49aqbTMG"},
{id:13,title:"  13 .- GRANDE GOZO HAY EN MI ALMA HOY ",anthem:" 13.- GRANDE GOZO HAY EN MI ALMA \n \n Grande gozo hay en mi alma hoy, \n Pues Jess conmigo est \n Y su paz, que ya gozando estoy \n Por siempre durar. \n CORO \n Grande gozo, Cun hermoso! \n Paso todo el tiempo bien feliz \n Porque veo de Cristo la sonriente faz,\n  Grande gozo siento en m. \n -2- \n Hay un canto en mi alma hoy, \n Melodas a mi Rey \n En su amor feliz y libre soy \n Y salvo por la fe. \n -3- \n Paz divina hay en mi alma hoy, \n Porque Cristo me salv. \n Las cadenas rotas ya estn \n Jess me libert. \n -4- \n Gratitud hay en mi alma hoy, \n Y alabanzas a Jess. \n Por su gracia a la gloria voy, \n Gozndome en la luz. ",track:"1rROgmH2yRTH8Li-yVg_3g1Pfs7EjtzzV",demo:"1TM2QqqsvBl62hl40b3wg2LWIb7zi2IuF"},
{id:14,title:"  14 .- AL MUNDO ADIOS ",anthem:" 14.- AL MUNDO ADIS \n \n Anhela mi alma la tranquilidad \n Que slo en Cristo tendr en verdad, \n El fin de mi vida llegando est \n Al mundo adis, buenos das all. \n CORO \n Al mundo adis, al cielo yo voy \n En gloria feliz cantando estoy \n Vivo donde es luz el bendito Jess, \n Al mundo adis, vivo hoy con Jess. \n -2- \n Con Cristo no hay que temer el morir \n Con El s que siempre yo voy a vivir. \n Si cierra mis ojos la muerte ac, \n Al mundo adis, buenos das all. \n -3- \n Un da vendr el amado Seor \n Los suyos El trasladar con amor \n Sin muerte, sin duelo, al cielo irn. \n Al mundo adis, buenos das all. ",track:"1Dfe5NWuY7MsZZRjQvBr2ZNHNY9haQxvV",demo:"1vmzjlUAQCIu7Ia3Qw_7gvugITWa0oQEt"},
{id:15,title:"  15 .- ALZA TU CANTO  ",anthem:" 15.- ALZA TU CANTO \n \n Vuelen al cielo los ecos santos \n Que arranco alegre de mi lad \n Vuelen al cielo mis dulces cantos, \n Mis dulces cantos de gratitud. \n CORO \n Alza tu canto, oh lengua ma! \n Alza tu canto, mi corazn. \n Llnese el alma de alegra, \n Con alegra de devocin. \n -2- \n Ya siento el fuego de los amores, \n De los amores del grato Edn.\n  Ya no me acosan crudos dolores \n Porque contemplo a Jerusalem. \n -3- \n Padre, en tu regia, santa morada \n Donde la dicha no tiene fin, \n All mi patria miro esmaltada \n De bellas flores de tu jardn. \n -4- \n Llvame, oh Padre, para consuelo \n Nada en la tierra yo espero ya \n Llvame al cielo, llvame al cielo, \n Que all tan slo mi dicha est. \n ",track:"1nHByUcHUvy-XAF91NCvpjUqQyEKi0spE",demo:"1cH1jPJLqAgi0guPUKs4v7S7uhe0FMLVb"},
{id:16,title:"  16 .- ALLA NO MAS DOLOR  ",anthem:" 16.- ALL NO MS DOLOR \n \n Las nubes vienen siempre \n En nuestra vida aqu, \n La senda es oscura, \n Se siente el miedo en s. \n Mas por la fe ya vemos \n El da sin afn, \n El da sin ms nubes \n Y sin ms huracn. \n CORO \n All . . . no ms dolor, \n All . . . eterno amor, \n All el da viene, \n Sin ms nubes, ms puro amor. \n -2- \n El mal que nos azota, \n No ms afligir, \n Las penas y las cargas \n No ms se acordarn \n Descanso y reposo, \n Salud, fuerza y vigor \n Tendremos en el cielo, \n Do reina el Seor. \n -3- \n Las caras de los nuestros \n Que hace mucho ya \n En muerte las perdimos, \n Vermoslas all. \n Con ellas en un coro \n Celeste y triunfal, \n Daremos gloria a Cristo \n Con himno celestial. \n ",track:"1Gh_1stSwWIL7QU8kvRdJAMA20_Vpl19b",demo:"1ZWJrhXNpsUcKi8KmPFCuf9fpKr0ujG1o"},
{id:17,title:"  17 .- ALLA VA EL EMBAJADOR ",anthem:" 17.- ALL VA EL EMBAJADOR \n \n All va el embajador, \n Con su rumbo a otros pases \n Sin importarle las crisis \n Ni el peligro ms traidor. \n All va el embajador, \n Muy valiente y decidido, \n Nunca creyendo perdido \n Su trabajo en el Seor. \n CORO \n Como la pea, como la roca, \n Sea tu firmeza, siervo de paz. \n Que si eres firme como la roca, \n Dios, tu prudencia, coronar. \n -2- \n All va el embajador, \n Enarbolando su bandera \n Por la cual Cristo muriera, \n El evangelio de amor. \n No le importa el terror \n Con que el diablo le amenace, \n Slo en su vida le place \n Ser de Cristo un seguidor. \n -3- \n Es embajador sin lmite \n De la gracia y de la ayuda, \n Con que Dios le estimula \n Y que en realidad existe. \n El rigor le dice, rndete, \n Y las pruebas le amenazan, \n Pero l como un alczar \n Con su cerrojo muy firme. \n ",track:"1d7PcuhTrHf9MxDwcmsA3yra-uBYTMhM8",demo:"1q1dSwtPQA5JMpYGmVBqG5C6nR_jXqO8o"},
{id:18,title:"  18 .- ALLI QUIERO IR Y TU?  ",anthem:" 18.- ALL QUIERO IR, Y T? \n \n Me dicen que hay una bella ciudad, \n All quiero ir, y t? \n En donde es eterna la felicidad, \n All quiero ir, y t? \n Jess las mansiones nos fue a preparar, \n All quiero ir, y t? \n A donde la muerte no puede llegar,\n  All quiero ir, y t? \n CORO \n // : All quiero ir, all quiero ir, \n All quiero ir, y t? : // \n -2- \n De perlas sus puertas, su mar de cristal, \n All quiero ir, y t? \n Es Cristo la luz del pas celestial, \n All quiero ir, y t? \n All los que moran jams morirn, \n All quiero ir, y t? \n En donde terminan congojas y afn, \n All quiero ir, y t? \n -3- \n Y cuando navegue la nave de Sin, \n All quiero ir, y t? \n Al darse a la vela para esa mansin, \n All quiero ir, y t? \n Y cuando coronen a Cristo tambin, \n All quiero estar, y t? \n Y cuando los coros pronuncien: Amn! \n All quiero ir, y t? \n ",track:"1DeSmeSx9gD3MazFjX52AvhADi9xER63a",demo:"12a-GmpmzDjB-CQUomo5iJLRDizrsJnt0"},
{id:19,title:"  19 .- AMA EL PASTOR SUS OVEJAS ",anthem:" 19.- AMA EL PASTOR SUS OVEJAS \n \n Ama el Pastor sus ovejas \n Con un amor paternal. \n Ama el Pastor su rebao \n Con un amor sin igual. \n Ama el Pastor a las otras \n Que descarriadas estn \n Y conmovido las busca \n Por donde quiera que van. \n CORO \n Por el desierto, errabundas,\n  Vense sufrir penas mil, \n Y al encontrarlas, en hombros, \n Llvalas tierno al redil. \n -2- \n Ama el Pastor sus corderos, \n malos, tierno, el Pastor. \n A los que a veces, perdidos \n Se oyen gemir de dolor. \n Ved al Pastor conmovido, \n Por los collados vagar, \n Y los corderos en hombros \n Vedlo llevando al hogar. \n -3- \n Ama las noventa y nueve \n Que en el aprisco guard \n Ama las que descarriadas \n Por el desierto dej. \n Oh, mis ovejas perdidas! \n -Clama doliente el Pastor- \n Quines vendrn en mi ayuda \n Para salvarlas, Seor? \n -4- \n Son delicados tus pastos \n Y quietas tus aguas son: \n Henos aqu, Oh, Maestro! \n Danos hoy tu comisin. \n Haznos obreros fervientes, \n Llnanos de un santo amor \n Por las ovejas perdidas \n De tu redil, buen Seor. \n ",track:"1PVKCEebJ3yxuvtiJUX1Kr-WkZ5-Vdw_L",demo:"11Ddl8BOICec-Qn7A3X0TRWsHFWOWHa2h"},
{id:20,title:"  20 .- AMOR FRATERNAL  ",anthem:" 20.- AMOR MATERNAL \n \n Los aos de mi infancia \n Me recuerdan con dolor \n Que a veces despreciaba \n De mi madre el tierno amor. \n Ahora que se ha ido \n Anhelo su solicitud, \n Dile, Oh, Seor! que en gloria la ver. \n CORO \n Oh, madre de mi amor \n En gloria te ver, \n Tu tierno amor jams olvidar. \n En la mansin de paz, \n Ver tu dulce faz \n Y junto a Ti por siempre morar. \n -2- \n Por ms que anduve errante yo \n Por sendas de maldad, \n Mi cariosa madre \n Me trataba con bondad. \n Mis penas infantiles \n Endulzaba con amor, \n Dile, Oh, Seor! que en gloria la ver. \n -3- \n Al verme solo y lejos \n De mi hogar, mi dulce hogar, \n Con su angustiado corazn \n Lloraba sin cesar. \n Y da y noche oraba a Dios \n Por m con grande fe, \n Dile, Oh, Seor! que en gloria la ver. \n -4- \n Infausta nueva un da \n Me lleg diciendo: Ven, \n Si quieres a tu madre ver, \n Que a descansar ya va. \n Llor cuando en los brazos \n De la muerte la encontr \n Y dije: Oh, madre! en gloria te ver. \n ",track:"1Zk-B_l5fhMmuTrn9qwV4uDyIrdWkBfTI",demo:"1CCnUEUnQEeulm9Au-Uc2iP-RN5qa1ZoM"},
{id:21,title:"  21 .- A SOLAS CON JESUS  ",anthem:" 21.- A SOLAS CON JESS \n \n A solas al huerto yo voy, \n Cuando duerme an la floresta. \n Y en quietud y en paz con Jess estoy \n Oyendo absorto all su voz. \n CORO \n l conmigo est, puedo or su voz.\n  Y que suyo, dice, ser. \n Y el encanto que hallo con l all, \n Con nadie tener podr. \n -2- \n Tan dulce es la voz del Seor \n Que las aves guardan silencio,\n  Y tan slo se oye esa voz de amor \n Que inmensa paz al alma da. \n -3- \n Con l, encantado yo estoy \n Aunque en torno lleguen las sombras, \n Mas me ordena ir y a escuchar yo voy \n Su voz doquier la pena est. \n ",track:"1t79EbFxqnNcceLi7jNxlVszAg6089KVE",demo:"152xmo-8EQEhB5X_1rs1gU3M1UKQOh4gs"},
{id:22,title:"  22 .- A TI Y A MI  ",anthem:" 22.- A TI Y A M \n \n Cun tiernamente nos est llamando \n Cristo a ti y a m. \n l nos espera con brazos abiertos, \n Llama a ti y a m. \n CORO \n Venid ... Venid ...\n  Si estis cansados, venid.\n  Cun tiernamente os est llamando! \n Oh, pecadores, venid! \n -2- \n Por qu tememos si est abogando, \n  Cristo por ti y por m? \n Sus bendiciones est derramando, \n Siempre por ti y por m.\n  -3- \n El tiempo vuela, lograrlo conviene \n Cristo te llama a ti \n Vienen las sombras y la muerte viene \n Vienen por ti y por m. \n ",track:"1tkWIb4SsBMh3Y_GAwIR_KXbGIyvKoqT5",demo:"1ir7jNdkSbDhN2JNd1BPpZMKl2D2KR9J_"},
{id:23,title:"  23 .- AUNQUE SEAN COMO ESCARLATA ",anthem:" 23.- AUNQUE SEAN COMO ESCARLATA \n \n Aunque sean como escarlata, \n Tus pecados borrar. \n Aunque sean rojos cual grana\n  Como nieve los har. \n S, aunque sean como escarlata\n  Yo los lavar. \n // : Aunque sean como escarlata : //\n  // : Tus pecados lavar : // \n -2- \n Oye voz que te suplica: \n Vuelve, vuelve a tu Seor.\n  Oye voz que te suplica, \n Vuelve, vuelve a tu Seor. \n Bueno es Dios, es compasivo \n Y de tierno amor. \n // : Oye voz que te suplica : // \n // : Vuelve, vuelve a tu Seor : // \n -3- \n  El aleja tus pecados \n Y su consecuencia atroz. \n El aleja tus pecados \n Y su consecuencia atroz.\n  Venid a m, pues, y sed salvos. \n Dice nuestro Dios. \n // : El aleja tus pecados : // \n // : Y su consecuencia atroz : // \n ",track:"1mxm6THsznVAWB7n2XUhAc8H0XMR77WSQ",demo:"1RgL388vFwuLbp-WlAbbwAJ1LYXllgcHG"},
{id:24,title:"  24 .- BELLAS MANITAS  ",anthem:" 24.- BELLAS MANITAS \n \n Bellas las manitas son \n Que obedecen a Jess. \n Bellos ojos los que estn \n Llenos de clica luz. \n CORO \n Bellas s, bellas las manos son \n Que obedecen a Jess. \n Bellos los ojos que siempre estn \n Llenos de clica luz. \n -2- \n Hizo las manitas Dios \n Porque a l podran servir, \n Hizo el tierno corazn \n En su servicio latir. \n -3- \n Toda boca debe orar \n Cada da al Salvador, \n Todo pie listo ha de estar \n Para ir do mande el Seor. \n -4- \n Cuanto puedas t hacer \n Cristo te lo exigir. \n Haz, pues, de ello tu placer \n Con el poder que l te da. \n ",track:"1KIAY8YOaC589BU4lXumKXoyJgiuGYU-e",demo:"1m8Ag1QKIjjFOUfTCcsISkYR7y6v7FYma"},
{id:25,title:"   25 .- BELLAS PALABRAS DE VIDA  ",anthem:" 25.- BELLAS PALABRAS DE VIDA \n \n Oh, cantdmelas otra vez, \n Bellas palabras de vida! \n Hallo en ellas mi gozo y luz, \n Bellas palabras de vida. \n S, de luz y vida, \n Son sostn y gua \n // : Qu bellas son, qu bellas son! \n Bellas palabras de vida : // \n -2- \n Jesucristo a todos da \n Bellas palabras de vida.\n  Hoy escchalas pecador, \n Bellas palabras de vida. \n Bondadoso te salva \n Y al cielo te llama \n // : Qu bellas son, qu bellas son! \n Bellas palabras de vida : // \n -3- \n Grato el cntico sonar, \n Bellas palabras de vida \n Tus pecados perdonar, \n Bellas palabras de vida. \n S, de luz y vida \n Son sostn y gua, \n // : Qu bellas son, qu bellas son! \n Bellas palabras de vida : // \n ",track:"1WsNbGV30w42PVgTK6VXaV0KFk33w_oXi",demo:"1aIZKUtPE7d7_GGL_0OpefxLShzd6D7Jv"},
{id:26,title:"  26 .- BORRADOS POR LA FE  ",anthem:" 26.- BORRADOS POR LA FE \n \n Mucho tiempo viv, \n Lejos de mi Salvador,\n  Agobiado por el tentador\n  Mas una voz o, \n Que me dijo: Ven a m,\n  Y l borr mis pecados por la fe.\n  CORO \n Son borrados por la fe, \n Son borrados por la fe. \n Mis pecados son borrados por la fe.\n  Con su sangre me limpi, \n Mis pecados l borr. \n Son borrados mis pecados por la fe.\n  -2- \n  No poda comprender, \n Por qu Cristo muri \n Por un vil pecador como yo.\n  Mas ahora bien s, \n Fue su grande amor, \n Que borr mis pecados por la fe. \n -3- \n De mis culpas tambin, \n Cristo ya me libr \n Y tambin El mi carga quit. \n Oh, qu amor recib, \n Cuando l me salv! \n Son borrados mis pecados por la fe.\n  -4- \n Ahora mi corazn \n Satisfecho est, \n Porque l me salv y me limpi. \n Gloria a Dios y a Jess \n Que su sangre verti! \n Son borrados mis pecados por la fe. \n ",track:"1rlktBi0evijS0JVqVD59lcJXfgkPPmEu",demo:"1nqpeb9MHSAfP3fC3Nclg_ElFVP3ZDtZZ"},
{id:27,title:"   27 .- BRILLA EN TU LUGAR ",anthem:" 27.- BRILLA EN TU LUGAR \n \n Nunca esperes el momento \n De una gran accin, \n Ni que pueda lejos ir tu luz, \n De la vida a los pequeos actos \n Da atencin, \n Brilla en el sitio donde ests.  \n CORO  \n Brilla en el sitio donde ests,  \n Brilla en el sitio donde ests. \n  Puedes con tu luz \n  Algn perdido rescatar, \n Brilla en el sitio donde ests.\n -2-  \n Puedes en tu cielo  \n Alguna nube disipar,  \n Haz a un lado tu egosmo cruel  \n Aunque slo un corazn  \n Pudieres consolar,  \n Brilla en el sitio donde ests.  \n -3-  \n Puede tu talento  \n Alguna cosa descubrir,  \n Do tu luz podr resplandecer  \n De tu mano el pan de vida,  \n Puede aqu venir,  \n Brilla en el sitio donde ests.  \n ",track:"1vIq0FnK-GT-XSKZuWy7J1el_6xzJ1kLv",demo:"1qjwxFnThljtfOXLZmF_PSUPlf9kWl43f"},
{id:28,title:"  28 .- BUENO ES DIOS  ",anthem:" 28.- BUENO ES DIOS  \n \n Juntos cantemos sin cesar:  \n Bueno es Dios.  \n Cielos y tierra proclamad:  \n Bueno es Dios.  \n Almas, despierten del pecar.  \n Cambien las penas por cantar, \n Cristo Jess puede salvar.  \n Bueno es Dios.  \n CORO  \n Bueno es Dios,  \n Bueno es Dios,  \n Juntos cantemos  \n Sin cansarnos,  \n Bueno es Dios.  \n -2-  \n Por todo el mundo proclamad:  \n Bueno es Dios.  \n Cristo nos trajo libertad:  \n Bueno es Dios.  \n Salvos por El de todo mal,  \n Danos poder pentecostal,  \n Vida perfecta celestial:  \n Bueno es Dios.  \n -3-  \n Dicha inefable da Jess:  \n Bueno es Dios.  \n El nos imparte santa luz:  \n Bueno es Dios.  \n El es escudo y protector,  \n El nos ayuda con amor,  \n Cantemos siempre su loor:  \n Bueno es Dios.  \n -4-  \n En el palacio celestial:  \n Bueno es Dios.  \n Libre de duelo terrenal:  \n Bueno es Dios.  \n Eternamente cantar,  \n A Jesucristo ensalzar,  \n Me ha redimido por la fe: Bueno es Dios. ",track:"15Qi36dZetpacBAJBAfVs-pmUg8WMLeVs",demo:"16n-sRNGchOhLRnWtxCh75Loy4FIb1sAQ"},
{id:29,title:"   29 .- BUEN SALVADOR  ",anthem:" 29.- BUEN SALVADOR  \n \n Buen Salvador, a Ti yo vengo  \n Llegando a Ti, tal como estoy.  \n Mi rebelin y mal te dejo,  \n Y ante tus pies postrado estoy. \n CORO  \n Usa mi vida, oh buen Jess, \n  Que deposito sobre tu altar,  \n T el Alfarero eres Seor,  \n Yo vil arcilla que habrs de usar.  \n -2-  \n Tu bendicin ansioso imploro,  \n Cansado estoy de batallar, \n  Mi voluntad completa rindo.  \n En Ti yo quiero descansar.  \n -3-  \n Venciste al fin, Oh Galileo,   \n Rendiste t mi corazn,  \n Duda y temor de el arrancaste.  \n Eres mi Rey, mi Salvador.  \n ",track:"1Y4vN9acHr49rfmExDTuPRMUl5I8Zi98a",demo:"1wImYreK8njTwyeFLcDUNsr25_95BVZIr"},
{id:30,title:"  30 .- CANTAR NOS GUSTA UNIDOS  ",anthem:" 30.- CANTAR NOS GUSTA UNIDOS  \n \n Cantar nos gusta unidos,  \n Cantar nos gusta unidos  \n Acordes y a una voz.  \n // : A nuestro eterno Padre : //  \n Y a su Hijo el Salvador.  \n Cun bueno es, cun bueno es  \n Cun bueno es cantar juntos!  \n Cun bueno es, cun bueno es  \n Cun bueno es loar a Dios!  \n -2-  \n Orar nos gusta unidos,  \n Orar nos gusta unidos  \n Con santa devocin.  \n // : A Cristo que nos haga : //  \n Aceptos en su amor.  \n Cun bueno es, cun bueno es,  \n Cun bueno es orar juntos! \n  Cun bueno es, cun bueno es  \n Cun bueno es loar a Dios!  \n  -3-  \n Leer nos gusta unidos,  \n Leer nos gusta unidos,  \n La fiel revelacin, \n  // : Que alumbra nuestros pasos : //  \n Con claro resplandor.  \n Cun bueno es, cun bueno es  \n Cun bueno es leer juntos!  \n Cun bueno es, cun bueno es,  \n Cun bueno es loar a Dios!  \n -4-  \n Estar nos gusta unidos,  \n Estar nos gusta unidos  \n En fe y adoracin.  \n // : Gozando las delicias : //  \n Del da del Seor.  \n Cun bueno es, cun bueno es, \n  Cun bueno es estar juntos!  \n Cun bueno es, cun bueno es  \n Cun bueno es loar a Dios! ",track:"1_BbU-PG971vJwnTvWDsVL6cZHFLjW4f1",demo:"1CpOABO21sv8BxOHXhbMA0NYK6rnv99gT"},
{id:31,title:"   31 .- CARA A CARA ",anthem:" 31.- CARA A CARA  \n \n En presencia estar de Cristo,  \n Ver su rostro, qu ser?  \n Cuando al fin en pleno gozo,  \n Mi alma le contemplar.  \n CORO  \n Cara a cara espero verle,  \n Ms all del cielo azul \n Cara a cara en plena gloria,  \n He de ver a mi Jess.  \n -2-  \n Slo tras oscuro velo,  \n Hoy le puedo aqu mirar.  \n Mas ya pronto viene el da,  \n Que su gloria ha de mostrar. \n  -3-  \n Cunto gozo habr con Cristo  \n Cuando no haya ms dolor  \n Cuando cesen los peligros  \n Y ya estemos en su amor!  \n -4-  \n Cara a cara, cun glorioso  \n Ha de ser as vivir.  \n Ver el rostro de quien quiso \n  Nuestras almas redimir!  \n ",track:"1iwtG1LBtZbWcVkL3pT6lVZ2qlupeHnwj",demo:"1jULX9sqLmQTdULv8BJgWSXeWva-z1g1Y"},
{id:32,title:"   32 .- CARIOSO SALVADOR  ",anthem:" 32.- CARIOSO SALVADOR  \n \n Carioso Salvador, \n Huyo de la tempestad  \n A tu seno protector,  \n Findome de tu bondad.  \n Slvame, Seor Jess,  \n De las olas del turbin,  \n Hasta el puerto de salud  \n Gua mi pobre embarcacin.  \n -2-  \n Otro asilo ninguno hay, \n  Indefenso acudo a Ti.  \n Mi necesidad me trae,  \n Porque mi peligro vi.  \n Solamente en Ti, Seor,  \n Puedo hallar consuelo y luz. \n  Vengo lleno de temor  \n A los pies de mi Jess.  \n     -3- \n  Cristo, encuentro todo en Ti \n  Y no necesito ms  \n Cado, me pusiste en pie.  \n Dbil, nimo me das.  \n Al enfermo das salud,  \n Das la vista al que no ve. \n  Con amor y gratitud  \n Tu bondad ensalzar.  \n ",track:"1IHwMH7fwUGmXk_JpaiDUeV8FooFWSVHy",demo:"1jA28bBy6qQearRsFMSRJfRpJJkjsXUp9"},
{id:33,title:"  33 .- CERCA DE TI SEOR  ",anthem:"        33.- CERCA DE TI, SEOR  \n \n      Cerca de Ti, Seor,  \n Quiero morar.  \n Tu grande y tierno amor \n  Quiero gozar.  \n Llena mi pobre ser, \n  Limpia mi corazn,  \n Hazme tu rostro ver  \n En comunin.  \n     -2- \n  Pasos inciertos doy \n  El sol se va  \n Mas si contigo estoy \n  No temo ya.  \n Himnos de gratitud \n  Ferviente cantar,  \n Y fiel a Ti, Jess,  \n Siempre ser.  \n     -3- \n  Da feliz ver \n  Creyendo en Ti,  \n En que yo habitar  \n Cerca de Ti.  \n Mi voz alabar  \n Tu dulce nombre all, \n  Y mi alma gozar  \n Cerca de Ti.  \n ",track:"1-kO73j7wS6ALLLomR4PTRkRWZZ_C7KXd",demo:"1Nc-Fxiwb4PVDdDFeBg3PqVmPdyripKj7"},
{id:34,title:"  34 .- COMO UN CANTICO SUBLIME ",anthem:"        34.- COMO UN CNTICO SUBLIME  \n \n     Como un cntico sublime  \n Mi plegaria va al Seor.  \n Donde le cuento mis cuitas,  \n Mis pesares y dolor.  \n Cristo paciente me escucha  \n Y me sabe responder  \n // : Si obedezco su palabra  \n Todo en esta vida  \n lo podr vencer : //  \n     -2-  \n Es mi Dios fuerte, celoso,  \n Nadie me podr daar  \n Aunque pase por mi vida  \n El ms fuerte vendaval.  \n Firme estar en esa Roca,  \n Roca de la eternidad.  \n // : Y por todas las edades  \n Gozar con Cristo,  \n Qu felicidad!: //  \n     -3-  \n En el cielo de la gloria  \n Hay una hermosa ciudad,  \n Con un bello trono blanco  \n Do mora la Trinidad,  \n Tiene sus calles de oro,  \n Sus puertas de perlas son.  \n // : Quiero serle fiel a Cristo  \n Para entrar un da  \n en tan bella mansin : //  \n     -4-  \n Cuando termine mi vida  \n En el mundo terrenal.  \n Tendr vestiduras blancas  \n En la patria celestial.  \n Una preciosa corona  \n All en el cielo tendr,  \n // : De blancas perlas brillantes  \n Por todas las vidas  \n que aqu rescat://  \n     -5-  \n En medio del Paraso  \n El rbol de Vida est,  \n Y comer de su fruto  \n Como de rico man.  \n Una piedrecita blanca  \n Mi buen Jess me dar,  \n // : Con un nombre nuevo escrito  \n Que nadie en el mundo  \n jams lo sabr : //  \n ",track:"1fcEgvICf_k5mhfs43mYzzy9Bu5kigoMI",demo:"18hDVenuBL4CVX0sSUlRFeREFTTef0QTG"},
{id:35,title:"  35 .- CANTA. OH BUEN CRISTIANO  ",anthem:"        35.- CANTA, OH BUEN CRISTIANO  \n  \n     Canta, oh buen cristiano,  \n Dulce ser cantar,  \n Hace el camino llano,  \n Libra el pesar.  \n Canta en las noches tristes,  \n Canta en el sol y en luz.  \n El mal as resistes  \n Canta de Jess.  \n     -2-  \n Canta, oh buen cristiano,  \n Templa tu corazn,  \n Alza a tu Soberano  \n Tu feliz cancin.  \n Siempre est lleno el mundo  \n De endechas y dolor.  \n Canta el amor profundo  \n De tu Salvador.  \n    -3-  \n Canta, oh buen cristiano,  \n Dios tu socorro es,  \n l sostendr tu mano,  \n Hasta la vejez.  \n Sabes que al diablo invitas  \n Cuando medroso ests?  \n Dios quitar tus cuitas  \n Si cantando vas.  \n ",track:"1wAjFBspcHBpkJZFya71h5lJkGDzTTJfT",demo:"1po2-sDUmTbJuMHOJUOiHKX0uts2hJbhM"},
{id:36,title:"  36 .- CON CANTICOS SEOR  ",anthem:"        36.- CON CNTICOS, SEOR  \n  \n     Con cnticos, Seor,  \n Mi corazn y voz  \n Te adoran con fervor,  \n Oh! Trino, Santo Dios.  \n En tu mansin yo te ver  \n Y galardn feliz tendr.  \n     -2-  \n Tu mano paternal  \n Traz mi senda aqu.  \n Mis pasos cada cual  \n Velados son por Ti.  \n En tu mansin yo te ver  \n Y galardn feliz tendr.  \n     -3-  \n Innumerables son  \n Tus bienes y sin par,  \n Que por tu compasin  \n Recibo sin cesar.  \n En tu mansin yo te ver  \n Y galardn feliz tendr.  \n     -4-  \n T eres, oh, Seor!  \n Mi sumo, todo bien  \n Mil lenguas, tu amor  \n Cantando siempre estn. \n  En tu mansin yo te ver  \n Y galardn feliz tendr.  \n ",track:"1U5qxiFR21xRvao4DfojZFs0V5-ivcK_s",demo:"1AGjv2v61ALBpk051VY1Gbk8JkLwYRcqe"},
{id:37,title:"   37 .- CONFIANDO EN JESUS  ",anthem:"        37.- CONFIANDO EN JESS  \n  \n     Aunque la furiosa tempestad  \n Amenace ruina sin piedad,  \n Con Jess tendr seguridad,  \n Fiando en Jess.  \n   CORO  \n Confiando slo en Jess,  \n S que su promesa cumplir  \n Yo confiando slo en Jess,  \n l me salvar.  \n     -2-  \n Penas y dolor podr tener  \n Que congojas causen a mi ser,  \n Pero todo yo podr vencer,  \n Fiando en Jess.  \n     -3-  \n Otras esperanzas pasarn,  \n Otros goces, s, fenecern  \n Mi esperanza y gozo durarn  \n Fiando en Jess.  \n ",track:"1jleL_IGlxBuGMMWTMIzJ4cQfQOESGi_m",demo:"1dduaWR5y9eKubYYIJifqOscZmTab63Aj"},
{id:38,title:"  38 .- CONMIGO SE  ",anthem:"        38.- CONMIGO S  \n  \n     Seor Jess, la luz del da se fue,  \n La noche cierra ya, conmigo s  \n Sin otro amparo, T, por compasin,  \n Al desvalido das consolacin.  \n     -2-  \n Veloz el da nuestro huyendo va,  \n Su gloria, sus ensueos pasan ya  \n Mudanza y muerte miro en derredor,  \n Conmigo s, bendito Salvador.  \n     -3-  \n Tu gracia en todo el da es menester,  \n Quin otro al tentador podr vencer?  \n Cul otro amante gua encontrar?  \n En sombra o sol, Seor, conmigo s.  \n     -4-  \n Y mire al fin en mi postrer visin  \n De luz la senda que me lleva a Sin.  \n Do alegre cantar al triunfar la fe  \n Jess, conmigo en vida y muerte fue.  \n ",track:"1HyT4H4T1X01wI1Zb0Tpf6HN_4_tRBI43",demo:"1IEzQvBCyYGsCFjBcHb03zPzBbvwPmvib"},
{id:39,title:"   39 .- CONSAGRACION  ",anthem:"        39.- CONSAGRACIN  \n  \n     Si Cristo dio su vida aqu,  \n Qu debo darle yo?  \n Acptame, Seor Jess,  \n Ser tu servidor.  \n  CORO  \n Mi vida, oh Seor, te doy,  \n Mi tiempo todo a Ti,  \n Te servir, Seor Jess,  \n Y oir tu dulce voz.  \n      -2-  \n No importa donde T mandes,  \n Ir sin vacilar.  \n Yo s que T protegers  \n A aquel que en Ti est.  \n     -3-  \n Si tengo que la mar pasar,  \n Contigo yo ir,  \n A dar las nuevas de tu amor,  \n Y gozo impartir.  \n     -4-  \n Yo aprecio amigos y hogar,  \n Con todo el corazn,  \n Pero al Seor Jess le doy  \n El centro de mi amor.  \n     -5-  \n Mi todo, oh Jess, te doy,  \n Acptalo Seor.  \n En mi humilde corazn  \n Tu trono quiero hacer.  \n ",track:"1lGfM0bfcwjOCIBkyBvW2skKTkcImw9gC",demo:"10UtVbjVLvXcM967a-cAEzj6M1rNuAx5i"},
{id:40,title:"   40 .- CONTENDAMOS JOVENES  ",anthem:"        40.- CONTENDAMOS JVENES  \n    Contendamos jvenes por la fe,  \n Aunque brame el mundo y Satans,  \n En la lucha nunca nos vencern,  \n Pues Jess nos guardar.  \n     CORO  \n Si sufrimos aqu, reinaremos all  \n En la gloria celestial.  \n Si llevamos la cruz  \n Por amor de Jess.  \n La corona l nos dar.  \n    -2-  \n No seamos tibios de corazn,  \n Ni dejemos nunca el amor de Dios  \n Mantengamos firme la profesin  \n De la fe del Salvador.  \n     -3-  \n Procuremos todos la santidad,  \n Sin la cual ninguno ver al Seor.  \n Gozo, paz y eterna felicidad  \n Cristo da al vencedor.  \n ",track:"1XXX6G2y9-3WgRuZCjuKn0MSGnzLemMa2",demo:"1etOKH-ufJaY4ACEjoYChXb1ZU7a05U60"},
{id:41,title:"  41 .- CONTENTO ESTOY ",anthem:"        41.- CONTENTO ESTOY  \n  \n     Contento estoy, sin hogar en el mundo,  \n Muy poco de oro me bastar  \n Mas en el cielo do viven los santos,  \n Mansin gloriosa me espera all.  \n     CORO  \n Tengo en el cielo, un hogar preparado  \n Do para siempre yo vivir,  \n Y un da pronto ver a mi Cristo,  \n Y en calles de oro caminar.  \n     -2-  \n No estoy yo triste ni desamparado,  \n Voy rumbo al cielo, a mi mansin  \n Cual peregrino yo ando en el mundo,  \n Pues busco yo mi morada en Sin.  \n     -3-  \n Oh! qu gozoso yo espero el da,  \n Cuando en las nubes Jess vendr,  \n Oh, alegre da, en la tierra bendita!  \n Adorar a mi Cristo all.  \n ",track:"1F5BcJmb0KXriDPCXd9ssflzXyDG6lq9I",demo:"1FyAks9ZqQaFDE8MJQN8-usn6T2kusyT1"},
{id:42,title:"   42 .- CON VOZ BENIGNA  ",anthem:"        42.- CON VOZ BENIGNA  \n  \n     Con voz benigna te llama Jess,  \n Invitacin de puro amor.  \n Por qu le dejas en vano llamar?  \n Sordo sers, pecador?  \n     CORO  \n Hoy te convida,  \n Hoy te convida,  \n Voz bendecida,  \n Benigna, convdate hoy.  \n     -2-  \n A los cansados convida Jess, \n  Con compasin mira el dolor.  \n Trele tu carga te bendecir  \n Te ayudar el Seor.  \n     -3-  \n Siempre aguardando contempla Jess,  \n Tanto esperar! Con tanto amor!  \n Hasta sus plantas ven, msero, y trae  \n Tu tentacin, tu dolor.  \n ",track:"1QxxtGaBI9-a_WwJa-PReg0kv8T3yZvHY",demo:"1U5NCwcLu9DIl-wqwQV65cPMsS0dgjOo6"},
{id:43,title:"  43 .- CRISTO BENDITO  ",anthem:"        43.- CRISTO BENDITO  \n  \n     Cristo bendito, yo, pobre nio,  \n Por tu cario me llego a Ti.  \n Para rogarte humildemente,  \n Tengas clemente piedad de m.  \n     -2-  \n Quiero a tus plantas, con alegra,  \n Sentarme un da donde t ests.  \n Oh, Cristo mo quiero buscarte,  \n Anhelo amarte cada vez ms.  \n ",track:"1fcHI3lTi1ObIAQjKv5Jc7ZAlnuuCGiCM",demo:"11HHb8L551mfKmKQmSiglNgGM21sO-SXR"},
{id:44,title:"  44 .- CRISTO EL PASTOR BENDITO  ",anthem:"        44.- CRISTO, EL PASTOR DIVINO  \n  \n     Es Jesucristo la vida y luz,  \n El nos anuncia la eterna verdad,  \n Verbo encarnado que muere en la Cruz,  \n Por darnos libertad.  \n     CORO  \n Cristo es Pastor enviado  \n Y divino Emanuel  \n Tierno me gua por sendas de paz,  \n Como su oveja fiel.  \n     -2-  \n Slo l nos libra de toda ansiedad,  \n Limpia, benigno, el fiel corazn \n Es su carcter amor y bondad,  \n La misma compasin.  \n     -3-  \n Fuente preciosa de gozo y salud  \n Que purifica de toda maldad \n Quiere llenarnos de su plenitud  \n Y de su santidad.  \n ",track:"1hFIVLboy0jQa5gwrz1q2jV7UkznGolIt",demo:"1Lx1K_TOmZKKjADePLZdpbj25Msfdc1ZP"},
{id:45,title:"  45 .- CRISTO ES EL TODO ",anthem:"     45.- CRISTO ES EL TODO  \n  \n     Es Jesucristo mi todo,  \n Grato es cantar su loor,  \n Oh cun sublime e infinito  \n Es su divino amor!  \n Cuando me vio errabundo,  \n Cual hijo prdigo,  \n Vino a buscar y a salvarme,  \n A su redil me llev.  \n     CORO  \n Cristo . . . Cristo . . .  \n T eres mi Salvador,  \n Cristo . . . Cristo . . .  \n Tuyo ser, Seor \n Te seguir donde quiera, si T guiando vas,  \n Y al terminar mi carrera, en gloria ver tu faz.  \n     -2-  \n Cristo es el Lirio del Valle,   \n La Rosa es de Sarn,  \n Cristo es el Astro Esplendente,  \n La Roca de Salvacin.  \n l es la fuente de vida  \n Y gozo eternal,  \n l satisface mi alma,  \n Con el man celestial.  \n     -3-  \n Cristo naci en un pesebre,  \n La amarga copa bebi,  \n Cual inocente cordero,  \n En el Calvario muri  \n Resucit de la tumba,  \n Y al cielo ascendi,  \n Mas pronto viene en su gloria,  \n Esta promesa nos dio.  \n ",track:"1qqR9Mk0Waqz2HPqqdXpK1lFNSZu_OUSk",demo:"1xuWl6Qxsb8HQHs6045F-4xbhA6juUEZk"},
{id:46,title:"  46 .- CRISTO LA ROCA  ",anthem:"        46.- CRISTO LA ROCA  \n  \n     En las olas inmensas  \n De embravecido mar,  \n Que asaltan de mi alma  \n La pobre embarcacin,  \n De rodillas a Cristo clam,  \n Y el huracn  \n Deshecho fue al instante,  \n A la voz de Dios.  \n     CORO  \n Es Cristo la Roca,  \n El ancla de mi fe,  \n Los males, lamentos  \n Y ayes de temor,  \n Terminan por siempre  \n Con mi supremo Rey  \n Es Jesucristo  \n Mi refugio.  \n     -2-  \n Me guarda de peligros,  \n De pruebas, de dolor,  \n l manda que los vientos  \n No agiten tempestad,  \n Los mares se detienen,  \n La ola repos,  \n Y en Cristo fijo el ancla,  \n Confiando ms. \n     -3-  \n Mi dulce Salvador, s,  \n Mi hermoso amigo y Dios,  \n Que libra de tristezas  \n Y aleja amarga hiel,  \n Por fe yo ir al cielo,  \n Mansin del ser de amor,  \n La fuente inagotable  \n De dicha y bien.  \n ",track:"16E00b61AF3kIJL95VWeV-PO7bYQtB6b5",demo:"164wDoA6x3n2hTYenzjnJTKY-P4El64we"},
{id:47,title:"  47 .- CRISTO ME AMA  ",anthem:"        47.- CRISTO ME AMA  \n  \n     Cristo me ama, bien lo s  \n Su palabra me hace ver  \n Que los nios son de Aquel,  \n Quien es nuestro amigo fiel.  \n     CORO  \n Cristo me ama, Cristo me ama,  \n Cristo me ama. La Biblia dice as.  \n        -2-  \n Cristo me ama, pues muri  \n Y, el cielo me abri  \n El mis culpas quitar,  \n Y la entrada me dar.  \n     -3-  \n Cristo me ama es verdad  \n Y me cuida en su bondad,  \n Cuando muera, si soy fiel,  \n Vivir all con l.  \n ",track:"1CPKIehWbZfdkw5Y85CVrZA5ms-FrfKUd",demo:"1b-vQjgG_EwqZKO--DI6iAjZOjRDyhvrY"},
{id:48,title:"  48 .- CRISTO MI PILOTO SE  ",anthem:"        48.- CRISTO, MI PILOTO S  \n \n     Cristo, mi Piloto s  \n En el tempestuoso mar  \n Fieras ondas mi bajel  \n Van a hacerlo zozobrar.  \n Mas si T conmigo vas  \n Pronto al puerto llegar,  \n Carta y brjula hallo en Ti,  \n Cristo, mi Piloto s.  \n     -2-  \n Todo agita el huracn  \n Con indmito furor  \n Mas los vientos cesarn  \n Al mandato de tu voz  \n Y al decir, que sea la paz  \n Ceder sumiso el mar.  \n De las aguas T el Seor,  \n Guame, oh Piloto fiel.  \n     -3-  \n Cuando al fin cercano est,  \n De la playa celestial,  \n Si el abismo ruge an,  \n Entre el puerto y mi bajel,  \n En tu pecho al descansar,  \n Quiero orte a Ti decir:  \n Nada temas ya del mar,  \n Tu Piloto siempre soy.  \n ",track:"18rmkVP_ZbXjAdCrt4utibw7SC1yW55-z",demo:"1khmt6-_fHgnRf-KWHXggn7JdKWUL3Ajb"},
{id:49,title:"  49 .- CRISTO NOS ABRIO EL CAMINO",anthem:"        49.- CRISTO NOS ABRI EL CAMINO  \n  \n     Cristo, el Salvador  \n Nos abri camino al cielo,  \n Cuando l dio su vida  \n En la cruenta cruz,  \n Quiere que digamos  \n Doquier la bella historia.  \n Cristo salva del pecado  \n Al mortal.  \n     CORO  \n El Seor abri . . . camino al cielo all . . .  \n Al morir . . . en la cruz . . . l nos dio santa  luz  \n Ya la prepar . . . camino nos abri  \n Nos guiar . . . a la luz . . .  \n Al hogar . . . del buen Jess.  \n     -2-  \n Bien marcado est  \n El camino por la sangre,  \n Do se ven las huellas  \n Del buen Redentor:  \n Llegars al fin  \n A la gloria si lo sigues,  \n Gozars por siempre  \n De su grande amor.  \n     -3-  \n Oye, pecador,  \n Con amor te llama Cristo,  \n Si su ley t guardas, \n  l te guiar  \n Vivirs con l  \n Para siempre all en los cielos,  \n Si servirle quieres  \n l te salvar.  \n ",track:"1CbjHtW_bV6hko86XSxBVPJGaeD656MzT",demo:"1e09HPJdXRxeJfsOP4rxHFNBiZYh_PHeb"},
{id:50,title:"  50 .- CRISTO QUIERE LIMPIOS CORAZONES",anthem:"        50.- CRISTO QUIERE LIMPIOS CORAZONES  \n  \n     Cristo busca limpios corazones,  \n Que le sirvan siempre con fidelidad  \n Que a los pecadores, insten fervorosos  \n Que se vuelvan del pecado a la verdad.  \n     CORO  \n Id a trabajar all  \n En los campos del Seor,  \n Que para la siega \n Se presentan blancos hoy  \n Oh, fieles siervos de Dios,  \n A quien debis todo honor  \n Od su voz, salid a trabajar.  \n     -2-  \n Labios puros Cristo necesita,  \n Que con gozo anuncien plena salvacin  \n Lenguas consagradas slo a su servicio,  \n Que proclamen al cautivo redencin.  \n     -3-  \n Cristo busca manos bien dispuestas  \n Para trabajar con buena voluntad  \n Siembras ya maduras piden vuestra ayuda \n  Las doradas mieses pronto cosechad.  \n     -4-  \n Vidas santas Cristo necesita  \n Que a los pecadores muestren su poder  \n Libres de ansiedades, en Jess confiadas \n  Y que pueda de ellas siempre disponer. \n ",track:"1ULd4fEA1QSwQ0Y41AKGI2cxFMqobkxcN",demo:"1SSk3bxLwt_NrelKIhlwIc7vLfqktwcRy"},
{id:51,title:"  51 .- CUAL MIRRA FRAGANTE ",anthem:"        51.- CUAL MIRRA FRAGANTE  \n  \n     Cual mirra fragante que exhala su olor  \n Y ricos perfumes esparce en redor,  \n Tu nombre, oh Amado! a mi corazn  \n Lo llena de gozo, transprtalo a Sin.  \n     CORO  \n Aleluya, Aleluya  \n Al Cordero de Dios:  \n Aleluya al Amado,  \n Al bendito Jess.  \n     -2-  \n Cual voz amigable que al triste viador,  \n En bosque perdido le inspira valor,  \n Tu nombre me anima y me hace saber  \n Que ofreces, piadoso, rescate a mi ser.  \n    -3-  \n Cual luz que brillando del alto fanal,  \n Al nauta en la noche seala el canal.  \n Tu nombre esparciendo benfica luz,  \n Al cielo me lleva, bendito Jess.  \n ",track:"1dDj1XUDYsbYVe3dDYDH813H-M_Afvzeq",demo:"121-QIg_yI8eJ8VtnHE7706fifu0ZMwh-"},
{id:52,title:"  52 .- CUANDO ALLA SE PASE LISTA ",anthem:"           52.- CUANDO ALL SE PASE LISTA  \n  \n     Cuando la trompeta suene  \n En aquel da final,  \n Y que el alba eterna rompa en claridad.  \n Cuando las naciones salvas  \n A su patria lleguen ya,  \n Y que sea pasada lista, all he de estar.  \n     CORO  \n Cuando all se pase lista,  \n Cuando all se pase lista,  \n Cuando all se pase lista,  \n A mi nombre yo feliz responder.  \n     -2-  \n En aquel da sin nieblas,  \n En que muerte ya no habr,  \n Y su gloria el Salvador impartir  \n Cuando los llamados entren  \n A su celestial hogar  \n Y que sea pasada lista, all he de estar.  \n     -3-  \n Trabajemos por el Maestro  \n Desde el alba al vislumbrar  \n Siempre hablemos de su amor y fiel bondad.  \n Cuando todo aqu fenezca,  \n Y nuestra obra cese ya  \n Y que sea pasada lista, all he de estar.  \n ",track:"1HlGfHQyZTkuAg0gh0kCrZVun-uIrRC6c",demo:"18r675KvOlYj-f23vbZEFYLCTEvl60Cg8"},
{id:53,title:"  53 .- CUANDO ANDEMOS CON DIOS",anthem:"        53.- CUANDO ANDEMOS CON DIOS  \n  \n     Cuando andemos con Dios \n  Escuchando su voz,  \n Nuestra senda  \n Florida ser:  \n Si acatamos su ley  \n l ser nuestro Rey,  \n Y con l reinaremos all.  \n     CORO  \n Obedecer,  \n Cumple a nuestro deber,  \n Si queris ser felices,  \n Debis obedecer.  \n     -2-  \n Cuando Cristo muri  \n Nuestro llanto enjug,  \n Proclamarle debemos doquier.  \n Gozars del amor  \n De tu Rey y Seor,  \n Si obediente  \n Le quieres t ser.  \n     -3-  \n No podremos probar  \n Sus delicias sin par,  \n Si seguimos Mundano el placer,  \n Obtendremos su amor,  \n Y el divino favor  \n Si sus leyes  \n Queremos hacer.  \n ",track:"1lP1gFyu-5y8vjIPOuQWaUtfqdG3oSSHl",demo:"1JW2oj-9Mo79LYRLnusFJ9aQjZm_VHRsh"},
{id:54,title:"   54 .- CUANDO CONBATIDOS ",anthem:"        54.- CUANDO COMBATIDOS  \n  \n     Cuando combatido por la adversidad,  \n Creas ya perdida tu felicidad,  \n Mira lo que el cielo para ti guard,  \n Cuenta las riquezas que el Seor te dio.  \n     CORO  \n Cuenta las riquezas de tu Dios  \n Mira las riquezas de su amor,  \n Pon los ojos donde Cristo est  \n Y tu mente guarde la divina paz.  \n     -2-  \n Andas agobiado por algn pesar?  \n Tu cruz te parece dura de llevar?  \n Cuenta las promesas del Seor Jess,  \n Y de las tinieblas nacer la luz.  \n     -3-  \n Cuando de otros veas la prosperidad,  \n Y tus pies claudiquen tras de su maldad,  \n Cuenta las riquezas que tendrs por fe,  \n Donde el oro es polvo que hollar tu pie.  \n     -4-  \n Aunque grande sea tu afliccin aqu  \n No te desalientes, Dios est por ti.  \n Si a su propio Hijo no se reserv,  \n Aun vers riquezas que ojo nunca vio.  \n ",track:"1PQCjtmNtp851eNuLB_sBdntCqo5MRnuJ",demo:"1HtrrsA32GCgasKh5ijm_tslK6vfLI-lE"},
{id:55,title:"  55 .- CUANDO EL VINO A MI CORAZON ",anthem:"   55.- CUANDO L VINO A MI CORAZN  \n  \n     Cun glorioso es el cambio  \n Operado en mi ser,  \n Viniendo a mi vida  \n El Seor  \n Hay en mi alma una paz  \n Que yo ansiaba tener,  \n La paz \n Que me trajo su amor.  \n     CORO  \n l vino a mi corazn,  \n l vino a mi corazn,  \n Soy feliz con la vida que Cristo me dio,  \n Cuando l vino a mi corazn.  \n     -2-  \n Ya no voy por la senda  \n Que el mal me traz,  \n Do slo encontr  \n Confusin,  \n Mis errores pasados  \n Jess los borr,  \n Cuando l vino  \n A mi corazn.  \n     -3-  \n Ni una sombra de duda  \n Oscurece su amor,  \n Amor que me trajo  \n El perdn,  \n La esperanza que aliento  \n La debo al Seor,  \n Cuando l vino  \n A mi corazn.  \n ",track:"1LcK5qBnbheakT-SQR7_dsOUt6Ae3vyjK",demo:"1gKhQ1dMesiOC8uk4rR1c4zreJjEeu5e4"},
{id:56,title:"  56 .- CUANDO LEO LA EN BIBLIA  ",anthem:"        56.- CUANDO LEO EN LA BIBLIA  \n     Cuando leo en la Biblia \n Como llama Jess  \n Y bendice a los nios con amor,  \n Yo tambin quisiera estar,  \n Y con ellos descansar  \n En los brazos del tierno Salvador.  \n       -2-\n Ver quisiera sus manos,  \n Sobre m reposar,  \n Cariosos abrazos de l, sentir,  \n Sus miradas disfrutar,  \n Las palabras escuchar:  \n A los nios dejad a m venir.  \n     -3-\n Mas an a su estrado  \n En oracin puedo ir,  \n Y tambin de su amor participar \n  Pues si aqu buscarle se,  \n Le ver y le escuchar,  \n En el reino que l fue a preparar.  \n     -4-\n Los que son redimidos  \n Y salvados por l  \n Al Cordero celebran inmortal,  \n All voces mil y mil  \n Se oyen del coro infantil,  \n Porque es de ellos el reino celestial.  \n     -5- \n Cuntos hay que no saben  \n De esa bella mansin  \n Y no quieren a Cristo recibir! \n Les quisiera yo mostrar  \n Que para ellos hay lugar,  \n En el cielo do los convida ir.  \n     -6-\n Yo anso aquel da   Venturoso sin fin,  \n El ms grande, el ms lcido, el mejor,  \n Cuando de cualquier nacin,  \n Nios mil, sin distincin,  \n A los brazos acudan del Seor.  \n ",track:"1SQRpz2Pdzsj0jCqr06RKQ670U9FckoPt",demo:"1FrzBN1_X4AKQgAeYiuOqibtsVrIGOCgp"},
{id:57,title:"  57 .- CUANDO CRISTIANOS APRENDAN A DIEZMAR ",anthem:"        57.- CUANDO CRISTIANOS APRENDAN A DIEZMAR  \n  \n     Oh, cun grande cambio en el mundo habr!  \n Cuando cristianos aprendan a diezmar:  \n Llegarn a tener lo que ansiaban al fin,  \n Cuando todos aprendan a diezmar.  \n     CORO  \n Cuando todos aprendan a diezmar,  \n Cuando todos aprendan a diezmar,  \n Cada alma gozar y la obra crecer,  \n Cuando todos aprendan a diezmar.  \n     -2-  \n Ningn nio habr que carezca de hogar,  \n Cuando cristianos aprendan a diezmar,  \n Los pastores no ms se entristecern,  \n Cuando todos aprendan a diezmar.  \n     -3-  \n Ningn alma habr en la oscuridad,  \n Cuando cristianos aprendan a diezmar,  \n Y de Dios y su amor todos conocern,  \n Cuando todos aprendan a diezmar.  \n     -4-  \n Entonces disfrutarn paz sin igual,  \n Cuando cristianos aprendan a diezmar  \n Nuestras almas de gozo rebosarn,  \n Cuando todos aprendan a diezmar.  \n ",track:"1z0RJzmpABQu0tEviBKYdtHzYOuTAvn5a",demo:"1Zlfbj3ynZpPYfSP7ddmRJ5NgXF4l1VNU"},
{id:58,title:"  58 .- CUANDO TODOS DEN SUS DIEZMOS",anthem:"        58.- CUANDO TODOS DEN SUS DIEZMOS  \n  \n     Cun glorioso ser  \n Cuando el pueblo leal  \n A la casa del Padre  \n Sus diezmos traern  \n Cuando todos unidos  \n Por gracia eternal  \n  A la causa de Cristo  \n Colaborarn.  \n     CORO  \n Cuando todos sus diezmos  \n A Cristo le den,  \n Cuando todos sus diezmos  \n A Cristo le den,  \n No habr nunca pobreza,  \n Ni ansiedad, ni tristeza.  \n Cuando todos sus diezmos  \n A Cristo le den.  \n        -2-  \n En el pueblo de Dios  \n Robadores no habr,  \n Que la parte de Dios  \n Para s retendrn  \n Pues la via de Cristo  \n Doquier crecer  \n Y abundante su pueblo  \n Sus dones traer.  \n     -3-  \n Nuestro Padre, sus dones  \n Ms ricos dar,  \n Y favores preciosos  \n A su pueblo fiel  \n Las ventanas del cielo  \n Con gozo abrir  \n Cuando todos sus diezmos  \n Le den a EMMANUEL.  \n ",track:"1yrgAoteeL8ySC-fVk_TRduy4MBE_QBRd",demo:"1bfMC1nDgNc1rOPJuz92qj2jNIozjmU1u"},
{id:59,title:"  59 .- CUAN DULCE EN LOS OIDOS ",anthem:"        59.- CUN DULCE EN LOS ODOS  \n  \n     Cun dulce, en los odos  \n Del triste pecador,  \n La voz de Cristo, suena,  \n Su amante Salvador:  \n Ven, alma trabajada  \n De penas y dolor,  \n Descarga en m tus culpas,  \n Tu angustia y tu temor  \n     CORO  \n Ven, s ven a m, oh s,  \n Ven, s ven a m, oh, oh,  \n Alma atribulada,  \n Ven, s ven a m.  \n     -2-  \n Deseas un amigo  \n Eternamente fiel,  \n Sostn en tus flaquezas,  \n Autor de todo bien? \n  Acude, pues, a Cristo  \n Y en El encontrars  \n Perdn, descanso y gozo,  \n Amor y dulce paz.  \n     -3-  \n S, Cristo es el camino,  \n La vida y la verdad,  \n El solo sacrificio  \n Que expa la maldad.  \n El solo Sacerdote,  \n El solo Mediador.  \n Cristo, s, slo Cristo  \n Es nuestro Salvador.  \n ",track:"1MAY_mI95hzNK18eDhO0jbYgOKK3zy1iN",demo:"1OVLiSBlfZbLmPuFb7oVdCzw1M1vrHJiX"},
{id:60,title:"  60 .- CUBA PARA CRISTO  ",anthem:"        60.- CUBA PARA CRISTO  \n  \n     Oh! Cristianos cubanos  \n Soldados de Jess.  \n Luchemos por la patria  \n Y el triunfo de la cruz  \n Que Cuba redimida  \n Conozca la verdad,  \n Que muera para siempre  \n El odio y la maldad.  \n     CORO  \n Oh! compaeros,  \n Luchemos sin cesar  \n Que Cuba para Cristo  \n Tenemos, tenemos que ganar.  \n     -2-  \n No importa si arrogante  \n Se muestra Satans,  \n Marchemos siempre firmes  \n Y sin mirar atrs  \n Es nuestra la victoria,  \n Es nuestra por Jess,  \n Corona nos espera  \n Eterna tras la cruz.  \n     -3-  \n Por Cristo y por su Iglesia  \n Luchemos sin temor,  \n Jess enva las armas  \n Nos llena de valor.  \n Alerta! luchadores,  \n Luchemos sin cesar,  \n Que Cuba para Cristo  \n Tenemos que ganar.  \n ",track:"1-y0dCnCgpOp-4SMcyrgH7VxZb_qkfWfo",demo:"1Y0mwJd1HEvtPwlNYIRdhHUS54JdXc3w_"},
{id:61,title:"   61 .- CUANTO MAS TIEMPO SERA ",anthem:"        61.- CUNTO MS TIEMPO SER  \n  \n    Qu bella es  \n La antigua historia,  \n Del advenimiento de Cristo en gloria  \n Vendr despus de ganar la victoria,  \n Del mundo a los suyos llevar.  \n     CORO  \n Cunto ms tiempo ser  \n Cundo nos podremos cantar?  \n Vuelve Cristo! Aleluya! Aleluya! Amn  \n Aleluya! Amn.  \n     -2-  \n Qu hora gloriosa!  \n Ser bienvenida,  \n Aunque para el mundo es desconocida  \n Vendr con poder y con inmortal vida,  \n Del mundo a los suyos llevar.  \n     -3-  \n Qu gloria inefable!  \n Amor trascendente,  \n Vendr Jesucristo en luz esplendente  \n Con su legin celestial, obediente,  \n Del mundo a los suyos llevar.  \n     -4-  \n Qu gozo tan grande  \n Poder transformarnos!  \n Sin nunca morir, cual Cristo seremos,  \n Viene en las nubes para llevarnos,  \n Del mundo consigo a morar.  \n ",track:"1h6180sPy6RllzoUWKDttOxJpZvzhTYMY",demo:"1SvrNXT_UbG-yuhYqTuQtdveu8CIfgNyC"},
{id:62,title:"  62 .- DADME LA BIBLIA  ",anthem:"        62.- DADME LA BIBLIA  \n  \n     Dadme la Biblia, reluciente estrella,  \n Norte del nauta en tormentoso mar,  \n Nunca el nublado esconder su brillo,  \n Pues vino Cristo al hombre a rescatar.  \n     CORO  \n Dadme la Biblia, santa y clara, nueva,  \n Luz del camino angosto y celestial.  \n Regla y promesa, ley y amor unidos,  \n Hasta que rompa el alba eternal.  \n     -2-  \n Dadme la Biblia en mi desaliento,  \n Cuando el pecado cuseme temor  \n Dadme los fieles dichos de mi Maestro.  \n Siempre me encuentre junto al Salvador.  \n     -3-  \n Dadme la Biblia, antorcha a mis pisadas \n  En la insegura senda terrenal  \n nica luz constante en las tinieblas  \n Prenda de paz y amparo celestial.  \n     -4-  \n Dadme la Biblia, luz de vida eterna \n Junto al sepulcro su esplendor alzad  \n Sobre el Jordn destquense las puertas  \n Flgidas de la clica ciudad.  \n ",track:"1eHsTMzaYI_Lnsc1srT1B6-IfZgr1IWMq",demo:"1-D_KoHZ-8x44EYvGNt6g9W3YWXZkjtsO"},
{id:63,title:"   63 .- DA LO MEJOR AL MAESTRO  ",anthem:"        63.- DA LO MEJOR AL MAESTRO  \n  \n     Da lo mejor al Maestro  \n Tu juventud, tu vigor  \n Dale el ardor de tu alma,  \n Lucha del bien en favor.  \n Cristo nos dio el ejemplo  \n Siendo el joven de valor  \n Sele devoto, ferviente,  \n Dale de ti lo mejor.  \n     CORO  \n Da lo mejor al Maestro  \n Tu juventud, tu vigor,  \n Dale el ardor de tu alma,  \n De la verdad lucha en pro.  \n     -2-  \n Da lo mejor al Maestro \n  Dale de tu alma el honor  \n Que sea l en tu vida  \n El mvil de cada accin.  \n Dale y te ser dado  \n El Hijo amado de Dios.  \n Srvele da por da,  \n Dale de ti lo mejor. \n    -3-  \n Da lo mejor al Maestro  \n Nada supera su amor,  \n Se dio por ti a S mismo,  \n Dejando gloria y honor.  \n No murmur al dar su vida  \n Por salvarte del error.  \n male ms cada da  \n Dale de ti lo mejor.  \n ",track:"1McJu9WOXne77FPPQfauzo4KRSWvt6OTY",demo:"10ceRaTraTD83R1dbC2tKH1ILy5pPZK0l"},
{id:64,title:"  64 .- DAME LA FE DE MI SEOR  ",anthem:"        64.- DAME LA FE DE MI SEOR  \n  \n     Dame la fe de mi Jess,  \n La fe bendita del Seor,  \n Que al afligido da la paz,  \n La fe que salva de temor  \n Fe de los santos galardn,  \n Gloriosa fe de salvacin.  \n     -2-  \n Dame la fe que trae poder,  \n De los demonios vencedor,  \n Que fieras no podrn vencer,  \n Ni dominarla el opresor,  \n Que pueda hoguera soportar,  \n Premio de mrtir alcanzar. \n      -3-  \n Dame la fe que vencer  \n En todo tiempo, mi Jess,  \n Dame la fe que fijar  \n Mi vista en tu divina cruz  \n Que puede proclamar tu amor,  \n Tu voluntad hacer, Seor.  \n     -4-  \n Dame la fe que da el valor,  \n Que ayuda al dbil a triunfar,  \n Que todo sufre con amor,  \n Y puede en el dolor cantar.  \n Que puede al cielo escalar,  \n O aqu con Cristo, caminar.  \n ",track:"1XVNx12JhPO6Q9XU60MSPaXJXQEgd1W8J",demo:"1fHbssW8Hb6tuA0Lc4UDdHAHAUfINJIRD"},
{id:65,title:"  65 .- DEL SOL SOMOS RAYITOS  ",anthem:"       65.- DEL SOL SOMOS RAYITOS  \n  \n     Dios pone a los niitos  \n En terrenal mansin,  \n Como sus favoritos  \n En excelsa misin.  \n Quiere que sean rayitos  \n De esperanza y de amor,  \n Que ahuyenten las tinieblas  \n Que causan el dolor.  \n     CORO  \n Del sol somos rayitos  \n Que al hombre Dios mand,  \n Y en sus tristes conflictos  \n Brillamos con amor.  \n     -2-  \n Las nubes nos ocultan  \n La hermosa luz del sol,  \n La vida tiene angustias  \n Y pena el corazn  \n Pero si somos fieles  \n En nuestra gran misin,  \n Al triste le daremos  \n Consuelo en su afliccin.  \n     -3-  \n Cumplamos, pues, contentos  \n Nuestra misin de luz,  \n Calmando sufrimientos  \n En nombre de Jess.  \n Las sombras de la vida  \n Disipe nuestro amor,  \n Y del alma afligida  \n Quitemos el dolor.  \n ",track:"1yxcQUt0v7iDlMQyFWQmixD7quNrHmtvD",demo:"191WraVqtneDk6scfACHlQCMuUkKUr3rX"},
{id:66,title:"  66 .- DESPIERTATE MI CORAZON  ",anthem:"        66.- DESPIRTATE, MI CORAZN  \n  \n     Despirtate, mi corazn,  \n Bendice a tu Creador,  \n Cantando en himnos de loor  \n Que Dios es Dios de amor.  \n Inmenso amor, amor sin fin  \n Que quiso demostrar,  \n Buscando al pobre pecador  \n Dispuesto a perdonar.  \n     CORO  \n Despirtate, mi corazn,  \n Y alaba, alaba al Salvador.  \n Cantando en himnos de loor  \n Que Dios es Dios de amor.  \n     -2-  \n Amor que quiso quebrantar  \n A Cristo en mi lugar,  \n Amor que quiso en dura cruz  \n Su sangre derramar.  \n Amor que busca hasta encontrar  \n Al pobre pecador.  \n Amor divino, amor sin par,  \n Amor del Salvador.  \n     -3-  \n Comprado a precio tan real  \n La sangre del Seor,  \n Dnde hallar con qu pagar  \n Tal gracia, tal amor?  \n Si su bondad excede an  \n Dignndose tambin  \n Tenerme a m cercano a S  \n Siempre jams, Amn.  \n ",track:"1qaxyvyQABbfVNKrJS039vlevbDZQXiSC",demo:"1lv_6zbOyWG-_hcq-A1v2lcRKQI-ISS2y"},
{id:67,title:"  67 .- DIOS BENDIGA A LAS ALMAS UNIDAS ",anthem:"        67.- DIOS BENDIGA LAS ALMAS UNIDAS  \n  \n     Dios bendiga las almas unidas  \n Por los lazos de amor sacrosanto,  \n Y las guarde de todo quebranto  \n En el mundo de espinas erial.  \n Que el hogar que a formarse comienza  \n Por la unin de estos dos corazones  \n Goce siempre de mil bendiciones  \n Al amparo del Dios de Israel.  \n      -2-  \n Que los dos que al altar se aproximan  \n A jurarse su fe mutuamente,  \n Busquen siempre de Dios en la fuente  \n El secreto de dicha inmortal.  \n Y si acaso de duelo y tristeza  \n Se empaasen sus sendas un da,  \n En Jess hallarn dulce gua  \n Que otra senda les muestre mejor.  \n     -3-  \n Que el Seor, con su dulce presencia,  \n Carioso estas bodas presida,  \n Y conduzca por sendas de vida  \n A los que hoy se han jurado lealtad.  \n Les recuerde que nada en el mundo  \n Es eterno, que todo termina  \n Y, por tanto, con gracia divina,  \n Cifrar deben la dicha en su Dios.  \n ",track:"1r-HogSCPuFuwpq00x4lldOqkXOuIaybQ",demo:"109OdonQhPVDHdfpITCvMAGu8Hozp4CwS"},
{id:68,title:"  68 .- DIOS ME CUIDARA ",anthem:"       68.- DIOS ME CUIDAR  \n    Somos unos pequeuelos  \n Y de humilde condicin,  \n Mas, hacer podemos mucho  \n Por Jess el buen Seor.  \n     -2-  \n Un fiel nio cada da  \n  Lucha con la tentacin  \n Vela y ora y es paciente  \n Cual soldado del Seor.  \n     -3-  \n Cuando impulsos de soberbia  \n Nacen en el corazn,  \n A ser mansos aprendamos  \n Como alumnos del Seor.  \n     -4-  \n Con miradas sonrientes  \n De apacible buen humor,  \n Como luces brillaremos  \n Para gloria del Seor.  \n     -5-  \n Aun el ms pequeo tiene  \n Su misin de paz y amor,  \n Y tambin su cruz pequea  \n Que llevar de Cristo en pos.  \n ",track:"1L_RP58Ise2P2ws8S-_QoNhwRm7y4SQjR",demo:"1A96gUbGC-xkwzMZ8EaHL2Ij52_4iCHS3"},
{id:69,title:"  69 .- DIOS OS GUARDE  ",anthem:"        69.- DIOS OS GUARDE  \n  \n     Dios os guarde en su santo amor  \n Hasta el da en que lleguemos,  \n A la patria do estaremos  \n Para siempre con el Salvador.  \n     CORO  \n Al venir Jess nos veremos  \n A los pies de nuestro Salvador  \n Reunidos todos seremos  \n Un redil con nuestro buen Pastor.  \n  -2- \n Dios os guarde en su santo amor  \n En la senda peligrosa  \n De esta vida tormentosa,  \n Os conserve en paz y sin temor.  \n     -3-\n Dios os guarde en su santo amor  \n Os conduzca su bandera,  \n Y os esfuerce en gran manera  \n Con su Espritu consolador.  \n     -4-  \n Dios os guarde en su santo amor;  \n Con su gracia l os sostenga, \n  Hasta que el Maestro venga  \n A fundar su reino en esplendor.  \n ",track:"1KOF_p9i0IOjk5cE-tKtTrHKHmgB7zKRr",demo:"1un3gBq6xw1Sok75mZcQgGlF15EVylz_9"},
{id:70,title:"  70 .- DISTRIBUID MENSAJEROS  ",anthem:"        70.- DISTRIBUID MENSAJEROS  \n  \n     Hablad del Mensajero,  \n Santo y fiel predicador,  \n Decid al mundo entero  \n Que habla de salvacin.  \n    CORO  \n Luchad, luchad,  \n Feliz distribuidor;  \n Luchad, luchad,  \n Que contigo va  \n El buen Pastor.  \n     -2-  \n Pregona el Mensajero,  \n No temas, siervo de Dios  \n Pues gua tu sendero  \n Jess, el Salvador.  \n     -3-  \n Escucha al Mensajero  \n Pueblo prevaricador,  \n Mas si eres obstinado  \n No irs con el Seor.  \n ",track:"1srvThAlfXmbI9IS3cJFDaXzpjP_l7bEc",demo:"1eaEkjzyRliYuVAovyss_a1-Iq5uNSttG"},
{id:71,title:"  71 .- DIVINA LUZ  ",anthem:"                 71.- DIVINA LUZ  \n  \n     Divina luz, con tu esplendor benigno  \n Guarda mi pie,  \n La noche es densa y spero el camino;  \n Mi gua s.  \n Harto distante de mi hogar estoy,  \n Que al dulce hogar de las alturas voy.  \n     -2-  \n Amargos tiempos hubo en que tu gracia  \n No supliqu,  \n De mi valor fiando en la  eficacia  \n No tuve fe.  \n Mas hoy deploro aquella ceguedad.  \n Prstame, oh Luz! tu grata claridad.  \n     -3-  \n Guiando T, la noche es esplendente,  \n Y cruzar  \n El valle, el monte, el risco y el torrente,  \n Con firme pie;  \n Hasta que empiece el da a despuntar.  \n Y entre al abrigo de mi dulce hogar.  \n ",track:"1VootD-jKW3OwF2t0koHinGx_ZpeuLKDI",demo:"1XdOo7qP42aEvCMseI4IDLUOHxX52YSP6"},
{id:72,title:"  72 .- DO TU NECESITES QUE VAYA IRE ",anthem:"        72.- DO T NECESITES QUE VAYA, IR  \n  \n     En la montaa podr no ser,  \n Ni sobre el rugiente mar;  \n Podr no ser en la ruda lid  \n Do Cristo me quiere emplear;  \n Mas, si l me ordenare seguir aqu  \n Senderos que yo ignor, \n  Confiando en l le dir: Seor,  \n Do T quieras que vaya, ir.  \n     CORO  \n Do T necesites que vaya, ir,  \n A los valles, los montes o el mar  \n Decir lo que quieras, Seor, podr.  \n Lo que quieras que sea, ser.  \n     -2-  \n Quiz hay palabras de santo amor  \n Que Cristo me ordena hablar,  \n Y en los caminos que reina el mal,  \n Algn pecador salvar.  \n Seor, si quieres mi gua ser,  \n Mi oscura senda andar;  \n Tu fiel mensaje podr anunciar,  \n Y as lo que quieras, dir.  \n     -3-  \n El vasto mundo lugar tendr  \n Do pueda con noble ardor.  \n Gastar la vida que Dios me da,  \n Por Cristo, mi Salvador;  \n Y siempre confiando en tu gran bondad,  \n Tus dones todos tendr,  \n Y alegre, haciendo tu voluntad  \n Lo que quieras que sea, ser.  \n ",track:"1qUxXiVT3EkOGdQGPF7kYvVvqknHRdYRc",demo:"1XucBaTzJ5o_yml25hrt7jzBl6jTwYYyL"},
{id:73,title:"  73 .- DULCE CONSUELO  ",anthem:"        73.- DULCE CONSUELO  \n     En Jesucristo, mrtir de paz,  \n En horas negras de tempestad,  \n Hallan las almas dulce solaz,  \n Grato consuelo, felicidad.  \n     CORO  \n Gloria cantemos al Redentor  \n Que por nosotros quiso morir.  \n Y que la gracia del Salvador  \n Siempre dirija nuestro vivir.  \n     -2-  \n En los peligros y en el dolor,  \n A cada paso su proteccin;  \n Calma le infunde, santo vigor,  \n Nuevos alientos al corazn.  \n     -3-  \n Cuando en la lucha falta la fe,  \n Y el alma siente desfallecer,  \n Cristo nos dice: Yo os colmar  \n De rica gracia, santo poder.  \n ",track:"1xBAJic6ToksQeaFs6JV3nNxqhJuYd_C9",demo:"1bi48zc-kYyNkgKbIdQJznlqI890kYdit"},
{id:74,title:"  74 .- DULCE COMUNION  ",anthem:"        74.- DULCE COMUNIN  \n  \n     Dulce comunin la que gozo ya  \n En los brazos de mi Salvador;  \n Qu gran bendicin en su paz me da!  \n Oh, yo siento en m su tierno amor!  \n     CORO  \n Libre, salvo,  \n De cuidados y temor!  \n Libre, salvo,  \n Amparado por mi Salvador!  \n     -2-  \n Cun dulce es vivir, cun dulce es gozar  \n En los brazos de mi Salvador!  \n Quiero estar con l y a su lado andar,  \n Siendo objeto de su tierno amor.  \n     -3-  \n En la tentacin siempre triunfar  \n Yendo al lado de mi Salvador,  \n Nada me podr apartar de l,  \n En sus brazos no tendr temor.  \n ",track:"1xBAJic6ToksQeaFs6JV3nNxqhJuYd_C9",demo:"1gtFbNrJEKBxvsQ5F6n8ww7YqELKVhT8L"},
{id:75,title:"  75 .- DULCES MELODIAS  ",anthem:"        75.- DULCES MELODAS  \n  \n     Mi Dios me envi del cielo un canto  \n Melodioso, inspirador;  \n Lo cantar con gozo y gratitud,  \n Con muy tierno y dulce amor.  \n    CORO  \n Suenan melodas en mi ser  \n De un canto celestial, sonoro, angelical;  \n Suenan melodas en mi ser  \n De un dulce canto celestial.  \n     -2-  \n Amo a Jess, que en el Calvario  \n Mis pecados ya borr.  \n Mi corazn se inflama en santo amor, \n Que en mi ser l derram. \n     -3-  \n Me gozar all en la gloria  \n Del gran trono en derredor,  \n Cantando siempre con los ngeles,  \n Alabanzas al Seor.  \n ",track:"1ix6jjlLUKTrPv1VjPlCtIx60WOiXXSWw",demo:"1ief0-qYOrT0WIAdwWL_1y4veh9SxwP3b"},
{id:76,title:"  76 .- DULCE ORACION  ",anthem:"        76.- DULCE ORACIN  \n  \n     Dulce oracin, dulce oracin,  \n De toda influencia mundanal  \n Elevas t mi corazn  \n Al tierno Padre celestial.  \n Oh, cuntas veces tuve en ti  \n Auxilio en ruda tentacin!  \n // : Y cuntos bienes recib  \n Mediante ti, dulce oracin : //  \n     -2-  \n Dulce oracin, dulce oracin,  \n Al trono excelso de bondad  \n T llevars mi peticin  \n A Dios, que escucha con piedad.  \n Por fe espero recibir  \n La gran divina bendicin,  \n // : Y siempre a mi Seor servir  \n Por tu virtud, dulce oracin : //  \n     -3-  \n Dulce oracin, dulce oracin,  \n Qu aliento y gozo al alma das;  \n En esta tierra de afliccin,  \n Consuelo siempre me sers.  \n Hasta el momento en que ver  \n Francas las puertas de Sin,  \n // : Entonces, me despedir  \n Feliz de ti, dulce oracin : //  \n ",track:"1yAuY_b_zZ3eu2kiBXAIQ2Co4yxo-QlCJ",demo:"1kVXPVvRUOY4DncYAVDL9B3Keb-ACjwbm"},
{id:77,title:"   77 .- EL AMOR DE DIOS  ",anthem:"        77.- EL AMOR DE DIOS  \n  \n     Oh, amor de Dios! Tu inmensidad  \n El hombre no podr contar,  \n Ni comprender la gran verdad  \n Que Dios, al hombre pudo amar.  \n Al primer par de su hogar  \n Echados, compadeci;  \n Y les visti, les perdon  \n Y un Redentor prometi.  \n     CORO  \n Oh amor de Dios! Brotando est  \n Inmensurable, eternal.  \n Por las edades durar  \n Inagotable raudal.  \n     -2-  \n Si fuera tinta todo el mar  \n Y todo el cielo un gran papel,  \n Y cada hombre un escritor  \n Y cada hoja un pincel.  \n Nunca podran describir  \n El gran amor de Dios,  \n Que al hombre pudo redimir  \n De su pecado atroz.  \n     -3-  \n Y cuando el tiempo pasar  \n Con cada reino mundanal,  Y cada trono caer  \n Con cada trama y plan carnal.  \n El gran amor del Redentor  \n Por siempre durar.  \n La gran cancin de salvacin  \n Su pueblo cantar. ",track:"1UUvSXq8sqJwQOl21IpOVjOMJhcDzx1Hv",demo:"1pYdALp_XgKJHUa5-OCwFUE1xyhp6jvfH"},
{id:78,title:"  78 .- EL APOSENTO ALTO  ",anthem:"        78.- EL APOSENTO ALTO  \n  \n     En un aposento alto  \n Con unnime fervor,  \n Ciento veinte esperaban  \n La promesa del Seor.  \n     CORO  \n // : Dios, manda tu gran poder! : //  \n A cada corazn.  \n    -2-  \n Con estruendo de los cielos  \n Descendi la gran virtud,  \n Todos fueron bautizados  \n Con el Santo Espritu.  \n     -3-  \n Este gran poder antiguo  \n Es el fiel celeste don;  \n Prometido a los creyentes  \n De humilde corazn.  \n     -4-  \n Dios est restituyendo  \n Este gran Pentecosts,  \n Y el Espritu, sus dones  \n Nos reparte otra vez.  \n ",track:"1ryhpAOdXeBVYs9FCvwBDeNHBHNVmxVkD",demo:"15P998qP9koFAkLbIs-eTokdEQknkR9cy"},
{id:79,title:"  79 .- EL CRUCIFICADO ",anthem:"            79.- EL CRUCIFICADO  \n     Rey de los cielos, que viniste a dar  \n Ofrenda eterna por la humanidad;  \n Un holocausto tu cuerpo,  \n Tu rostro manso y sereno;  \n De aquel costado que traspasaron,  \n Fluye la sangre que lava del mal.  \n Bendito Pacto que nos da perdn!  \n A los enfermos, salud, bendicin!  \n     -2-  \n Te maltrataron sin ms compasin,  \n Menospreciado como un malhechor,  \n Tus manos fueron clavadas  \n En aquel tosco madero,  \n Tan solo y triste! Tan despreciado!  \n De la cruz baja si T eres de Dios,  \n As clamaba la turba infernal,  \n Cual llama ardiente sobre aquel altar.  \n     -3-  \n Tus labios buenos resecos de sed  \n Fueron mojados con vinagre, hiel,  \n Por qu me has desamparado?  \n Por qu te vas de mi lado?  \n Clamaba al Padre que no escuchaba  \n El triste ruego de nuestro Seor.  \n Vctima santa que con gran dolor  \n Dios ofreciera por la redencin.  \n Infinito amor!  \n ",track:"1VH2TB8IJxYhqDvl8lxq7q6Xo5Hw1avRB",demo:"1gpp4ZagG9k3CwvrwYXlNwY91ip0esTGg"},
{id:80,title:"  80 .- EL DIA DE LAS MADRES  ",anthem:"        80.- EL DA DE LAS MADRES  \n  \n     Un cntico de gracias  \n Queremos elevar,  \n Por nuestras buenas madres  \n Que T, Oh Dios! nos das.  \n Nos tienen tanto amor,  \n Tan fieles ellas son  \n Y da y noche, sin cesar  \n Nos cuidan con bondad.  \n     -2-  \n Palabras cariosas  \n Nos dan con dulce voz;  \n Las amaremos siempre  \n De todo corazn.  \n Debieran de gozar  \n Salud, felicidad;  \n Reciban toda bendicin  \n De Ti, Oh santo Dios!  \n ",track:"1O5erAvy_GgiAZOmhcPBSEodITyU50S5d",demo:"1jFuc4LX52N3L85HQ0cwLOD5_Q1pvpenp"},
{id:81,title:"  81 .- EL ETERNO AMOR DEL PADRE ",anthem:"        81.- EL ETERNO AMOR DEL PADRE \n \n     El eterno amor del Padre \n Brilla como un faro all, \n Mas, las luces de la costa \n l, al fiel el cargo da. \n     CORO \n Dejad, pues, que vuestras luces \n Den sus rayos sobre el mar; \n Hay un nufrago sufriendo \n Que tal vez podis salvar. \n     -2- \n Viene noche cruel en males, \n Ruge horrendo el bravo mar, \n Los ansiosos tripulantes \n Quieren luces divisar. \n     -3- \n Que Dios brille en ti, hermano, \n Pues los nufragos estn \n Hoy buscando el santo puerto, \n Como asilo all ellos van. \n ",track:"1i1WCpW3wqEiWoGJNU52w5a6sLEksujpA",demo:"1DI_tjUus0I1dyInxVepmFGgpyIa6E-a0"},
{id:82,title:"  82 .- EL GRAN DIA DEL JUICIO  ",anthem:"         82.- EL GRAN DA DEL JUICIO \n \n     So que el gran da del juicio \n Lleg y son el clarn; \n So ver los pueblos reunidos \n Para or de su suerte sin fin. \n Del cielo baj un gran ngel \n Y parado en tierra y mar, \n Jur, con su diestra alzada, \n Que el tiempo ya no ms ser. \n     CORO \n Con llanto y duelo, entonces, \n Los perdidos su cuenta darn; \n Clamarn a las rocas: Cubridnos! \n Orarn, pero tarde ser. \n     -2- \n El rico lleg, mas su oro \n Se fue y se desvaneci. \n Cual pobre parse ante el trono, \n De sus deudas a Dios se acord. \n El grande tambin, mas la muerte, \n Le haba quitado su honor \n Y el ngel, abriendo los libros \n No hall nada en su favor. \n      -3- \n Vino el moralista al juicio, \n Mas, vana fue su pretensin; \n Tambin los que a Cristo mataron, \n Hicieron moral profesin. \n Y el alma que daba la excusa: \n Hoy no, otro da mejor. \n Hall que por siglos eternos \n Sufrira por su gran error. \n ",track:"15fuCpNTKHoSFW6kOpnH6Q-o2VGMOMluV",demo:"1J6U7iZiUQeArdm_s3jrKpMzT-tmH4F3q"},
{id:83,title:"  83 .- EL MAESTRO DE LAS AGUAS ",anthem:"        83.- EL MAESTRO DE LAS AGUAS \n \n     Una tarde en el mar se encontraban \n Los discpulos con el Maestro, \n Mientras ellos bogando se hallaban \n El Seor descansaba su cuerpo. \n Mas la mar luego fue azotada \n Por una tempestad y los vientos, \n El abismo les amenazaba, \n Mas, recuerdan que duerme el Seor. \n     CORO \n Maestro, Maestro, despierta tu sueo, \n Reprende los vientos \n Que perece nuestra embarcacin! \n      -2- \n El Seor les mir tiernamente \n Y les dice con voz dulce y suave: \n Si tuvieran fe en m plenamente, \n Aunque duerma, seguros se hallasen. \n A la mar le habl en el momento, \n A los vientos les manda que callen. \n Calma el mar, enmudecen los vientos, \n Porque habla el divino Creador. \n        -3- \n Cuntas almas se encuentran perdidas \n Naufragando en el mar del pecado, \n Sin saber que Jess es la vida, \n Y sin l, no podrn ser salvadas. \n Ven a Cristo a entregarte enseguida \n Y salvarte podr del pecado, \n Porque tu alma ser redimida \n Por la sangre del Hijo de Dios. \n ",track:"1UzyE7VdvnilInVt5sJIUW4csBRJUKyqU",demo:"1Dw2dPZKorC2tGm_hBjCYy5BcKtAXYjoj"},
{id:84,title:"  84 .- EL MEJOR AMIGO ",anthem:"        84.- EL MEJOR AMIGO \n \n     Es Jesucristo mi amigo mejor, \n Tierno y amante cual otro no habr; \n Toda mi vida descansa en su amor, \n Tan slo en l, segura estar. \n      CORO \n Oh dulce amigo! cerca de Ti, \n Mi senda quiero firme seguir; \n Sentir yo quiero que mora en m \n Tu santa gracia para vivir. \n     -2- \n l en mis pruebas desciende hasta m \n Y hace en mi pecho la dicha nacer; \n Nunca por l olvidado me vi, \n Nunca falt su amor en mi ser. \n     -3- \n l en mis dudas me inunda de luz, \n Y hallo el sendero que debo seguir; \n Todo en mi vida lo debo a Jess, \n Slo por l yo puedo vivir. \n     -4- \n l me conduce cual tierno Pastor, \n Lleva mi vida por sendas de paz; \n l me rescata de todo temor, \n Dndome el gozo de ver su faz. \n ",track:"1LlQRGhrUJNiT8g1GDkIv-iA_RN_Ije8g",demo:"1rKLLRbWHuuSLiz9QOd0pSb5tLQo9FcKp"},
{id:85,title:"  85 .- EL MENSAJERO  ",anthem:"        85.- EL MENSAJERO \n \n      El Mensajero en las manos tomad, \n Su grande esfuerzo en alto alzad; \n Hasta la victoria poder alcanzar, \n Y al vil enemigo poder derrotar. \n  CORO \n Vamos marchando, tengamos valor, \n Armas nos sobran nos dice el Seor, \n Conflicto terrible Satn provoc, \n Las armas tomemos que ya comenz. \n     -2- \n El Mensajero al mundo anunciad, \n Mil luchas y pruebas nos ha de costar; \n Con Cristo por jefe, quin se ha de cansar? \n Cantando marchemos soldados del Seor. \n     -3- \n El Mensajero es voz del Seor, \n Que anuncia el camino de vida inmortal; \n Es Dios nuestra fuerza, es nuestra cancin, \n Resueltos marchemos alzando pendn. \n ",track:"1-_d7y0syBi9eEsPSZXeXEA8TKI021g3i",demo:"1y6M9jga0b0q1RFsAA7mmYUqVVAXiht05"},
{id:86,title:"  86 .- EL MUNDO NO ES MI HOGAR ",anthem:"        86.- EL MUNDO NO ES MI HOGAR  \n \n     La senda ancha dejar, \n Yo quiero por la angosta andar, \n Y muchos no sabrn por qu, \n Mas, voy a mi celeste hogar. \n     CORO \n No puede el mundo ser mi hogar, \n No puede el mundo ser mi hogar; \n En gloria tengo mi mansin, \n No puede el mundo ser mi hogar. \n    -2- \n Algunos quieren verme ir, \n Por el sendero de maldad; \n Or no puedo su llamar, \n Mas voy a mi celeste hogar. \n     -3- \n Oh, ven conmigo, pecador, \n Y sigue en pos del Salvador! \n Por qu no quieres t buscar \n La hermosa tierra ms all? \n ",track:"1a7OWCHGWrCC86uqSndTcYbZ-eikWrQCp",demo:"164Z9LGAwJERh-fhYX7QNwPE4CBazpptI"},
{id:87,title:"  87 .- EL NOMBRE DE JESUS  ",anthem:"        87.- EL NOMBRE DE JESS \n \n     Tan dulce el nombre de Jess! \n Sus bellas notas cantar, \n Que mi alma llena al proclamar \n El nombre de Jess. \n      CORO \n Cristo, Oh, qu dulce es! \n Cristo, para siempre es: \n Cristo, yo te aclamar \n Por siempre, Oh, mi Cristo! \n     -2- \n Adoro el nombre de Jess, \n Jams me faltar su amor, \n Y pone aparte mi dolor, \n El nombre de Jess. \n     -3- \n Tan puro el nombre de Jess, \n Que mi pesar pudo quitar, \n Y grata paz a mi alma da \n El nombre de Jess. \n     -4- \n El dulce nombre de Jess \n Por siempre quiero alabar, \n Y todos deben ensalzar \n El nombre de Jess. \n ",track:"1K4NmWEj6IWHt4FGjQq9jDKhgw_pph9Rq",demo:"16dowbLfp7gVSvIkYc4C6kwg_TE5e9mLW"},
{id:88,title:"  88 .- EL PASTOR  ",anthem:"        88.- EL PASTOR \n \n     Tengo lejanas ovejas \n Que no son de este redil, \n Y me conviene traerlas \n Para as juntos vivir. \n Lejos ya de las tinieblas, \n Rodeados de un gran amor, \n Donde slo habr un redil, \n Un redil y un Pastor. \n     CORO\n  Od la voz de Cristo que nos dice:\n  Yo soy el Pastor, \n El buen Pastor, \n Mi vida doy por las ovejas. \n Yo soy la puerta, entrad por M \n Y al cielo todos pasarn. \n        -2- \n En estos ltimos tiempos, \n Se deja or el Pastor, \n Y nos ensea el camino \n Que nos conduce hasta Sin. \n Firmes luchemos hermanos, \n Perseverad hasta el fin, \n Para que el da de gloria \n Estemos en su redil. \n ",track:"1BNzhRWJdobMNOVlBQM3tmiK9xmiguTy7",demo:"1CuWK8P4REN87o_uIu2NEWSJaxYUXbBca"},
{id:89,title:"  89 .- EL RAPIDO FELIZ  ",anthem:"        89.- EL RPIDO FELIZ \n \n     Santo Espritu, llena mi vida, \n Santo Espritu, llname hoy, \n Santo Espritu, llena mi vida, \n  Por los perdidos danos compasin.\n  // : Alabanzas, alabanzas, alabanzas, \n Al Seor Jess! : // \n Nos vamos a palacio en el Rpido Feliz, \n Escrito lleva el nombre tan dulce de Jess, \n El guardia grita: Al cielo? \n Y respondemos, S. \n Nos vamos a palacio En el Rpido Feliz. \n ",track:"1OO9LlXUXF8MOMrR5qjFfbVgAzTfTemR1",demo:"1s-bxJGd6qE7xc9RqUAlaKTwovW7_vMkP"},
{id:90,title:"  90 .- EL VARON DEL GRAN DOLOR  ",anthem:"        90.- EL VARN DE GRAN DOLOR \n \n     El varn de gran dolor \n Es el Hijo del Seor, \n Vino al mundo por amor. \n Aleluya! Es mi Cristo! \n     -2- \n  l llev la cruenta cruz, \n Para darnos vida y luz, \n Ya mi cuenta l pag. \n Aleluya! Es mi Cristo! \n      -3- \n l por m quiso morir, \n Hoy por l puedo vivir, \n Quiero slo a l servir. \n Aleluya! Es mi Cristo! \n     -4- \n Cuando venga nuestro Rey, \n Luego yo su faz ver, \n Y sus glorias cantar. \n Aleluya! Es mi Cristo! \n ",track:"1vIpYzaSYLb816sNopZwQJ3s_LWoeEUpR",demo:"1NB7ETkiGH4FGuaLqOuim5uMFsEtrNnDF"},
{id:91,title:"  91 .- EN BUSCA DE OBREROS  ",anthem:"        91.- EN BUSCA DE OBREROS \n \n     Cristo est buscando obreros hoy \n Que quieran ir con l, \n Quin dir: Seor, contigo voy, \n Yo quiero serte fiel? \n     CORO \n Oh, Seor, es mucha la labor! \n Y obreros faltan ya; \n Danos luz, ardiente fe y valor, \n Y obreros siempre habr. \n      -2- \n Cristo quiere mensajeros hoy, \n Que anuncien su verdad; \n Quin dir: Seor, yo listo estoy, \n Har tu voluntad? \n     -3- \n Hay lugar, si quieres trabajar, \n De Cristo en la labor; \n Puedes de su gloria al mundo hablar \n De su bondad y amor. \n     -4- \n Vives ya salvado por Jess? \n Su amor conoces ya? \n Habla, pues, anuncia que en la luz \n De Cristo vives ya. \n ",track:"1ICYnQiOhreqraq6mn64r3JLdKTx5cRIZ",demo:"1T2hdzSI09oPkVtGVpr86_HfR59MPemZk"},
{id:92,title:"  92 .- EN CALVARIO  ",anthem:"        92.- EN CALVARIO \n \n     En Calvario muri Jess, \n Crucificado en una cruz, \n Su sangre derram por m \n Comprndome perdn all. \n     CORO \n En Calvario, en Calvario, \n Donde Jess sangre verti, \n En Calvario, en Calvario, \n El Salvador por m muri. \n     -2- \n En medio de la oscuridad, \n Sufri Jess en humildad, \n Y al morir el velo abri, \n La gloria as nos revel. \n     -3- \n Diste tu vida oh Seor, \n Por m un pobre pecador, \n La agona, la triste cruz, \n Sufriste T por m, Jess. \n ",track:"1NhRiLTACi4lpyGWehdWJiax25MLTXhuL",demo:"1H-GLLxewR3qcUNuqlzEdsmdC44kKIaK3"},
{id:93,title:"  93 .- EN JESUS ENCONTRE DULCE PAZ ",anthem:"        93.- EN JESS, MI SEOR, ENCONTR DULCE PAZ \n \n     En Jess, mi Seor, encontr dulce paz, \n En Jess, quien por m se entreg; \n Si acudieres a El este don t tendrs, \n Ve, que a nadie jams rechaz. \n     CORO \n Sublime don! Don sin igual! \n Solamente l podr conceder; \n Es un rayo de luz, de su luz celestial \n Que en nosotros l quiere encender. \n     -2- \n En Jess encontr inefable perdn \n Cuando a l suplicante acud; \n En amor encendi mi falaz corazn, \n Y por l nueva fe recib. \n     -3- \n En Jess nueva vida tendrs, pecador, \n Sin tardanza dirgete a l; \n En tu pecho pondr nueva fe, nuevo amor, \n Y despus, siempre srvele fiel. \n ",track:"1NP15lnyuA-dAmGA9-IHSdsWonGSPyePa",demo:"1ERhvl1smD86ocptqjj7dzQOId2rclUXU"},
{id:94,title:"  94 .- EN LA CELICA MORADA  ",anthem:"        94.- EN LA CLICA MORADA \n \n     En la clica morada, \n De las cumbres del Edn, \n Donde cada voz ensalza \n Al Autor de todo bien, \n El pesar recordaremos \n Y la triste nublazn? \n Tantas luchas del espritu \n Con el dbil corazn? \n     CORO \n Oh, all ser gratsimo \n En el proceder pensar, \n Del Pastor fiel y benfico \n Que nos ayud a llegar! \n     -2- \n Oracin, deberes, penas, \n Vas que anduvimos ya, \n Poseyendo las riquezas \n Que Jess nos guarda all. \n La memoria retendremos \n A cubierto del dolor, \n Del camino largo, asprrimo \n Con sus luchas, su temor? \n     -3- \n La bondad con que nos mira \n Sin cansarse, cuando ve \n Poco fruto en nuestra vida, \n Y tan dbil nuestra fe. \n Nos acordaremos de ella \n En aquel dichoso hogar \n De eternal aurora esplndida, \n E inefable bienestar? \n ",track:"1h2h02H8LeE6ISBzQq3ar3EY_CvX4OA7g",demo:"1KNiijq77ZjcuRzuZAh2Sox5OZbvgZp5N"},
{id:95,title:"  95 .- EN LA NUEVA JERUSALEM  ",anthem:"        95.- EN LA NUEVA JERUSALEM \n \n    Cuando cesen los conflictos \n De la vida terrenal, \n Y dejemos este mundo de afliccin, \n Entraremos por las puertas \n De la patria celestial \n En la nueva Jerusalem. \n     CORO \n Cantaremos con los santos \n La cancin de redencin \n En Jerusalem, en Jerusalem. \n Con acentos de alegra, \n Alabando al Salvador \n En la gran Jerusalem. \n        -2- \n Aunque el mar embravecido \n Y las olas del turbin \n Siempre agiten nuestra \n Pobre embarcacin, \n Fiando en Cristo llegaremos \n A la playa celestial \n En la nueva Jerusalem. \n     -3- \n Consagremos nuestras vidas \n Al servicio del Seor, \n Siempre hablemos \n De su grande salvacin, \n Si en su via trabajamos \n Nos espera galardn \n En la nueva Jerusalem. \n     -4- \n En aquel pas hermoso \n Do jams se dice adis, \n Gozaremos el descanso sin afn; \n Cara a cara all veremos \n A Jess quien nos salv, \n En la nueva Jerusalem. \n ",track:"1_RcAwAlJ8A-dn8u5eLitEuSmYvZTNnM7",demo:"1wuokbzJ14STYv6ifqDqkF1VSmsfAEgdf"},
{id:96,title:"   96 .- EN LA CRUZ  ",anthem:"        96.- EN LA CRUZ \n \n     Me hiri el pecado, fui a Jess \n Mostrle mi dolor; \n Perdido, errante, vi su luz, \n Bendjome en su amor. \n     CORO \n En la cruz, en la cruz, \n Do primero vi la luz \n Y las manchas de mi alma yo lav.\n  Fue all por fe, do vi a Jess \n Y siempre feliz con l ser. \n     -2- \n Sobre una cruz, mi buen Seor, \n Su sangre derram \n Por este pobre pecador, \n A quien as salv. \n           -3- \n Venci la muerte con poder \n Y al cielo se exalt; \n Confiar en l es mi placer, \n Morir, no temo yo. \n     -4- \n Aunque l se fue, solo no estoy \n Mand al Consolador. \n Divino Espritu que hoy \n Me da perfecto amor. \n ",track:"1ccIZ9FusR4t1ieANnHs2Ig6Jo0foacQD",demo:"1kPXb38LFnH07MijlJjcUI1ojMRlpRRQM"},
{id:97,title:"  97 .- EN LA VIA DEL SEOR  ",anthem:"        97.- EN LA VIA DEL SEOR \n \n     Yo quiero trabajar por el Seor, \n Confiando en su palabra y en su amor \n Quiero yo cantar y orar, \n Y ocupado siempre estar \n En la via del Seor. \n     CORO \n Trabajar, y orar \n En la via, en la via del Seor \n S, mi anhelo es orar \n Y ocupado siempre estar, \n En la via del Seor. \n     -2- \n Yo quiero cada da trabajar \n Y esclavos del pecado libertar, \n Conducirlos a Jess, \n Nuestro gua, nuestra luz, \n En la via del Seor. \n     -3- \n Yo quiero ser obrero de valor \n Confiando en el poder del Salvador, \n Y el que quiera trabajar \n Hallar tambin lugar, \n En la via del Seor. ",track:"1HwRDjte4kEqOAn-OcwShPkygslp8zlwt",demo:"1H_5LO7qOMwnzwpVuG54xiNcG7zlkP9nn"},
{id:98,title:"  98 .- EN LAS AGUAS DEL BAUTISMO",anthem:"        98.- EN LAS AGUAS DEL BAUTISMO \n \n     En las aguas de la muerte \n Sumergido fue Jess, \n Mas su amor no fue apagado \n Por sus penas en la cruz; \n Levantse de la tumba \n Sus cadenas quebrant, \n Y triunfante y victorioso \n A los cielos ascendi. \n     -2- \n En las aguas del bautismo, \n Hoy, confieso yo mi fe, \n Jesucristo me ha salvado \n Y en su amor me gozar. \n En las aguas humillantes \n A Jess siguiendo estoy; \n Desde ahora para el mundo \n Y el pecado, muerto soy. \n     -3- \n Ya que estoy crucificado, \n Cmo ms podr pecar? \n Ya que soy resucitado, \n Santa vida he de llevar. \n Son las aguas del bautismo \n Mi seal de salvacin, \n Y yo quiero consagrarme \n Al que obr mi redencin. ",track:"1aDmAzwUq7wVaSwVmddGhWNsU91gRPF7d",demo:"1vRUr-Qj9rBqZ8ESa8gsQ-_VlFgzlv6jJ"},
{id:99,title:"  99 .- EN LAS MONTAAS ",anthem:"        99.- EN LAS MONTAAS \n \n     Id a traer el alma perdida, \n Y sealar a Cristo Jess, \n Que puede dar eterna la vida \n Al caminar por sendas de luz. \n     CORO \n Id a buscar en las montaas, \n A los errantes, regresad, \n A mi redil, dice el Maestro, \n Donde tendrn seguridad. \n     -2- \n Id a buscar con santo cuidado \n A los que van muy lejos de Dios, \n Venid a m, perdidos, cansados \n Que del Maestro dulce es la voz. \n     -3- \n Yo quiero ir con Cristo siguiendo \n Por todas partes con la misin, \n De levantar al que va cayendo \n Al sealar de Dios la misin. \n ",track:"1QxDLRtsX0IBL02cyyL3F9teocGMM82SG",demo:"1kAcpg4r8wCGzfAs3q4V-6S9RDRvIimU8"},
{id:100,title:"  100.- EN MEDIO DEL MORTAL DOLOR",anthem:"        100.- EN MEDIO DEL MORTAL DOLOR \n \n     En medio de mortal dolor, \n La cruenta cruz yo vi; \n Y all raudal de gracia hall \n Bastante para m. \n     CORO \n En la cruz fluye sin cesar, \n Insondable cual es el mar, \n Esta gracia que brota all \n Basta para m. \n     -2- \n Sufriendo fue mi corazn \n Y apenas pude all, \n Creer que gracia habra de hallar \n Bastante para m. \n     -3- \n Cuando en la cruz clavadas ya \n Mis culpas yo sent, \n Raudal de gracia a mi alma entr \n Bastante para m. \n     -4- \n Cuando en el cielo con Jess \n Alegre cante all, \n Dir que aquella gracia fue \n Bastante para m. \n ",track:"1t6tE9JZCmUNXtI7_b3OsgBKdSZ9wPDks",demo:"10LdMD5VUO8EbLdBrHVwtUANfheKT5_wq"},
{id:101,title:"  101.- EN PECADO YO VIVIA  ",anthem:"        101.- EN PECADO YO VIVA \n \n     En pecado yo viva \n Lejos del buen Salvador \n Mas, un da entend mi condicin. \n Yo clam a Jesucristo, \n Acept su santo amor. \n l me oy y ya me dio su salvacin. \n        CORO \n Oh, gloria sea a Cristo \n Con su plena salvacin! \n Alabemos al Seor con gran fervor! \n Tengo al cielo por mi blanco, \n Gozo de gran bendicin. \n Pronto en gloria he de ver al Salvador. \n     -2- \n Aunque el mundo me ofrezca \n Gran riqueza y placer, \n Ya no quiero ms de su gran vanidad, \n Mi Jess se fue al cielo \n las moradas a hacer, \n A los suyos les dar por heredad. \n     -3- \n Todo el oro y la plata \n De este mundo no podrn \n Separarme del amor de mi Jess,\n  Pues en l estoy contento \n Y no tengo ms afn, \n Ya mi noche se cambi en plena luz. \n     -4-\n Al llegar all al cielo \n Donde todo es esplendor, \n Quiero ver primero a mi Redentor, \n Y con todos los salvados \n Quiero darle gran loor, \n Alabanzas y amenes al Seor. \n ",track:"1gyGYkufBGKhHse9FgLTW8I00WDCw2j84",demo:"1F1eZt2fMGVsth-rmt_-G08d0S6BecFts"},
{id:102,title:"   102.- ENTERA CONSAGRACION  ",anthem:"        102.- ENTERA CONSAGRACIN \n \n     Que mi vida entera est \n Consagrada a Ti, Seor; \n Que a mis manos pueda guiar, \n El impulso de tu amor. \n     CORO \n Lvame en tu sangre, Salvador,\n  Lmpiame de toda mi maldad, \n Traigo a Ti mi vida para ser, Seor, \n Tuya, por la eternidad. \n     -2- \n Que mis pies tan slo en pos, \n De lo santo puedan ir, \n Y que a Ti, Seor, mi voz, \n Se complazca en bendecir. \n     -3- \n Que mi tiempo todo est \n Consagrado a tu loor; \n Que mis labios al hablar, \n Hablen slo de tu amor. \n     -4- \n Toma Oh Dios! mi voluntad, \n Y hazla tuya nada ms; \n Toma, s, mi corazn, \n Y tu trono en l tendrs. \n ",track:"1DZGTzG1qfB62ZUsFy9lwQK1TLT7d7c1C",demo:"1szT-xaBmG4cHhovlrChgLoPAoxpowz52"},
{id:103,title:"  103.- ERES LIMPIO EN LA SANGRE?",anthem:"        103.- ERES LIMPIO EN LA SANGRE? \n \n     Has hallado en Cristo plena salvacin, \n Por la sangre que Cristo verti? \n Toda mancha lava de tu corazn. \n Eres limpio en la sangre eficaz? \n     CORO \n Eres limpio en la sangre, \n En la sangre de Cristo Jess? \n Es tu corazn ms blanco que la nieve? \n Eres limpio en la sangre eficaz? \n     -2- \n Vives siempre al lado de tu Salvador \n Por la sangre que l derram? \n Del pecado eres siempre vencedor? \n Eres limpio en la sangre eficaz? \n     -3- \n Tendrs ropa blanca al venir Jess? \n Eres limpio en la fuente de amor? \n Ests listo para la mansin de luz? \n Eres limpio en la sangre eficaz? \n     -4- \n Cristo ofrece hoy pureza y poder, \n Oh, acude a la cruz del Seor! \n l la fuente es que limpiar tu ser \n Oh, acude a su sangre eficaz! \n ",track:"1kaU5PazFsDwR0wCiF6UtSdTSkbVUvqOB",demo:"199_6qh_gTUXMU3F2gZRlIzvw2CjJ09H3"},
{id:104,title:"  104.- ES EL TIEMPO DE LA SIEGA ",anthem:"        104.- ES EL TIEMPO DE LA SIEGA \n \n     Es el tiempo de la siega \n Y t sin vacilar \n Declarando con holgura, \n no hay que trabajar \n Mientras tanto que el Maestro \n Te vuelve a llamar: \n Joven, joven, ven, trabaja ya. \n     CORO \n Ven, y ve los campos blancos como estn, \n Aguardando manos que los segarn; \n Joven, despierta, hazlo pronto y alerta, \n S el primero en decirle: \n  Heme aqu, Seor. \n Por doquier se inclina la madura mies \n Que las ureas mueven y qu bella es! \n Joven, despierta, hazlo pronto y alerta, \n Pocos das hay que restan \n Para el Segador. \n     -2- \n Las gavillas que recojas, \n Jjoyas de esplendor, \n Brillarn en la corona \n Que dar el Seor; \n Busca pronto eternas joyas, \n Dios es premiador, \n Joven, joven, ven, trabaja ya.\n      -3- \n Va pasando la maana \n Y nunca volver, \n Pronto el tiempo de la siega \n Aqu terminar, \n Te hallars al fin vaco \n Ante el Creador, \n Joven, joven, ven, trabaja ya. \n ",track:"132EjvvzauDnGKMN1ZiP5PBg4KdxkCgkK",demo:"1NO27Y_qASgLXQCPSXi1Uyet3HgbArjMa"},
{id:105,title:"  105.- ESTOY RESUELTO  ",anthem:"        105.- ESTOY RESUELTO \n \n     Estoy resuelto a no ms quedarme \n Lejos del Salvador; \n A l yo quiero hoy entregarme, \n Me obliga su amor. \n        CORO \n A l doy mi alma \n Slo de l ser; \n Cristo, ten mi vida \n Eres T mi Rey. \n  -2- \n Estoy resuelto a seguir a Cristo \n Y serle siempre fiel; \n Escuchar la voz del Maestro, \n De nuestro Emmnuel. \n     -3- \n Estoy resuelto a que en pos de Cristo \n Siempre yo he de ir, \n Obedeciendo su gran mandato, \n Hasta de aqu partir. \n     -4- \n Estoy resuelto a que he de verle \n En nubes a Jess, \n En esplendor y honra mirarle, \n De l por siempre en luz. \n ",track:"1QR5Ywquy5emc2CBqSVoF9LYC9lQ0of7i",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:106,title:"  106.- ETRELLAS EN MI CORONA  ",anthem:"        106.- ESTRELLAS EN MI CORONA \n \n     Sin cesar siempre pienso \n En la tierra mejor, \n Do al ponerse mi sol, llegar, \n Y al hallarme en los cielos, \n Con Cristo el Seor, \n Mi corona de estrellas tendr? \n     CORO \n Mi corona tendr sus estrellas all \n En las almas que yo rescat? \n Cuando el sol ya decline \n Y me encuentre yo en Ti, \n Mi corona de estrellas, tendr? \n     -2- \n De la fuerza de Dios \n Esperando el poder, \n Trabajar quiero siempre y salvar, \n A las almas, y al fin,\n Cual estrellas saber \n Que en mis sienes irn a brillar. \n     -3- \n Oh qu gozo en los cielos \n Ser para m \n Vivas gemas poner a sus pies, \n Y tener en mi frente \n Corona que all \n Ornen joyas de tal brillantez! \n ",track:"1seP2TFfU5cQOLTOa9MJIbg3B6xaY0137",demo:"1iAlhlbuya5EeUSOL762jQHeMNDjE-G7Z"},
{id:107,title:"  107.- FELICES LOS HOMBRES  ",anthem:"  107.- FELICES LOS HOMBRES \n \n     // : Felices los hombres que son perdonados \n Y cuyos pecados borrados ya son : // \n // : Siendo renacidos del Espritu Santo \n Son testigos fieles en Cristo Jess : // \n      CORO \n Aleluya, aleluya, aleluya, \n Aleluya, aleluya, al Seor \n Aleluya, aleluya aleluya,\n  Aleluya, cun grande es su amor! \n     -2- \n // : Felices los hombres que van predicando\n  De Dios el mensaje de la salvacin : // \n // : Siendo revestidos del Espritu Santo \n A la esperanza eterna en Cristo Jess : // \n     -3- \n // : Felices los hombres que viven velando, \n Orando y esperando la venida del Seor : // \n // : En un solo instante sern arrebatados \n Y entrarn triunfantes al reino celestial : //  \n ",track:"1lgVxOTIJs0zRk4HixGnKqmwSJky6jaG0",demo:"1ra2OA2YPN6yO7vmocAOHyJUMSIa2cHQQ"},
{id:108,title:"  108.- FELIZ MOMENTO  ",anthem:"       108.- FELIZ MOMENTO \n \n     Feliz momento en que escog \n Servirte, mi Seor y Dios;\n  Preciso es que mi gozo en Ti \n Lo muestre hoy con obra y voz. \n      CORO \n Soy feliz! Soy feliz! \n Y en tu favor me gozar; \n En libertad y luz me vi \n Cuando triunf en m la fe; \n Y el raudal carmes \n Salud de mi alma enferma fue. \n     -2- \n Pas, mi gran deber cumpl \n De Cristo soy y mo es l, \n Me atrajo con placer segu, \n Su voz conoce todo fiel. \n        -3- \n Reposa dbil corazn; \n A tus contiendas pon ya fin, \n Hall ms noble posesin \n Disfruta, pues, del gran festn. \n ",track:"1EdLeeRrZFnVY2tMBZjrtvpPjnIMz6uZT",demo:"1v5gN3vQPHpi4GCebYYXBUrQISbgj_woz"},
{id:109,title:"   109.- FE SENCILLA  ",anthem:"        109.- FE SENCILLA \n \n     Cun gloriosa es la fe sencilla \n Del creyente fiel, \n Cristo es hoy como ayer el mismo, \n No hay mudanza en l; \n Salva aun al pecaminoso, \n Y hace al ciego ver;\n  Muchos cojos y sordos sanan\n  Por su gran poder.\n      CORO \n Cristo, ayer y hoy el mismo, \n Por los siglos es fiel. \n Todos mudan menos Cristo, \n Roca eterna es l. \n Roca eterna es l, Roca eterna es l; \n Todos mudan menos Cristo \n Roca eterna es l! \n     -2- \n Trae alivio a los que sufren, \n Calma la ansiedad; \n Lleva nuestras enfermedades, \n Nuestra iniquidad. \n Suele dar como en tiempo antiguo \n Al contrito paz, \n Dice: No te condeno, \n Vete y no peques ms.\n      -3- \n Cuando el borde de su vestido \n La mujer toc, \n Cristo dijo: Tu fe te salva, \n Y ella as san; \n Todava con nuestras culpas \n Quiere l cargar, \n Tus pecados y enfermedades \n Quiere hoy quitar. \n        -4- \n Conversaba Jess con ellos \n Yendo a Emmas, \n Y ense que era necesaria\n  Su muerte en la cruz; \n Volver otra vez al mundo, \n Pronto ven, Seor! \n Mas ser este mismo Cristo, \n Rey y Salvador. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:110,title:"   110.- FIELES SIERVOS DEL MAESTRO",anthem:"        110.- FIELES SIERVOS DEL MAESTRO\n \n      Fieles Siervos del Maestro, \n A la lid hoy avanzad, \n En su via hay trabajo \n Nunca debis descansar. \n Sus mandatos fiel haciendo, \n Sus promesas hoy creed \n El poder de Dios tomando \n Y gozando su merced. \n     CORO \n Cantad, cantad; Confiad, confiad \n Mostrad vuestra fe y amor, \n Su nombre alabad. Su nombre alabad \n Es vuestro Dios y Redentor;\n  Su voluntad cumplid \n Al mundo id, \n El Evangelio proclamad, \n Pues en nosotros Cristo siempre est \n Cantad, cantad. \n     -2- \n Levantad hoy la bandera \n Sobre todo monte y mar \n Anunciad al alma, aurora, \n De su noche de pecar, \n Buenas nuevas proclamando \n De Jess el santo amor, \n Los perdidos rescatando \n En el nombre del Seor. \n     -3- \n Es muy corto ya el tiempo, \n El trabajo cesar, \n Pronto viene el reposo \n La batalla l parar.\n  Por los suyos Cristo viene \n Van con l al cielo all, \n En Jess la Iglesia tiene, \n Su herencia eternal. \n ",track:"1wUGTQmOVa45HoKBrsAdxkaoiwJclJ2G6",demo:"1P2oqfudZ6p_8o3ZDr6JPNBVOwzTxPeR_"},
{id:111,title:"  111.- FIEL HUESPED  ",anthem:"        111.- FIEL HUSPED \n \n     Fiel husped a tu puerta est \n Anhela luego entrar,\n  Tu nombre sabe y tu morar, \n Escucha su llamar! \n Su mano traspasada est, \n Su rostro con sudor, \n Es el Seor, Rey celestial, \n Atiende su clamor! \n     CORO \n yelo, a tu puerta llama \n El divino Redentor. \n Quires ser de su Iglesia amada? \n brele tu corazn. \n     -2- \n Agnico Getseman, \n Vergenza cruel sufri, \n Su pura sangre carmes \n Con gozo derram. \n Salvarte quiso, pecador, \n De negra iniquidad, \n Despreciars t al Seor \n Que quiere en ti morar? \n     -3- \n El caro Husped hoy est \n Su voz dejando or, \n Y llama tierno, sin cesar, \n Le quires recibir? \n Despierta, oh alma! pues se va, \n Entrar le dejars? \n El rostro triste volver \n No llamar jams. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:112,title:"  112.- FIRMES Y ADELANTE  ",anthem:"        112.- FIRMES Y ADELANTE \n \n     Firmes y adelante, huestes de la fe, \n Sin temor alguno, que Jess nos ve. \n Jefe soberano, Cristo, al frente va, \n Y la regia ensea tremolando est. \n     CORO \n Firmes y adelante huestes de la fe, \n Sin temor alguno que Jess nos ve. \n      -2- \n Al sagrado nombre de nuestro Adalid \n Tiembla el enemigo y huye de la lid, \n Nuestra es la victoria, dad a Dios loor \n Y igalo el averno lleno de pavor. \n     -3- \n Muvase potente la Iglesia de Dios; \n De los ya gloriosos marchamos en pos. \n Somos slo un cuerpo, y uno es el Seor, \n Una la esperanza y uno nuestro amor. \n     -4- \n Tronos y coronas pueden perecer; \n De Jess la Iglesia fiel habr de ser, \n Porque la promesa nunca faltar. \n ",track:"1BuqlFn-3kPQZRckXAWVk9DkFKQZAVJyD",demo:"1YA1C7AeeVC6kOiMF5AjuzdRsCSKm3W8y"},
{id:113,title:"  113.- FUENTE DE LA VIDA ETERNA ",anthem:"     113.- FUENTE DE LA VIDA ETERNA \n \n     Fuente de la vida eterna \n Y de toda bendicin; \n Ensalzar tu gracia tierna \n Debe cada corazn; \n Tu piedad inagotable, \n Abundante en perdonar, \n nico ser adorable, \n Gloria a Ti debemos dar. \n     -2- \n De los cnticos celestes \n Te quisiramos cantar, \n Entonados por las huestes \n Que lograste rescatar; \n Almas que a buscar viniste \n Porque les tuviste amor \n De ellas te compadeciste \n Con tiernsimo favor. \n     -3- \n Toma nuestros corazones \n Llnalos de tu verdad \n De tu Espritu los dones, \n Y de toda santidad; \n Guanos en obediencia \n Humildad, amor y fe; \n Nos ampare tu clemencia; \n Salvador, propicio s. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:114,title:"   114.- GEDEONISTAS AVANZAD  ",anthem:"        114.- GEDEONISTAS, AVANZAD \n \n     La trompeta anuncia ya \n Que se apresten las huestes del Seor. \n Quin a l recibir? \n Los valientes \n Y esforzados estarn \n Para luchar con gran tesn \n Teniendo a Cristo como Capitn. \n     CORO \n Van saliendo decididos \n Los fieles Gedeonistas, \n Enarbolando el glorioso \n Pabelln de libertad, \n El mensaje de la redencin. \n Van saliendo victoriosos a las Amricas, \n Van llevando la gran verdad \n Del evangelio de Cristo, \n Gedeonistas, avanzad. \n     -2- \n Nuestra lucha dura es, \n Vamos todos combatiendo por la fe. \n Con Dios, quin nos detendr? \n Si somos guiados \n Por quien gui a Josu, \n El Dios de Abraham y de Geden, \n Pues lucharemos sin vacilacin. \n     -3- \n La trompeta anunciar \n La segunda venida del Seor, \n Quin a l recibir? \n Los que lucharon \n Aqu sin vacilar \n Por libertar al pecador, \n Estos tendrn de Dios el galardn. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:115,title:"  115.- GETSEMANI  ",anthem:"              115.- GETSEMAN \n \n     Getseman, \n Recuerdo los momentos \n Cuando Jess, \n Mostrndome su angustia, \n Sufri por m \n En aquel cruel momento \n Mostrndome su amor, \n Gimiendo all bebi \n La copa del dolor. \n     CORO \n Fue llevado a Caifs y a Pilato, \n Como un vil fue azotado Jess, \n Mientras se oye \n Aquel pueblo que clama: \n Es un blasfemador, \n Se dice Hijo de Dios, \n Matad al impostor!. \n     -2- \n Tortura cruel, \n Las burlas del Pretorio, \n Soldado infiel, \n No escupas ms su rostro, \n No hieras ms \n La faz del que te ama. \n Indigno centurin, \n No tienes compasin \n De quien te da su amor. \n     -3- \n Nubes y sol, \n Testigos de su muerte, \n Cuando expir, \n El velo se rompi; \n Y aquella gente \n Que le contemplaba \n Les sorprendi temor, \n Diciendo en alta voz: \n Este era Hijo de Dios. \n ",track:"1aovfg6hBp8oddPWjpNjYw8WhCCZ0h8H7",demo:"1Zmu3w1Gr6zh9nTyuTS6ws1Vno6pdgfSY"},
{id:116,title:"  116.- GRACIAS SEAN DADAS  ",anthem:"        116.- GRACIAS SEAN DADAS \n \n     Gracias sean dadas al buen Salvador, \n Libre es su amor, libre es su amor; \n Ha preparado para el pecador \n Fuente preciosa de amor. \n Y si t quieres en Cristo confiar \n Libre es su amor, libre es su amor; \n De vida eterna irs a gozar; \n Inagotable es su amor. \n     CORO \n Tierno te llama Jess, quires ir? \n Oh, pecador! Oh, pecador! \n l ha dejado en la cruz al morir, \n Fuente preciosa de amor. \n     -2- \n Por qu viviendo en pecado has de estar? \n Libre es su amor, libre es su amor; \n Llama el Espritu, ven sin tardar, \n Inagotable es su amor. \n Deja tinieblas por luz celestial, \n Libre es su amor, libre es su amor \n Hoy mismo quiere librarte del mal. \n Inagotable es su amor. \n     -3- \n El que creyere perdn obtendr \n Libre es su amor, libre es su amor, \n Ven, al momento te bendecir, \n Inagotable es su amor. \n Jess te espera, yele llamar, \n Libre es su amor, libre es su amor, \n Fe en su nombre te ha de salvar; \n Inagotable es su amor. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:117,title:"  117.- GRATO ES DECIR LA HISTORIA ",anthem:"        117.- GRATO ES DECIR LA HISTORIA \n \n     Grato es decir la historia \n Del celestial favor, \n De Cristo y de su gloria, \n De Cristo y de su amor; \n Me agrada referirla, \n Pues s que es la verdad \n Y nada satisface \n Cual ella, mi ansiedad. \n     CORO \n Cun bella es esa historia! \n Mi tema all en la gloria, \n Ser la antigua historia \n De Cristo y de su amor. \n  -2- \n Grato es decir la historia \n Que brilla cual fanal, \n Y en glorias y portentos \n No reconoce igual; \n Me agrada referirla, \n Pues me hace mucho bien, \n Por eso, a ti deseo \n Decrtela tambin. \n     -3- \n Grato es decir la historia \n Que antigua sin vejez, \n Parece, al repetirla, \n Ms dulce cada vez. \n Me agrada referirla, \n Pues hay quien nunca oy \n Que para hacerle salvo \n El buen Jess, muri. \n ",track:"1fiIUA6Dga5KZnNkl29tRVZLd0gnhjvMU",demo:"1hqxTkj3mecPpswsfbzSwwkxyzG2Tf4Tn"},
{id:118,title:"   118.- GOZO UNIVERSAL  ",anthem:"        118.- GOZO UNIVERSAL \n \n     Gozo a la tierra proclamad, \n Que vino ya el Seor; \n // : En vuestras almas preparad : // \n Un sitio al Redentor, \n Un sitio al Redentor; \n Un sitio a vuestro Redentor. \n     -2- \n Gozo a la tierra! El Salvador \n Triunfante ha de reinar; \n // : Resuenen coros de loor : // \n En cielo, tierra y mar, \n En cielo, tierra y mar, \n En cielo, tierra y vasto mar. \n     -3- \n Cese en el mundo la afliccin \n Y ahuyntese el dolor; \n // : Que brote en cada corazn : // \n Paz, gozo y santo amor, \n Paz, gozo y santo amor, \n Paz, pleno gozo y santo amor. \n     -4- \n l rige el mundo con verdad \n Y gozo paternal; \n //:Su amor sublime y gran bondad:// \n Jams tendrn su igual, \n Jams tendrn su igual, \n Jams, jams tendrn su igual. \n ",track:"1G4nptaxG786v92V8L_8Z_O_NnXTouDwr",demo:"1_ezj-um_ZXi29ui87Cy8HnsJExDj8ZuU"},
{id:119,title:"   119.- GUARDA EL CONTACTO  ",anthem:"        119.- GUARDA EL CONTACTO \n \n     Quires sobre el mundo ser un vencedor? \n Quires t cantar aun cuando hay dolor? \n Quires ser alegre cual fiel luchador? \n Guarda el contacto con el Salvador. \n     CORO \n Guarda el contacto con el Salvador, \n Luego las tinieblas no te cubrirn, \n Por sendero alegre t caminars \n Si el contacto guardas con el Salvador. \n     -2- \n En el mundo hay muchos tristes en pecar \n Dales el mensaje, no hay que callar, \n Al Seor ignoran que puede salvar \n Guarda el contacto con el Salvador. \n     -3- \n Quieres t con Dios la comunin tener,? \n Y su gloria siempre en ti permanecer? \n Que el mundo a Cristo pueda en ti ver \n Guarda el contacto con el Salvador. \n ",track:"1Le6kXOhXYKzbXY3IFgTkBHhrT5ifLKAQ",demo:"1K6a9G6oqbXiSO0hdFPl8qndJetDtYl6H"},
{id:120,title:"   120.- HABLAME MAS DE CRISTO ",anthem:"        120.- HABLADME MS DE CRISTO \n     Quiero que hablis de aquel gran amor \n Que en el Calvario Dios nos mostr; \n Quiero que hablis del buen Salvador, \n Habladme ms de Cristo! \n     CORO \n   Quiero escuchar la historia fiel \n De mi Jess, mi Salvador; \n Quiero vivir tan solo por l, \n Habladme ms de Cristo! \n     -2- \n Cuando me asalte la tentacin \n Y que sus redes tienda a mi pie. \n Quiero tener en l proteccin, \n Habladme ms de Cristo! \n     -3- \n Cuando en la lucha falte la fe, \n Y el alma sienta desfallecer, \n Quiero saber que ayuda tendr, \n Habladme ms de Cristo! \n ",track:"1RrjQqo_AaKoldQQB7IThoHkVgMkIhaAs",demo:"1voXoyaxYSURHBroeEgel0CGOeB6bFxr0"},
{id:121,title:"  121.- HABLAME SEOR  ",anthem:"        121.- HBLAME, SEOR \n \n     Habla, Seor, a mi alma! \n Hable tu dulce voz, \n Susurra en tiernas notas: \n T no ests solo, no; \n Mi corazn prepara \n Presto a escuchar tu ley, \n Loores mi alma llenen \n De contricin y fe. \n    CORO \n Hblame en dulces notas \n Hblame con amor: \n Ya la victoria es tuya, \n No tengas ms temor. \n Hblame cada da, \n Hable tu tierna voz. \n Susurra a mis odos: \n T no ests solo, no. \n     -2- \n Habla a tus hijos siempre \n Dales tu santidad, \n Llnales de tu gozo, \n Ensales a orar. \n A Ti consagren todo, \n Vivan tan slo en Ti; \n Traigan tu reino pronto, \n Vean tu rostro aqu. \n     -3- \n Habla cual en lo antiguo \n Diste tu santa ley. \n Tus testimonios siempre \n Quiero guardar por fe, \n Quiero magnificarte, \n Quiero a tu gloria dar \n El grato testimonio \n De obedecer y amar. \n ",track:"1RxQmHgQj4W8m8Z_MBirb3aP2nm_wFq_p",demo:"1Ao6-ax6wOfY83_6aRQJMVm3t1C_Oz_oK"},
{id:122,title:"  122.- HACIA LA PLAYA CELESTIAL  ",anthem:"        122.- HACIA LA PLAYA CELESTIAL \n \n     Navega sobre el ancho mar \n Al puerto de la eternidad, \n Te ordena tu gran Capitn: \n Navega ms y ms! \n     CORO \n Navega ms y ms en alta mar! \n La tempestad pasando va; \n Navega an! Dios vive \n Y manda: Avanza ms y ms! \n     -2- \n Si el sol no puedes contemplar \n Y el cielo ennegrecido est, \n Resuena un eco sobre el mar: \n Navega ms y ms! \n     -3- \n Si el vendaval en su furor \n Tu barca azota sin cesar, \n Tu rumbo sigue, y con valor \n Navega ms y ms! \n     -4- \n No temas si tan slo vas, \n Ya guiado por tu Capitn, \n Al urea playa llegars, \n Navega ms y ms! \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:123,title:"  123.- HAS TRAIDO TU TODO AL SEOR?",anthem:"        123.- HAS TRAIDO TU TODO AL SEOR? \n \n     Es tu anhelo tenerDulce paz y ms fe? \n Has orado al Seor sin cesar? \n Pero no has obtenido \n Respuesta a tu prez, \n Hasta dar todo sobre el altar. \n     CORO \n Has trado tu todo al altar del Seor? \n El Espritu ejerce dominio en ti? \n Bendecido sers, paz y gozo tendrs \n Si tu ser pones sobre el altar. \n     -2- \n Andars con Jess, \n Disfrutando su paz \n Satisfecho y feliz t sers; \n Pero haz s u voluntad \n Y del mal triunfars, \n Todo debes traer al altar. \n     -3- \n No podremos saber \n Dnde oculta el Seor \n Todo aquello por lo cual se or, \n Hasta que nuestro cuerpo \n Y alma estn \n Consagrados del todo en su altar. \n     -4- \n Quin podr revelar \n Cunto Dios quiso amar? \n Quin podr nuestra dicha apreciar, \n De la dulce amistad \n Que tendremos con l, \n Al poner nuestro todo en su altar? \n ",track:"1NMukNHIRiuzR91lOHETvUKXZnMFBGh-W",demo:"16suKP2dLUbC8EcHV_-xnIdM9Qw9rEqT_"},
{id:124,title:"  124.- HAY AQUI QUIEN NOS AYUDE?",anthem:"        124.- HAY AQU QUIN NOS AYUDE? \n \n     Hay aqu quin nos ayude, \n Quin comprenda nuestro ser, \n Cuando el alma est transida del dolor? \n Hay quin sienta simpata \n Nuestra condicin al ver, \n Y nos d lo que deseamos con amor? \n     CORO \n Uno hay, uno hay, \n En Cristo el bendito, uno hay; \n Cuando viene afliccin \n A nuestro corazn, \n Un amigo hay en Cristo, uno hay. \n     -2- \n Hay aqu quin nos ayude \n Nuestra carga a llevar, \n Aunque grave y difcil pueda ser? \n Hay quin quiera con ternura \n Al cado levantar, \n Y en sus brazos amorosos recoger? \n     -3- \n Hay aqu quin nos ayude, \n Quin nos d tranquilidad, \n Cuando estamos bajo el peso del dolor? \n Quin al pecador ofrezca \n El perdn de su maldad, \n Y por l se sacrifique por amor? \n     -4- \n Hay aqu quin nos ayude \n Y nos libre de temor, \n Del Jordn las olas fras al pasar? \n Quin alumbre nuestra senda \n De su luz con el fulgor, \n Y nos d sus bendiciones sin cesar? \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:125,title:"  125.- HAY PODER EN JESUS  ",anthem:"        125.- HAY PODER EN JESS \n \n     Quires ser salvo de toda maldad? \n Tan slo hay poder en mi Jess. \n Quires vivir y gozar santidad? \n Tan slo hay poder en Jess. \n     CORO \n Hay poder, poder, sin igual poder, \n En Jess quien muri; \n Hay poder, poder, sin igual poder, \n En la sangre que l verti. \n    -2- \n Quires ser libre de orgullo y pasin? \n Tan slo hay poder en mi Jess; \n Quires vencer toda cruel tentacin? \n Tan slo hay poder en Jess. \n      -3- \n Quires servir a tu Rey y Seor? \n Tan slo hay poder en mi Jess; \n Ven, y ser salvo podrs en su amor. \n Tan slo hay poder en Jess. \n ",track:"1SdxiGOEbcnG3U8BRGwD-JBIyAHpXn-qu",demo:"1md81UW1gQZiIM5W0o_W_z4ck0Er7G41E"},
{id:126,title:"  126.- HAY QUIEN VELE MIS PISADAS",anthem:"                 126.- HAY QUIEN VELE MIS PISADAS \n \n     Hay quien vele mis pisadas \n En la sombra y en la luz, \n Por las sendas escarpadas \n Velar por m, Jess. \n Por los valles, por los montes \n Do me lleva su bondad, \n Miro ya los horizontes \n De una nueva claridad. \n     CORO \n Hay quien vele mis pisadas \n Y mi senda trazar \n A las clicas moradas \n Es Jess quien me guiar. \n      -2- \n Hay contacto que me explica \n La presencia del Seor, \n En las penas santifica \n Y me libra del temor. \n Cristo, con su mano herida, \n Proteccin me puede dar, \n Cuando cruzo de la vida, \n El inquieto y fiero mar. \n     -3- \n Hay un corazn amante \n De infinita compasin, \n Que confiere paz constante \n Al contrito corazn. \n En su amor hay eficacia \n l es un amigo fiel \n Hay en l verdad y gracia, \n Suyo soy, y mo es l. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:127,title:"  127.- HAY UN PRECIOSO MANANTIAL",anthem:"        127.- HAY UN PRECIOSO MANANTIAL \n \n     Hay un precioso manantial \n De sangre de Emanuel, \n Que purifica a cada cual \n Que se sumerge en l. \n      CORO \n Lvame, Seor Jess, \n En la sangre de tu cruz. \n Lvame, Seor Jess, \n Y ms blanco que la nieve yo ser. \n     -2- \n El malhechor se convirti \n Pendiente de una cruz, \n l vio la fuente y se lav \n Creyendo en Jess. \n     -3- \n Y yo tambin mi pobre ser \n All logr lavar, \n La gloria de su gran poder \n Me gozo en ensalzar. \n     -4- \n Eterna fuente carmes \n Raudal de puro amor, \n Se lavar por siempre en ti \n El pueblo del Seor. \n ",track:"1QiwRDKN1fUMicVyShBYait9wpH7kpRLU",demo:"1YoXlOwP_RYhyJ1uQ1fKUXt0NVFUzMDQH"},
{id:128,title:"   128.- HAZ DE MI VIDA UN TEMPLO",anthem:"        128.- HAZ DE MI VIDA UN TEMPLO \n \n     Entr a tu hogar, Seor, \n Todo en silencio \n Y contempl tu majestad; \n All tu voz me susurr \n Dndome gozo espiritual, \n Mi corazn se derram \n Como una ofrenda, ante tu altar. \n     -2- \n Ante tu altar, Seor, \n Y de rodillas, \n Cuntas grandezas apreci; \n Casa de Dios o de oracin, \n Lugar de paz y santidad, \n Solemne ambiente espiritual, \n En donde el alma anhela estar. \n     -3- \n Cun pobre soy, Seor! \n A Ti yo vengo, \n Postrado estoy en oracin, \n Quiero por siempre estar aqu \n Junto a la corte celestial. \n Cmo podr, mi buen Jess, \n Lograr mi santa aspiracin? \n Tu bendicin! \n Haz de mi vida un templo. \n ",track:"11giLegGLGrA2hWMrKzwRKOShLxCNbBC5",demo:"1pi1Wve7qVLvJkz0tTNpaGq5-UMZM91t2"},
{id:129,title:"  129.- HAZ LO QUE QUIERAS  ",anthem:"       129.- HAZ LO QUE QUIERAS \n \n     Haz lo que quieras de m, Seor; \n T el Alfarero, yo el barro soy, \n Dcil y humilde anhelo ser, \n Cmplase siempre en m tu querer. \n     -2- \n Haz lo que quieras de m, Seor; \n Mrame y prueba mi corazn, \n Lvame y quita toda maldad \n Para vivir yo en santidad. \n     -3- \n Haz lo que quieras de m, Seor; \n Cura mis llagas y mi dolor, \n Tuyo es, Oh Cristo! todo poder \n Sname hoy, si es tu placer. \n     -4- \n Haz lo que quieras de m, Seor; \n Dueo absoluto de mi amor;\n  Del Paracleto dame poder \n Y el mundo a Cristo pueda en m, ver. \n ",track:"1B7xVMjnFqpvH8mqyn-N-QtDbLaP8PuJ8",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:130,title:"  130.- HE DE VER A JESUS  ",anthem:"        130.- HE DE VER A JESS \n \n     Cuando aqu de \n Mi vida mis afanes cesen ya \n Y se anuncie \n Bella aurora celestial, \n En las playas del cielo \n Mi llegada esperar \n Mi Seor, \n Con bienvenida paternal. \n     CORO \n  Puedo entonces conocerle \n Y seguro en su seno estar; \n Cara a cara espero verle \n Y con l, redimido, vivir. \n        -2- \n Oh! qu gozo tendr yo, \n Cuando pueda ver su faz \n Y en eterna vida \n Est con mi Seor; \n De su lado ya nunca \n Me podrn quitar jams; \n Los halagos \n De mi fiero tentador. \n     -3- \n He de ver a los seres \n Que en la tierra yo perd, \n Cuando en brazos \n De la muerte los dej; \n Y aunque de ellos entonces, \n Con dolor me desped, \n Junto al trono de Jess \n Los hallar. \n     -4- \n Al entrar por las puertas \n De la clica ciudad, \n Me unir con los que ya \n Con triunfo irn, \n Y el himno que alabe \n De mi Dios la majestad, \n Los acentos por los siglos, \n Sonarn. \n ",track:"13Up0ssmgHRqbpir7PZPpWaTtv8_ph814",demo:"1hn5ChE9gPWf65zZSYkT6GWW9lYqrSFGE"},
{id:131,title:"  131.- HOY ES DIA DE REPOSO  ",anthem:"        131.- HOY ES DA DE REPOSO \n \n     Hoy es da de reposo, \n El gran da de solaz; \n Es el da venturoso \n Que nos trae dulce paz. \n Es el da sealado \n Con el sello del amor; \n Nuestro Dios lo ha designado, \n Es el da del Seor. \n     -2- \n Celebremos a porfa \n Al Autor de aquel buen don \n Que nos da el festivo da \n Y se goza en el perdn. \n Aceptemos hoy con gusto \n El descanso semanal, \n Esperando el da augusto \n Del reposo celestial. \n     -3- \n Trabajad, es la sentencia \n De la antigua creacin, \n Y morir, la consecuencia \n De la prevaricacin. \n Mas, reposo y vida estable \n Dios nos da quitando el mal; \n Y su amor inescrutable \n De la gracia es el raudal. \n     -4- \n Los que a Ti nos acercamos \n Por Jess, Dios de verdad, \n Hoy alegres proclamamos \n Tu justicia y tu bondad. \n En los fastos de la historia \n Siempre se celebrar; \n Y en los cielos su memoria \n Por los siglos durar. \n ",track:"1Ljt4M8YDsN9VMEQDBNpHFEzgU9t9tWAF",demo:"1oUi1w26FFXGY8bmfFzxuTzir6_sGFudl"},
{id:132,title:"  132.- HOY CON TROMPETA  ",anthem:"        132.- HOY CON TROMPETA \n \n     Hoy con trompeta la alarma dad, \n Huestes de Sin, al mundo predicad \n Que vivimos en das de adversidad, \n Y Cristo vendr otra vez. \n     CORO \n Jess, otra vez al mundo volver, \n En breve vendr, \n Y con l, galardn nos traer. \n     -2- \n El anticristo aparecer \n Todo el mundo l engaar, \n El que siga sus pasos se condenar, \n Mas, Cristo, le ha de vencer. \n     -3- \n Levntate, Iglesia del Seor, \n Al mundo predicad con gran valor \n Esta gran verdad que viene el Salvador, \n En breve, l regresar. \n ",track:"1v684VUJ8NiHJNP_ZZhYE1_SgaopK9N9b",demo:"1eehJKFPv301LSw4zgV9arfM5cACav6Sm"},
{id:133,title:"  133.- HUBO QUIEN POR MIS CULPAS",anthem:"        133.- HUBO QUIEN POR MIS CULPAS \n \n     Hubo quien por mis culpas \n Muriera en la cruz, \n Aun indigno y vil como soy, \n Soy feliz, pues su sangre \n Verti mi Jess, \n Y con ella, mis culpas borr. \n     CORO \n Mis pecados llev en la cruz do muri \n El sublime, el tierno Jess; \n Los desprecios sufri y mi alma salv. \n l cambi mis tinieblas en luz. \n     -2- \n l es tierno y amante \n Cual nadie lo fue, \n Pues convierte al infiel corazn \n Y por esa paciencia \n Y ternura yo s, \n Que soy libre de condenacin. \n     -3- \n Es mi anhelo constante \n A Cristo seguir, \n Mi camino su ejemplo marc \n Y por darme la vida, \n El quiso morir \n En su cruz, mi pecado clav. \n ",track:"1QZBS3-C_5Hrg-rNvpKzljypGdVjXovMq",demo:"14KauQ-CbC_2VlkeETr0l8FuTLzB7mO4W"},
{id:134,title:"  134.- HUYE CUAL AVE A TU MONTE",anthem:"        134.- HUYE CUAL AVE A TU MONTE \n \n     Huye cual ave a tu monte \n Alma azotada del mal, \n All Jess, la gran fuente, \n Lava tu lepra mortal. \n Huye del mal vergonzoso, \n Clama, y a tu ser medroso \n Cristo dar su reposo, \n // : Oh! alma abrumada del mal : // \n     -2- \n Quiere Jess hoy salvarte, \n Tus lgrimas enjugar; \n Promete nunca dejarte \n Defensa eterna ser. \n Ven, pues va el da volando, \n Ya no andes ms suspirando, \n No te detengas llorando \n // : Tus males, Jess, quitar : // \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:135,title:"  135.- IGNORO YO PORQUE SERA  ",anthem:"        135.- IGNORO YO, POR QU SER \n \n     Ignoro yo por qu ser \n Mis planes veo fracasar; \n Mas cuando llegue a ver al Rey \n Entonces, comprender. \n     CORO \n Un da, yo comprender \n El da cuando le ver. \n De lgrimas libre ser, \n Yo s que lo entender. \n     -2- \n En mi carrera hay gran dolor, \n Afn, pesar o cruel temor; \n Mas cuando en gloria yo est \n Entonces, comprender. \n     -3- \n En medio de tribulacin, \n En tentacin o afliccin; \n Descansar en el amor \n De Jess, mi Salvador. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:136,title:"  136.- INVOCACION ALA TRINIDAD  ",anthem:"        136.- INVOCACIN A LA TRINIDAD  \n \n     Oh Padre, eterno Dios! \n Alzamos nuestra voz \n En gratitud \n De cuanto t nos das \n Con sin igual amor, \n Hallando nuestra paz \n En Ti, Seor. \n     -2- \n Bendito Salvador! \n Te damos con amor \n El corazn. \n Aqu nos puedes ver, \n Que humildes a tu altar, \n Venimos a ofrecer, \n Precioso don. \n  -3- \n Espritu de Dios! \n Escucha nuestra voz \n Y tu bondad \n Derrame en nuestro ser \n Divina claridad, \n Para poder vivir \n En santidad. \n ",track:"1H7f0QjL9aSCfzzOPk-ZwYgWxnNgayD32",demo:"1YGMduFygQr5J6wjWweVpvpiDMD_o3SbQ"},
{id:137,title:"  137.- JAMAS SE DICE ADIOS ALLA ",anthem:"        137.- JAMS SE DICE ADIS ALL \n \n     Amigos, con placer nos vemos \n En tiempo tan veloz, \n Mas, siempre llega el tiempo triste \n En que se dice, adis. \n     CORO \n Jams se dice adis all, \n Jams se dice adis, \n En el pas de gozo y paz, \n Jams se dice adis. \n     -2- \n Cun dulce es el consuelo dado \n Por ellos al partir; \n Que al venir Jess en gloria \n Nos hemos de reunir! \n     -3- \n La voz de triste despedida \n Jams all se oir, \n Mas la cancin de paz y gozo \n Por siempre durar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:138,title:"  138.- JESUS DEL HOMBRE HIJO  ",anthem:"        138.- JESS, DEL HOMBRE HIJO \n \n     Jess, del Hombre Hijo \n Del Hombre Redentor, \n Amigo del que sufre, \n Bendito Salvador. \n Permite que exponga \n Mi triste condicin \n Y ver lo que me falta \n Sabiendo lo que soy. \n        -2- \n Yo s que nada impuro \n Tendr tu aprobacin, \n Y s que en tu presencia \n Continuamente estoy. \n Me falta hacia el pecado \n Sentir honda aversin, \n Me falta ser perfecto, \n Me lo dars, Seor. \n     -3- \n Jess, del Hombre Hijo, \n Bendito Salvador, \n Ya ves cunto me falta, \n Ya ves cun pobre soy. \n A tu piedad me entrego, \n De m ten compasin, \n T puedes darme todo, \n Me lo dars, Seor. \n ",track:"168_vGiFt7cdlEt8gTGtX1wcAF5O2HY9x",demo:"19pxulNnCy8ARZPs41yjjLFSqmysAdMjf"},
{id:139,title:"  139.- JESUS DE LOS CIELOS  ",anthem:"        139.- JESS DE LOS CIELOS \n \n     Jess, de los cielos, al mundo baj \n En busca de joyas que amante compr. \n     CORO \n Los nios salvados \n Sern como el sol, \n Brillando en la gloria \n Del Rey Salvador. \n     -2- \n Angustias y muerte, y horrible afliccin \n Costaron las joyas que amante compr. \n     -3- \n Su hermosa diadema de eterno esplendor, \n La adornan las joyas que amante compr. \n     -4- \n Los nios y nias que van al Seor, \n Son todos, las joyas que amante compr. \n      -5- \n Venid, pues, alegres al buen Redentor, \n l quiere las joyas que amante compr. \n ",track:"1xwQU8FEzmQePUnXsXEXwdfVz0tvpR20q",demo:"1aKT4HhX1rciegyI2o3GL11nlHGYmelGC"},
{id:140,title:"  140.- JESUS ES MI AMIGO  ",anthem:"        140.- JESS ES MI AMIGO \n \n     Jess es mi amigo, mi buen amigo fiel. \n A mi alma es hermoso y sin par, \n Mi Salvador y Gua encuentro slo en l, \n La virtud que trae perfecto bienestar. \n Mis penas y mis cuitas, las llevo siempre a l \n Porque l me ha mandado hacerlo as; \n En todo este mundo no hay otro amigo tal, \n Pues Jess es vida y dicha para m. \n     CORO \n El Lirio de los valles es Cristo, mi Seor, \n Me guarda con cario sin igual; \n Es la resplandeciente Estrella de Amor, \n Que me gua a la patria celestial. \n     -2- \n Alivia mis pesares mi buen amigo fiel, \n En la tentacin, su amor es mi solaz, \n Los dolos del alma por l apartar \n Y las faltas que me priven de la paz; \n Aun cuando me dejare amigo terrenal \n Y Satans tratare de triunfar, \n Yo quedar constante al lado de Jess, \n Y con l podr victoria alcanzar. \n     -3- \n Si cumplo sus mandatos viviendo por la fe, \n En mis pruebas todas, l me sostendr, \n Rodeado de sus brazos yo nada temer, \n Con sus huestes el Seor me guardar. \n Entonces a la gloria ir yo a contemplar \n Su rostro y el cielo heredar, \n Y all con los salvados y el coro angelical, \n Cantar sus alabanzas sin cesar. \n ",track:"1tQ27m9Woih2DNdD3O-Vi_mq2a2LZw63T",demo:"15junccYQP2gcrVrzPhHmZ0Fdu1Abu1u2"},
{id:141,title:"  141.- JESUS PRONTO VOLVERA ",anthem:"        141.- JESS PRONTO VOLVER \n \n     Jess, pronto volver \n Al mundo en gran poder, \n Promesas nos dio, l regresar, \n S, viene el Salvador: \n Seales abundan hoy, \n Cumplindose ya estn \n Por su gracia listo estoy, \n Le espero sin ms afn. \n     CORO \n Muy pronto Jess vendr \n l viene, no tardar; \n l ha de venir, l descender \n Del cielo el Rey vendr, \n Despirtate, pecador, \n El tiempo es corto ya, \n Viene pronto el Salvador, \n No sabes cundo ser. \n     -2- \n Cuando Cristo volver \n El pueblo que le am, \n No ms sufrir, al cielo ir,\n  Pues Cristo ya le salv. \n Mas el que rechaza aqu \n A Cristo y su amor, \n Sufrir por siempre all, \n Tinieblas y gran dolor. \n     -3- \n Del Norte y del Sur vendrn, \n De China y del frica, \n De las Islas tambin miles vendrn\n  Rodeando la mesa all, \n Con Cristo se gozarn, \n Las bodas al celebrar \n Y jams se cansarn \n De aquel tan feliz hogar. \n ",track:"1tF6e73_fMSb-49xrWwnJ-MnE0Ouf2utW",demo:"1WGJ_bQC-ee6i0kgKC9ZBPEdDLPxBU4dx"},
{id:142,title:"  142.- JESUS YO TE AMO  ",anthem:"        142.- JESS, YO TE AMO \n \n     Jess, yo te amo y tuyo ser, \n Por Ti los placeres del mundo dej; \n Pues T me redimes, me das salvacin, \n // : Borrando mis culpas me diste perdn : // \n     -2- \n Primero me amaste y diste por m, \n De sangre, precioso raudal carmes; \n Con ella pagaste mi gran transgresin, \n // : Borrando mis culpas me diste perdn : // \n     -3- \n En bellas mansiones, celestes sin par, \n Tus glorias eternas yo he de cantar, \n Tu gracia bendita ser mi cancin \n // : Borrando mis culpas me diste perdn : // \n ",track:"1c2LuzRteT2zdPaQoe8Xfczjel5V4ykUw",demo:"1dt-0MI4-9jXfSVuxz1Z5s272ViMjAd30"},
{id:143,title:"  143.- JUNTO A LA CRUZ  ",anthem:"     143.- JUNTO A LA CRUZ \n \n     Junto a la cruz do Jess muri, \n Junto a la cruz do salud ped, \n Ya mis maldades l perdon, \n A su nombre, gloria! \n     CORO \n A su nombre, gloria! \n A su nombre, gloria! \n Ya mis maldades l perdon, \n A su nombre, gloria! \n     -2- \n Junto a la cruz donde le busqu, \n Cun admirable perdn me dio! \n Ya con Jess, siempre vivir \n A su nombre, gloria! \n     -3- \n Fuente preciosa de salvacin, \n Qu grande gozo yo pude hallar! \n Al encontrar en Jess perdn, \n A su nombre, gloria! \n     -4- \n T, pecador, que perdido ests, \n Hoy esta fuente ven a buscar \n Paz y perdn encontrar podrs, \n A su nombre, gloria! \n ",track:"12cRz8tpSFvsbMP4YSRgLFzUYXfwAX_GH",demo:"1OXUbxBpUGDhnZY480ECTNo_-oqFDnjh3"},
{id:144,title:"  144.- JUVENTUD ",anthem:"        144.- JUVENTUD \n     Jvenes, todos a luchar \n Con el poder de Dios; \n General tenemos \n Es el Redentor; \n A luchar, con valor. \n     CORO \n // : Juventud, \n A triunfar, \n Jvenes, todos a luchar : // \n     -2- \n El mundo clama libertad \n Debes luchar por l, \n Hblales de Cristo, \n El Cordero fiel, \n Trabajad por el Rey. \n     -3- \n Con la bandera del amor \n Y armas de salud, \n Sigue batallando \n Santa juventud \n Con afn y virtud. \n     -4- \n Cuando termine el batallar \n Corona t tendrs, \n Juventud amada, \n T recibirs \n Bendicin celestial. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:145,title:"  145.- LA BANDERA DE LA CRUZ  ",anthem:"        145.- LA BANDERA DE LA CRUZ \n     Cual pendn hermoso despleguemos hoy \n La bandera de la cruz, \n La verdad del Evangelio, el blasn \n Del soldado de Jess. \n     CORO \n Adelante, adelante, \n En pos de nuestro Salvador! \n Con plena fe en nuestro Rey \n Adelante, con valor! \n     -2- \n Prediquemos siempre lo que dice Dios \n De la sangre de Jess; \n Como limpia del pecado al mortal \n Y le compra la salud. \n     -3- \n En el mundo proclamamos con fervor \n Esta historia de la cruz; \n Bendigamos sin cesar al Redentor \n Que nos dio su paz y luz. \n     -4- \n En el cielo nuestro cntico ser: \n Aleluya a Jess! \n Nuestro corazn, all rebosar \n De amor y gratitud. \n ",track:"1VBmwrFGEKQer1KJyXUaUF47khiTCGd_n",demo:"1uXg3l6ngTyRU-J3pd-tELqYaBDNkiHrk"},
{id:146,title:"   146.- LA CRUZ DE JESUS  ",anthem:"     146.- LA CRUZ DE JESS \n \n     En el monte Calvario \n Estaba una cruz, \n Emblema de afrenta y dolor. \n Y yo amo esa cruz \n Do muri mi Jess \n Por salvar al ms vil pecador. \n     CORO \n Oh! yo siempre amar esa cruz, \n En sus triunfos, mi gloria ser; \n Y algn da en vez de una cruz, \n Mi corona, Jess, me dar. \n     -2- \n Y aunque el mundo desprecie \n La cruz de Jess, \n Para m, tiene suma atraccin; \n Pues en ella llev \n El Cordero de Dios \n De mi alma la condenacin. \n     -3- \n En la cruz de Jess, \n Do su sangre verti \n Hermosura contemplo sin par; \n Pues en ella triunfante \n A la muerte venci \n Y mi ser puede santificar. \n     -4- \n Yo ser siempre fiel \n A la cruz de Jess \n Sus desprecios con l llevar; \n Y algn da feliz \n Con los santos en luz \n Para siempre su gloria ver. \n ",track:"1mjQs9HtGPHY5KLzTEEK6M_IDHq81bmeL",demo:"1RNQecE6F1Z99W-vsK9Itv6OPP-dbHNE1"},
{id:147,title:"  147.- LA CRUZ NO SERA MAS PESADA",anthem:"        147.- LA CRUZ NO SER MS PESADA \n \n     La cruz no ser ms pesada \n Que la gloria que l me da; \n Y si la tormenta me espanta \n No podr esconder su faz. \n        CORO \n La gracia de Dios, me bastar, \n Su ayuda, jams me faltar, \n Consolado por su amor \n Que echa fuera mi temor, \n Confiar en mi Seor. \n     -2- \n Mi cliz nunca es tan amargo \n Como el de Getseman; \n En mis das ms apurados \n No se aparta Dios, de m. \n     -3- \n La luz de su rostro me alumbra, \n En el tiempo de afliccin; \n Y mi alma gozosa vislumbra \n El palacio de mi Dios. \n ",track:"130SfKItBa2E65WyoAhmko5jHdfUX_4cV",demo:"1bVQP9azvEIKWovaL34xC4FsK0j9Ur_nK"},
{id:148,title:"  148.- LA FUENTE ESTERNAL HALLE ",anthem:"        148.- LA FUENTE ETERNAL HALL \n \n     Por fe yo puedo contemplar \n La fuente carmes; \n Cristo, el Seor, puede salvar. \n Su vida dio por m. \n     CORO \n La fuente sin igual hall, \n De vida y gozo manantial. \n Oh, gloria a Dios! me limpia a m, \n Me limpia a m, me limpia a m. \n     -2- \n Mi ser entrego al Salvador, \n Y libre de ansiedad \n Yo voy en pos de mi Seor, \n Confiando en su bondad. \n     -3- \n Ya vivo en la gloriosa luz \n De plena salvacin. \n Entronizado est Jess \n En este corazn. \n     -4- \n Sublime es el amor de Dios, \n Profundo como el mar, \n Alegre cantar mi voz \n Sus glorias sin cesar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:149,title:"  149.- LA FUENTE SANADORA  ",anthem:"        149.- LA FUENTE SANADORA \n \n     Ved la fuente que nos sana, \n La que abri el Salvador, \n Cuyas aguas refrescantes \n Son de perennal valor. \n     CORO \n Oh, preciosa fuente sanadora! \n Para todos fluye libre; \n Oh, preciosa fuente sanadora! \n Gloria a Dios, me sana a m! \n     -2- \n En la fuente que nos sana \n He hallado el perdn, \n Y lavado toda mancha \n De mi pobre corazn. \n     -3- \n En la fuente que nos sana \n Cristo ofrece la salud, \n Pues, venid, enfermos todos, \n Probaris su gran virtud. \n     -4- \n Esta fuente que nos sana \n An hoy da es eficaz; \n Ven, sumrgete en ella; \n Cree, y sano quedars. \n ",track:"1GFiviLj-Pa66ZyTnEuzXtQGHnS1oJzmW",demo:"14hUDZIe2hrmca2jL5unlcHC0LQkTZXbt"},
{id:150,title:"  150.- LA GLORIOSA APARICION  ",anthem:"        150.- LA GLORIOSA APARICIN \n \n     Da de victoria y gozo celestial \n Cuando Cristo volver; \n Qu glorioso encuentro con mi Salvador,\n En las nubes se ver! \n     CORO \n En las nubes l vendr \n En aquel da final; \n Cristo, el Salvador, muy pronto volver \n Por aquellos que l, am. \n     -2- \n Da de gran gozo, da sin igual, \n Cuando Cristo volver; \n De la tierra al cielo, l nos llevar \n A su seno paternal. \n     -3- \n Oye la trompeta que anunciando est \n La venida del Seor. \n Ya no ms dolores, ya no ms afn, \n Con Jess, triunf el amor. \n ",track:"1GHqFJ6Hg1p-zjnQFD9AEF-TOFZxtIx7s",demo:"1QZRrGfFdasN285SJaWmm9mlqfeM6j7Ey"},
{id:151,title:"  151.- LA IGLESIA EN EL VALLE  ",anthem:"151.- LA IGLESIA EN EL VALLE \n     Ves la Iglesia que ufana levanta \n De la cruz el glorioso pendn? \n Es la Iglesia do todo creyente \n Alza a Dios su ferviente oracin. \n     CORO \n Oh, ven, ven, ven, ven, \n Ven a la Iglesia conmigo, \n Oh, ven a adorar al Seor; \n No hay sitio ms dulce en la tierra \n Que la casa del Padre de amor. \n     -2- \n En el valle do se alza mi Iglesia, \n Bellas flores se ven en redor, \n Que lozanas exhalan su aroma, \n Pregonando que Dios es amor. \n     -3- \n Todo all en armonioso concierto \n Rinde culto supremo al Creador, \n Por qu t que su gracia recibes, \n No le adoras viviendo en su amor? \n    -4- \n Si es que alientas en tu alma el deseo \n De encontrar el alivio al dolor, \n Lleva tu alma hasta el trono de gracia \n Y a la Iglesia de Cristo el Seor. \n ",track:"1rUqAtqyaH_vhkhn5-WtwvuUcED0-O2q-",demo:"1wZsebeEDdqNI6NrEeec3VegX2c4IKu1t"},
{id:152,title:"  152.- LA LUCHA SIGUE  ",anthem:" 152.- LA LUCHA SIGUE \n Luchando estis, \n An suena la trompeta hoy \n Llamando a los soldados a la lid; \n A Jesucristo, con valor decid: yo voy\n  Y l os dir: Venid, oh s, venid! \n CORO \n La lucha sigue, oh cristianos, \n Y brazo a brazo lucharis; \n En Jesucristo seguid confiando \n Y por la fe en l, venceris, \n La lucha sigue, oh cristianos, \n Sed fieles y en Jess confiad; \n La lucha siempre seguid, hermanos, \n Y la victoria esperad. \n   -2- \n Luchando estis, \n Confiados en Jess marchad, \n Luchando estis en contra de Satn; \n Es Jesucristo nuestra fortaleza y luz \n Y El tambin es nuestro Capitn. \n     -3- \n Luchando estis, \n Confiados en Jess marchad, \n Haciendo huir al enemigo vil, \n Y Jesucristo, nuestro Jefe amante y fiel, \n Sostn ser de todos en la lid. \n ",track:"1Wb3tCMToAP_IHaPEBX1rQOIFFXhGLr_v",demo:"17rHGReP7qZLDntSrQ7NiPsm9l36QCSCI"},
{id:153,title:"  153.- LANZAD LA CUERDA  ",anthem:"        153.- LANZAD LA CUERDA \n \n     Lanzad la cuerda del salvavidas! \n Hay un hermano que hundindose est, \n Lanzad la cuerda y no ms tardar, \n Es un hermano que habis de salvar. \n     CORO \n Lanzad la cuerda del salvavidas! \n Alguien hundindose est, \n Lanzad la cuerda del salvavidas \n Cristo le salvar! \n        -2- \n Lanzad la cuerda del salvavidas! \n Vedle que ya alejndose va! \n Con mano fuerte se puede salvar \n Oh! por qu tardis? la cuerda lanzad! \n     -3- \n Todos los que en peligro estn, \n Lejos de Dios y de la salvacin; \n Olas de ayes y de tentacin, \n Los llevarn pronto al negro huracn. \n     -4- \n Corto es el tiempo en que se han de salvar, \n Pronto se irn a la eternidad; \n Presto, hermano, y sin ms tardar \n Para salvarlos, la cuerda lanzad. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:154,title:"  154.- LA PUERTA ES CRISTO  ",anthem:"        154.- LA PUERTA ES CRISTO \n \n     La puerta es Cristo para entrar \n Al reino celestial; \n Si quieres t participar, \n Aprtate del mal. \n     CORO \n Por cul camino vas? \n Y si resuelto ests, \n Ya sabes que a Jess \n Tu cuenta le dars, \n Por cul camino vas? \n     -2- \n Por el angosto hay afliccin, \n Pruebas y tentacin; \n El que venciere, reinar \n Con Cristo en la mansin. \n     -3- \n Por el ms ancho slo van \n Todos los que hacen mal, \n Tambin su pago lo tendrn \n Al fuego eternal. \n ",track:"1gng6ATo7NzJe3lhYCUdqt12AJA9Upnw7",demo:"1IqlqYYRHhm6NYfmiLOHrZmpW9uPXtdpg"},
{id:155,title:"  155.- LAS AMERICAS DE CRISTO ",anthem:"              155.- LAS AMRICAS DE CRISTO \n \n     S, s, las Amricas de Cristo sern, sern. \n     -1- \n Embajador del Rey, embajador del Rey, \n Que con valor, \n Vas implantando la sagrada fe, \n Embajador, tendrs si permaneces fiel, \n Bella corona de vida eternal, \n Corona de vida eternal. \n     CORO \n Cuba es del Seor, \n Hait, Colombia y El Salvador, \n Mxico y Panam; \n Y en Costa Rica la obra est. \n Jamaica, Honduras, \n Guatemala y Nicaragua, \n Del Seor sern. \n     -2- \n Embajador, irs por tierra y por mar, \n De Dios, cumpliendo siempre \n Con amor, su voluntad. \n Y as, embajador, fiel siervo del Seor, \n Tu nombre escrito en el cielo estar \n Y tu corona guardar. \n     -3- \n Embajador de Dios, saldrs de tu nacin \n Para ofrecer al pecador \n Por Cristo, salvacin. \n Quiz vas a sufrir y hasta por l morir, \n Pero no importa, si vas a vivir \n Con l, por los siglos sin fin. \n ",track:"1Ha9YwpmULPlGJmL1EEuFFkLynbPXK-3B",demo:"1cX0ZnVhPeaa9cSBthhQGmTj2z_ADo1fu"},
{id:156,title:"  156.- LAS BIENAVENTURANZAS  ",anthem:"        156.- LAS BIENAVENTURANZAS \n \n     Bienaventurados son \n Los pobres en espritu, \n Porque de ellos es el reino \n De los cielos. \n Bienaventurados son \n Los que lloran, \n Porque ellos recibirn, \n Dice Cristo, consolacin. \n     -2- \n Bienaventurados son \n Los mansos, fieles en verdad. \n Esa tierra hermosa y santa \n Ser su heredad \n Bienaventurados son \n Los que tienen sed, \n Sern hartos del pan de vida \n Y del agua de su merced. \n     -3- \n Bienaventurados son \n Los limpios, s, de corazn, \n Porque ellos al fin vern \n Al Seor Jehov. \n Bienaventurados son \n Los que buscan paz, \n Porque hijos de Dios llamados sern \n Y tendrn solaz. \n     -4- \n Bienaventurados los \n Que misericordiosos son, \n Pues tambin misericordia \n Podrn alcanzar. \n Bienaventurados son \n Los que sufren hoy; \n Porque ellos con Cristo all reinarn \n En aquel hogar. \n Porque ellos, siempre reinarn \n En aquel hogar. \n \n  Letra y Msica de Selvin Hall ",track:"1AJS2mRiLJMgaYec_lBnIrg_0t-uPbkiu",demo:"175I9QdbJEYBIZdFgvIMnDwsC1Y_bEJNV"},
{id:157,title:"  157.- LAS GAVILLAS ",anthem:"        157.- LAS GAVILLAS \n \n     Los que con lgrimas sembraron \n Con regocijo segarn; \n Donde sus lgrimas regaron \n Hermosos frutos crecern. \n // : Andando irn, llorando irn, \n Pero con gozo, volvern : // \n     -2- \n Trayendo al hombro sus gavillas \n Y en la garganta una cancin, \n No se perdieron las semillas, \n Mas, dieron frutos en bendicin. \n // : Andando irn, llorando irn, \n Pero con gozo volvern : // \n ",track:"1Hf6yO-vkp1-ZzOW3hwBTJJFNeui4j6jz",demo:"1sDVcOJmgccRgLK-p9MwzjqSpBygcDdqI"},
{id:158,title:"  158.- LAS PALABRAS DE JESUS ",anthem:"        158.- LAS PALABRAS DE JESS \n     Anhelo yo or la voz \n De mi buen Salvador; \n Sus dichos, un banquete son, \n Mi verdadera luz. \n Cual el man, a mi alma dan \n Dulzura y sostn, \n Consuelo, paz, recibo aqu \n Del Maestro de Beln. \n        CORO \n Tus palabras, oh Jess! \n Tus palabras vida son! \n Me ensean, alumbran, \n Me traen valor; \n Las palabras de Jess. \n     -2- \n S, las palabras de Jess, \n Infunden fuerza y fe; \n Alejan de mi corazn \n Las dudas, yo lo s. \n La mansedumbre es de l, \n Con l aprender; \n Y en la gloria su amor \n Siempre alabar \n     -3- \n En todo tiempo, mi Jess, \n Palabras me hablar; \n En pruebas, ellas son mi bien; \n Su voz me alentar. \n En ellas mi alma tiene paz \n Quin la podr turbar? \n Jess me ofrece un festn \n Sin ms por desear. \n     -4- \n Lugar no hay, no lo habr \n En este mundo, no, \n En que el alma goce paz, \n Si esto olvid, \n Pues, su Palabra fuente es \n De toda bendicin; \n Y medio solo del Seor \n Para la salvacin. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:159,title:"  159.- LA SEGUNDA BENDICION  ",anthem:"        159.- LA SEGUNDA BENDICIN \n \n     Para los regenerados \n Hay segunda bendicin, \n Pueden ser santificados \n Y tener un corazn \n Ya limpiado enteramente, \n No habr condenacin, \n Esta bendicin se llama, \n Santidad. \n     CORO \n Hoy buscad santidad, \n Dios ofrece este don, perfecto amor; \n Suplicad santidad, \n Sin la cual, jams veris al Salvador. \n     -2- \n Da en sacrificio vivo \n Tu alma y cuerpo al Seor, \n Ponte en el santo altar, \n Y el fuego purificador \n Quemar raz de mal, \n Te alumbrar con su fulgor, \n Y tendrs la bendicin \n De santidad. \n     -3- \n Pues, la voluntad de Dios \n Es vuestra santificacin; \n De justicia, paz y gozo, \n Llenar tu corazn; \n l, os guardar por siempre \n Sin temor o reprensin \n En la hermosura \n De la santidad. \n ",track:"1YFnab6oHe_k-U0WsOzriuYjSMGNoDx9_",demo:"1xRsyUbjxtfYyWdOnzFJuIFNdlFM7WYoP"},
{id:160,title:"   160.- LA SENDA ES ESTRECHA  ",anthem:"        160.- LA SENDA ES ESTRECHA \n \n     Ya no es mi vida cual antes fue, \n Borrado el pecado est, \n Hoy canto feliz: Aleluya! \n Y voy feliz al dulce hogar. \n     CORO \n La senda es muy estrecha, mas la sigo \n La sigo, la sigo; \n La senda es muy estrecha, mas la sigo,\n  Yo la sigo por la huellas de Jess. \n    -2- \n Mi vida, fue cruel desilusin \n Mi amigo constante hall; \n Mas luz y muy grato consuelo, \n Hall en mi buen salvador. \n     -3- \n Ya no ms luto ni cruel pesar, \n Cual nubes huyeron ya; \n Al cielo yo miro gozoso \n Do hay gloria, gloria sin fin. \n ",track:"1Iwyla6WEkq36zqHHA8Jic7TvZ-yPWq4p",demo:"1HN-KPItRY2vZJXtPz5FmOFoM9Jsx8sgN"},
{id:161,title:"  161- LA SIEMBRA  ",anthem:"        161.- LA SIEMBRA \n \n     Sembrar la simiente preciosa \n Del glorioso evangelio de amor; \n Sembrar, sembrar mientras viva, \n Dejar el resultado al Seor. \n     CORO \n Sembrar, sembrar, \n Mientras viva, simiente de amor; \n Segar, segar, \n Al hallarme en la casa de Dios. \n     -2- \n Sembrar en corazones sensibles, \n La doctrina del Dios de perdn; \n Sembrar, sembrar mientras viva, \n Dejar el resultado al Seor. \n     -3- \n Sembrar en corazones de mrmol \n La bendita Palabra de Dios; \n Sembrar, sembrar mientras viva, \n Dejar el resultado al Seor. \n ",track:"1hB4NZbpKlHMQDPDegOED54IZVFhVTNyU",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:162,title:"   162.- LAS CAMPANAS  ",anthem:"           162.- LAS CAMPANAS \n \n     1er. Coro: \n Las campanas, las campanas \n yelas sonar. \n (resonar, resonar) \n yelas sonar. \n     -1- \n O decir en el pueblo santo \n Que Jess resucit; \n Un ngel muy de maana vino \n Y la gran piedra, quit. \n     CORO \n Las campanas, las campanas, yelas sonar, \n Anunciando que la muerte, Cristo venci, \n Suenan, suenan, muy alegres, \n La gran nueva al mundo dan; \n Las campanas, las campanas, yelas sonar. \n \n Repeitr 1er. coro: \n      -2- \n Cuando de maana las Maras, fueron \n Su amado a ungir. \n Un ngel vino con la buenas nuevas: \n No est aqu, resucit. \n     -3- \n La luz an brilla en el alto cielo, \n Y la noche ya pas: \n El Seor que amamos es el Rey de gloria. \n Y por siempre vencer. \n     -4- \n Muchos que lloraban en el pueblo santo \n Cuando el Seor muri, \n Alegres lo vern, pues vuelve invicto \n Sobre el mundo a reinar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:163,title:"   163.- LAS MANOS DE CRISTO  ",anthem:"        163.- LAS MANOS DE CRISTO \n \n     Manos cariosas, manos de Jess, \n Manos que llevaron la pesada cruz, \n Manos que supieron slo hacer el bien, \n Gloria a esas manos, aleluya, Amn. \n     -2- \n Blancas azucenas, lirios del amor,\n  Fueron esas manos de mi Redentor. \n Que multiplicaron los peces y el pan, \n Manos milagrosas, que la vida dan. \n     -3- \n Oh, Jess, tus manos yo las vi en visin, \n Y vert mi llanto con el corazn. \n Vi sus dos heridas y la sangre vi, \n Que T derramaste por salvarme a m. \n ",track:"1J0LPDsm0l2olCFdVeVmAs6zUaPDoGeV5",demo:"1DvIkOodLLfXCAnQpGQACgbCaLflgapHA"},
{id:164,title:"  164.- LAS NAVECILLAS  ",anthem:"        164.- LAS NAVECILLAS \n \n     Las navecillas, que navegando \n En el profundo, agitado mar, \n Van sostenidas por una mano \n Que el mundo entero pudo formar \n //: Ellas confan en el piloto \n Que las conduce hasta llegar :// \n     -2- \n Es Jesucristo el marinero \n Que nuestras almas conduce ya; \n Por el camino que es el sendero \n Donde la muerte no puede andar; \n //: Y ni las fieras, ni los leones \n Con sus cachorros podrn entrar :// \n     -3- \n Slo los santos, que por su nombre \n Han obtenido ya el perdn, \n Van caminando ya muy confiados, \n No existe duda en su corazn; \n //: Porque el que duda es una nave \n Que sin piloto navega ya :// \n     -4- \n Es este mundo un gran ocano \n Que nuestras almas quiere inundar. \n Con sus mentiras, con sus engaos; \n Sus vanas glorias nos quiere dar, \n //: Con sus caricias e invitaciones \n Nuestra barquilla quiere desviar :// \n     -5- \n Jess nos dice: Soy el camino, \n La vida eterna y la verdad, \n El que quisiere ir a la gloria \n Su pasaporte debe tener, \n //: De otra manera, aqu se queda \n Mirando al cielo y no entrar :// \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:165,title:"  165.- LAS PISADAS DEL MAESTRO ",anthem:"        165.- LAS PISADAS DEL MAESTRO \n \n     Quiero seguir las pisadas del Maestro; \n Quiero ir en pos de mi Rey y Seor, \n Y modelando por l mi carcter \n Canto, con gozo, a mi Redentor. \n     CORO \n Qu hermoso es seguir \n Las pisadas del Maestro! \n Siempre en la luz, cerca de Jess; \n Qu hermoso es seguir \n Las pisadas del Maestro! \n En su santa luz. \n     -2- \n Ando ms cerca de l, que me gua, \n Cuando el maligno me quiere tentar, \n Siempre confiando en Cristo, mi fuerte, \n Debo con gozo, su nombre ensalzar. \n     -3- \n Sigo sus pasos de tierno cario, \n Misericordia, amor y lealtad; \n Yendo hacia l, con el don de la gracia, \n Voy al descanso, gloriosa ciudad. \n     -4- \n Quiero seguir las pisadas del Maestro; \n Siempre hacia arriba con l quiero andar, \n Viendo a mi rey en gloriosa hermosura, \n Con l, en gloria podr descansar. \n ",track:"1NXhXFQAMC3RDNRmvBsSuBG_43ciTuQYK",demo:"1XtGe0bbO0ndEsWuvI7Ks2tm3C3vuDpSI"},
{id:166,title:"  166.- LAS PROMESAS DE JESUS ",anthem:"        166.- LAS PROMESAS DE JESS \n \n     Todas las promesas del Seor Jess, \n Son apoyo poderoso de mi fe; \n Mientras luche aqu buscando yo su luz \n Siempre en sus promesas confiar. \n        CORO \n Grandes, fieles, \n Las promesas que el Seor Jess, ha dado, \n Grandes, fieles, \n En ellas para siempre confiar. \n     -2- \n Todas sus promesas para el hombre fiel, \n El Seor en sus bondades cumplir, \n Y confiado s que para siempre en l \n Paz eterna mi alma gozar. \n     -3- \n Todas las promesas del Seor, sern, \n Gozo y fuerza en nuestra vida terrenal; \n Ellas en la dura lid nos sostendrn, \n Y triunfar podremos sobre el mal. \n ",track:"1QLvViHL-BXlSVE9MdXqeK35eEAjFXiM-",demo:"1Jwr3EjexbDicPIvZsX8GE2k_hs-Zmc3u"},
{id:167,title:"  167.- LA VOZ DEL PASTOR  ",anthem:"        167.- LA VOZ DEL PASTOR \n     Oigo la voz del buen Jess \n En angustiosa soledad, \n Llama al perdido que sin luz \n Se halla en densa oscuridad. \n     CORO \n El Pastor, con bondad,\n  Quiere darte libertad, \n Ven a m, con amor, \n Dice Cristo, el Salvador. \n     -2- \n Quin ayudar quiere a Jess,\n A los perdidos rescatar? \n Anuncia por doquier la luz \n Y el Evangelio predica. \n     -3- \n Triste desierto el mundo es \n Y lleno de peligros mil; \n Dice Jess: Ven a la mies, \n Trae mis ovejas al redil. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:168,title:"   168.- LIBERTAD  ",anthem:"        168.- LIBERTAD \n \n     Aleluya, que el Seor, \n Tiene grande salvacin \n Libertad, libertad, \n Nuestro Dios tiene poder \n Para a Satans vencer; \n Libertad, libertad. \n     CORO \n Libertad y redencin, \n Aleluya, Cristo, ya \n Me am y me salv; \n Gloria, gloria, aleluya. \n l es todo, en todo es l \n     -2- \n Yo confo en Jehov, \n l me da la santidad, \n Libertad, libertad, \n Tengo paz y gozo ya, \n Desde que l me salv, \n Libertad, libertad. \n     -3- \n Hablaremos siempre aqu, \n Ensalzando a nuestro rey, \n Libertad, libertad, \n Porque bondadoso y fiel \n l nos llama hijos de l, \n Libertad, libertad. \n     -4- \n Cantaremos siempre all \n Cuando pasemos de aqu, \n Libertad, libertad, \n Alabando al seor, \n Cantaremos con fervor; \n Libertad, libertad. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:169,title:"  169.- LIBRES ESTAMOS  ",anthem:"        169.- LIBRES ESTAMOS \n \n     Libres estamos, Dios, nos absuelve; \n l nos protege, paz nos devuelve; \n Vinos perdidos, nos socorri \n Aunque enemigos, nos am. \n     CORO \n l nos redime! Nada temamos; \n Verdad sublime! No la dudemos; \n Nuestra cadena, Cristo, rompi, \n Libre de penas nos dej. \n  -2- \n Ciegos, cautivos, faltos de calma, \n En cuerpos vivos, muertos en alma; \n Siempre violando contra el Seor \n Todas sus leyes, sin temor. \n     -3- \n Hoy libertados, ya no pequemos, \n Ya rescatados, suyos seremos; \n Sangre preciosa Cristo verti; \n Bellas lecciones nos dej. \n ",track:"1ozaASBu-bZ_-B2LR_aXIgWBsRA67xn3G",demo:"1Li2RlG7XgLW5wVYKO7n09GsGqNgqYZZu"},
{id:170,title:"   170.- LIMPIA MI CULPA  ",anthem:"        170.- LIMPIA MI CULPA \n \n  Mi alma sufra en la maldad \n Lejos de Cristo en senda fatal; \n Por los vallados, lejos de Dios, \n Perdida, errante, sin paz, sin amor. \n     CORO \n Limpia mi culpa, buen Salvador, \n Mira mi llaga, quita el dolor; \n Libra mi alma del tentador, \n S mi refugio mi buen Salvador. \n     -2- \n A los que viven lejos de Dios, \n Sordos y ciegos en perdicin, \n Cristo les dice con tierno amor: \n Dejen al mundo por la Salvacin. \n     -3- \n En las tinieblas resplandeced, \n En la tristeza, gozo anunciad, \n Pronto el auxilio debes prestar \n A los heridos del fiero Satn. \n     -4- \n Cuando en los cielos puedas estar \n Sin ms congojas, sin ms afn; \n Cantando en coro podrs gozar \n Las alabanzas del buen Salvador. \n \n     Armando Rodriguez ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:171,title:"  171.- LOOR A TI  ",anthem:"               171.- LOOR A TI \n \n     Loor a Ti, mi Dios, en esta noche, \n Por todas las bondades de este da; \n Oh! gurdame y que tus potentes alas \n Sean por siempre la defensa ma. \n     -2- \n Por los pecados que hoy he cometido, \n Perdname, Seor, por tu Hijo amado; \n Contigo, con el prjimo y conmigo \n Quede antes de dormir, reconciliado. \n     -3- \n Ensame a vivir, que no me espante \n La tumba, ms que el lecho de reposo; \n Ensame a morir, para que pueda \n El da del juicio, despertar glorioso. \n     -4- \n Que pueda reposar en Ti mi alma, \n Mis prpados los cierre dulce sueo, \n Un sueo que vigor pueda prestarme \n Para servirte al despertar, mi dueo. \n ",track:"1T6d3dZl2XKqlPqs7wvUrKuuGtiJCpGtv",demo:"1XhGk-2HfHhNV99_AKN644kf-Eg1RT86K"},
{id:172,title:"  172.- LOORES DAD A DIOS  ",anthem:"        172.- LOORES DAD A DIOS \n \n   Oh, jams nos cansaremos \n De la gran cancin! \n Loores a Dios, Aleluya! \n Por la fe cantaremos \n Con el corazn, \n Loores a Dios, Aleluya! \n     CORO \n A los hijos del Seor, pertenece el cantar \n Pues vendr el Salvador y nos arrebatar, \n Del palacio celestial, gozaremos ms all, \n Loores a Dios, Aleluya! \n     -2- \n Oh! la indecible gloria \n Del divino amor, \n Loores a Dios, Aleluya! \n Que en sus alas lleva el alma \n Donde el Salvador, \n Loores a Dios, Aleluya! \n     -3- \n Qu vistosos son los ngeles \n Morando en luz! \n Loores a Dios, Aleluya! \n Ms lucientes los creyentes \n Al venir Jess! \n Loores a Dios, Aleluya! \n     -4- \n Coronados en la gloria \n Con el Salvador \n Loores a Dios, Aleluya! \n Cantaremos alabanzas \n Del divino amor! \n Loores a Dios, Aleluya! \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:173,title:"  173.- LOS GEDEONISTAS  ",anthem:"        173.- LOS GEDEONISTAS \n \n     Los gedeonistas, van esparciendo \n La bella historia del Salvador; \n Van por los montes y los collados, \n Buscando almas para el Seor. \n //: A ellos los gua un santo libro \n Que es la Palabra del Salvador :// \n     -2- \n Los misioneros, son unos hombres \n Que estn guiados por el Seor; \n Ellos predican el gran mensaje \n Que es vida eterna y redencin. \n //: Y los que creen y se arrepienten, \n En el momento, tendrn perdn :// \n     -3- \n Amparo y gua es Jesucristo \n Para los hombres que creen en l; \n Y si ellos sufren aqu en la tierra \n All en el cielo se gozarn. \n //: Y una corona de bellas perlas \n All en sus sienes colocarn :// \n     -4- \n Y los que queden aqu en la tierra, \n Sus pobres almas se perdern; \n Mas, los que suben al paraso, \n En angelitos se volvern. \n //: Y pobrecitos de los perdidos \n Que con el diablo se quedarn :// \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:174,title:"  174.- LOS QUE AMAN AL SEOR ",anthem:"  174.- LOS QUE AMAN AL SEOR \n \n     Los que aman al seor, \n Elevan su cancin, \n //: Que en dulces notas de loor :// \n //: Ascienda a su mansin :// \n     CORO \n A Sin caminamos, \n Nuestra mansin, la gloriosa, \n Cantando todos marchamos \n De Dios, a la bella mansin. \n     -2- \n Que callen los que a Dios \n No anhelen conocer, \n //: Mas, canten todos a una voz :// \n //: Los hijos del gran rey :// \n     -3- \n En Sin disfrutaris \n La gracia del Seor, \n //: Desde hoy, ofrece que tendris: // \n // : Del trono en derredor : // \n     -4- \n Cantemos con fervor, \n Dejando de llorar, \n //: Vayamos libres de temor :// \n //: Al ms feliz hogar :// \n ",track:"1tbdSnkgrpYFQAp00WK46Rw_OBGcMgVcF",demo:"1p5jXsWUl38gJgAis3KRbzJVAUtKvgK6l"},
{id:175,title:"  175.- LUGAR PARA CRISTO ",anthem:"        175.- LUGAR PARA CRISTO \n \n     T dejaste tu trono \n Y corona por m, \n Al venir a Beln a nacer: \n Mas, a Ti no fue dado el entrar al mesn \n Y en pesebre te hicieron nacer. \n     CORO \n Ven a mi corazn, Oh Cristo!\n Pues en l hay lugar para Ti; \n Ven a mi corazn, Oh Cristo, ven! \n Pues en l hay lugar para Ti. \n     -2- \n Alabanzas celestes \n Los ngeles dan \n En que rinden al Verbo loor, \n Mas, humilde viniste \n A la tierra, Seor \n A dar vida al ms vil pecador. \n     -3- \n Siempre pueden las zorras \n Sus cuevas tener \n Y las aves sus nidos tambin, \n Mas el Hijo del hombre \n No tuvo un lugar \n En el cual reclinara su sien. \n     -4- \n T viniste, Seor, \n Con tu gran bendicin, \n Para dar libertad y salud; \n Mas con odio y desprecio \n Te hicieron morir, \n Aunque vieron tu amor y virtud. \n     -5- \n Alabanzas sublimes \n Los cielos darn; \n Cuando vengas glorioso de all; \n Y tu voz entre nubes \n Dir: Ven a m \n Que hay lugar junto a m para ti. \n ",track:"1Thmbs786PvEgTymXhw1-y5MONW_uI3an",demo:"1PNvQxvlyyxW9xT9AzvoCUoDZkxB7oLSZ"},
{id:176,title:"   176.- LLENA HOY  ",anthem:"        176.- LLENA HOY \n \n     Santo Espritu, desciende \n A mi pobre corazn; \n Llnalo de tu presencia \n Y haz en l tu habitacin. \n     CORO \n Llena hoy, llena hoy, \n Llena hoy mi corazn! \n Santo Espritu, desciende \n Y haz en m, tu habitacin. \n      -2- \n De tu gracia puedes darme \n Inundando el corazn; \n Ven, que mucho necesito, \n Dame hoy tu bendicin. \n     -3- \n Dbil soy, Oh, s, muy dbil! \n Y a tus pies postrado estoy; \n Esperando que tu gracia \n Con poder me llene hoy. \n     -4- \n Dame paz, consuelo y gozo, \n Cbreme hoy con tu perdn; \n T confortas y redimes \n T das grande salvacin. \n     -5- \n Santo Espritu, T eres \n Ese prometido don; \n Mucho anhelo recibirte, \n Dame hoy tu santa uncin. \n     -6- \n Ven, bautzame ahora, \n Obediente espero aqu; \n Ven a ser mi eterno gua, \n Haz tu voluntad en m. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:177,title:"  177.- LLUVIAS DE GRACIA  ",anthem:"        177.- LLUVIAS DE GRACIA \n \n     Dios nos ha dado promesa: \n Lluvias de gracia enviar, \n Dones que os den fortaleza; \n Gran bendicin os dar. \n     CORO \n Lluvias de gracia, \n Lluvias pedimos, Seor, \n Mndanos lluvias copiosas, \n Lluvias del Consolador. \n     -2- \n Cristo nos dio la promesa \n Del Santo Consolador; \n Dndonos paz y pureza, \n Para Su gloria y honor. \n     -3- \n Muestra, Seor, al creyente \n Todo tu amor y poder; \n T eres de gracia la fuente, \n Llena de paz nuestro ser. \n   -4- \n Obra en tus siervos piadosos \n Celo, virtud y valor, \n Dndoles dones preciosos, \n Dones del consolador. \n ",track:"14kSYrfCWX8O_X9fT2KR69Dv4ghefskm3",demo:"1Nf902EGicvxvUv7wsfIXIEFQHnfjM2PO"},
{id:178,title:"  178.- MANDA FUEGO SEOR  ",anthem:"        178.- MANDA FUEGO, SEOR \n \n     Tuyo quiero ser, Seor, y hacer tu voluntad; \n Del pecado y del mundo, libre ser, \n De rodillas ante Ti, yo te imploro, oh, Seor, \n Manda fuego de los cielos sobre m. \n     CORO \n //: Manda fuego, Seor :// \n El fuego de tu amor, \n Que consume la maldad, \n Manda fuego de los cielos sobre m. \n     -2- \n Suficiente gracia quiero yo, Seor, tener, \n Para el enemigo siempre derrotar, \n Y poder con fe llenar el lugar que T me das. \n Manda fuego de los cielos sobre m. \n     -3- \n Santo Espritu de Dios, llname de tu amor, \n Para que yo pueda, al mundo derrotar, \n Y poder con fe llenar el lugar que T me das. \n Manda fuego de los cielos sobre m. \n     -4- \n En la via quiero estar, da y noche laborar, \n Siempre firme y siempre fiel, quiero yo ser, \n Pero para que, Seor, yo te pueda presentar, \n Manda fuego de los cielos sobre m. \n ",track:"1ymu5EhiRF9oLcKwrACztIpVpns_D1jJE",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:179,title:"   179.- MARCHARE ",anthem:"        179.- MARCHARE \n \n     Marchar en la luz de Dios, \n Marchar, siempre marchar; \n En la huellas del buen Jess, \n Siempre Marchar. \n     CORO \n Vestido blanco yo tendr, \n Corona eterna ceir; \n Feliz all podr gozar \n En el Edn sin par. \n     -2- \n Marchar con mi gua fiel, \n Marchar, siempre marchar; \n Triunfar por la fe en l, \n Siempre marchar. \n     -3- \n Marchar almas a buscar, \n Marchar, siempre marchar; \n Los perdidos a rescatar, \n Siempre, marchar. \n     -4- \n Por Jess, ms que vencedor, \n Marchar, siempre marchar, \n Al hogar de mi Salvador, \n Siempre marchar. \n ",track:"1q77Lus5Sy6C7NErsG4adHmxK7jwosjo7",demo:"1kvnTfweSmq0EOKj8pcaJJClV3MbGg81M"},
{id:180,title:"   180.- MARINO DIRIGETE A LA LUZ  ",anthem:"        180.- MARINO, DIRGETE A LA LUZ \n \n     Navega en el mar de la vida, un barco \n Bamboleado, por cruel y fatal tempestad; \n Muy cerca a estrellarse en contra las rocas. \n Ya perdido el timn, sin refugio encontrar. \n     Do: \n Cuando entonces una luz brilla \n Cual estrella en su fulgor; \n Y sobre el cristal de las aguas \n De fanal su luz cundi. \n     CORO \n Marino, vete al puerto, \n Su luz alumbra el mar; \n Dirgete al asilo, \n A Cristo, el gran Salvador. \n Marino, vete al asilo fiel, \n Que es Cristo, el gran Salvador. \n     -2- \n Mi barco era aquel prximo para hundirse, \n La tormenta arreciaba y la noche sin luz, \n Qu gozo a mi ser buenas nuevas trajeron, \n Al cambiar mi sendero, ya guiado a Jess. \n     Do: \n Esa hermosa luz an brilla \n En el mundo sin cesar; \n Y sobre las altas olas \n Nos guiar en seguridad. \n     -3- \n Marino, escucha la voz, y tu barco \n Hoy dirige a la luz del brillante fanal; \n Que para guiar los perdidos es puesto, \n Conducirlos a puerto, alumbrando la mar. \n     Do: \n Y cruzar la mar podremos, \n Y al cielo al fin llegar, \n Al Seor Jess veremos \n En aquel eterno hogar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:181,title:"  181.- MAS CUAL JESUS  ",anthem:"        181.- MS CUAL JESS \n     Ms cual mi Cristo, quiero siempre ser, \n Ms mansedumbre, ms benignidad; \n Celo en servirle, fidelidad mostrar, \n Ms consagrado a la obra que me da. \n     CORO \n Toma mi ser, tan slo tuyo es \n Toma mi ser, lo doy, Seor, a Ti; \n Mi corazn imploro limpies, s, \n Tenme, soy tuyo por la eternidad. \n     -2- \n Ms cual mi Cristo, es mi oracin, \n Ms fuerza tenga, ms resignacin; \n Ms serio siempre, ms espiritual. \n Al que vagaba, traerle al altar. \n     -3- \n Ms cual mi Cristo, ms profunda fe, \n De su cario grande compasin, \n Cual l, paciente, humilde y veraz; \n Es mi anhelo cual Cristo aqu andar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:182,title:"  182.- MAS SANTIADA DAME  ",anthem:"        182.- MS SANTIDAD, DAME. \n \n     Ms santidad dame, ms odio al mal; \n Ms calma en las penas; ms alto ideal; \n Ms fe en mi Maestro; ms consagracin; \n Ms celo en servirte, ms grata oracin. \n     -2- \n Ms prudente hazme, ms sabio en El; \n Ms firme en su causa, ms fuerte y ms fiel; \n Ms recto en la vida, ms triste al pecar; \n Ms humilde hijo, ms pronto en amar. \n     -3- \n Ms pureza dame, ms fuerza en Jess; \n Ms de su dominio, ms paz en la cruz; \n Ms rica esperanza, ms obras aqu; \n Ms ansias del cielo, ms gozo all. \n ",track:"1wPp9Oz2I8XaT2cnpiCbXgVyFn7WOxVpd",demo:"1V8i89cu6biJdQbM-O8A2vISeR2Oo32B8"},
{id:183,title:"  183.- ME CUIDA DIOS MI PROTECTOR",anthem:"        183.- ME CUIDA DIOS, MI PROTECTOR \n \n      Me cuida Dios, mi Protector, \n La Roca de la eternidad; \n Mi gua fiel, mi Ayudador \n Y abrigo de la tempestad. \n     CORO \n Confa en Jess, que la Roca es l, \n La Roca es l, la Roca es l; \n Confa en Jess, que la Roca es l \n Y abrigo de la tempestad. \n     -2- \n Es sombra fresca en el calor, \n La Roca de la eternidad; \n No temo, pues, que es mi Seor, \n Mi abrigo de la tempestad. \n     -3- \n Cuando l ordena hay calma y luz, \n La Roca de la eternidad; \n No temer, pues mi Jess, \n Me guardar en la tempestad. \n     -4- \n Oh amada Roca! Salvador, \n La Roca de la eternidad; \n Acudo a Ti, mi Protector \n Y abrigo de la tempestad. \n ",track:"1C90m_BHlb1kfAybvm8Ah9A1sOeCJSoK1",demo:"1TBAZYHa-1cG7MyYAZ4eiKchhI91kOk3M"},
{id:184,title:"  184.- MEDITAD ",anthem:"           184.- MEDITAD \n     Meditad en que hay un hogar \n En la margen del ro de luz, \n Donde van para siempre a gozar \n Los creyentes en Cristo Jess. \n     CORO \n Ms all, ms all, \n Meditad en que hay un hogar,\n  Ms all, ms all, ms all, \n En la margen del ro de luz.\n      -2- \n Meditad en que amigos tenis \n De los cuales marchamos en pos; \n Y pensad en que al fin los veris \n En el alto palacio de Dios. \n     -3- \n Reunido a los mos ser, \n Cuando Cristo, en gloria vendr; \n Y en mi hogar celestial entrar, \n Do mi alma, reposo tendr. \n ",track:"1_oiLf2ABm_XuaVX9K_18YJLZdNKd03Aa",demo:"13vZnaWN-MH3DTIHGbD8aqM8BiCOGLoYs"},
{id:185,title:"  185.- MEDITAD EN JESUS ",anthem:"       185.- MEDITAR EN JESS \n \n     Meditar en Jess, ha de ser mi afn, \n Su bondad hacia el pobre pecador, \n l, gan para m, diadema celestial, \n En la cruz, do le puse mi amor. \n     CORO \n Ven! Ven! buen Jess! \n Hblame, dulce bien, en Ti quiero reposar; \n Y tu dulce mirada, gozar. \n     -2- \n Consultar a Jess, ha de ser mi afn, \n Y Jess, me dar su clara luz; \n Sin Jess, y por m, nada quiero practicar, \n Mis acciones inspire Jess. \n     -3- \n Predicar a Jess, ha de ser mi afn \n Y Jess, me dar su clara luz; \n Sin Jess y su amor, es en vano trabajar; \n De Jess en la cruz hablar. \n  -4- \n Imitar a Jess, ha de ser mi afn, \n En Jess, quiero mi dechado ver; \n Sin mirar a Jess, nada bueno puedo hacer,\n  Y mirando a Jess, todo es bien. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:186,title:"  186.- ME FALTAN PALABRAS  ",anthem:"        186.- ME FALTAN PALABRAS \n \n     Me faltan palabras para contar \n De las virtudes del Salvador; \n En grande Gozo cambi mi llanto, \n Llev mis cargas en Glgota. \n     CORO \n Llev mis cargas en Glgota \n Do mis pecados, Jess borr; \n Sec mi llanto, con gozo canto, \n Llev mis cargas en Glgota. \n     -2- \n Verti su sangre el Salvador; \n Sufri la muerte por la impiedad, \n Crucificado por mis pecados \n Llev mis cargas en Glgota. \n     -3- \n En el calvario Jess muri \n Sacrificado a la humanidad; \n Como cordero al matadero \n Llev mis cargas en Glgota. \n ",track:"1gB7FSkNr4j84ZgtY_L2lSqyyJZ6mqqG-",demo:"1XKygR71NpEurNL8CpBJ0WqA_KxZ8yD9C"},
{id:187,title:"   187.- ME HA PERDONADO  ",anthem:"        187.- ME HA PERDONADO \n \n     Cruzando el mundo triste y sediento, \n Ya sin aliento en mi dolor; \n O tu acento que me llamaba \n Y me invitaba con tierno amor. \n     CORO \n Tu sacrificio me ha perdonado \n Y reconozco tu grande amor, \n Vida en tu nombre, yo he recibido, \n Cristo bendito mi Salvador. \n     -2- \n Si ests cansado y en pena amarga, \n Ven, esa carga te quitar; \n Dulce descanso, tranquila calma, \n Para tu alma ofrecer. \n     -3- \n Dios adorado, vivo en pecado, \n Ten, pues, clemente, piedad de m, \n El grave peso de mis maldades, \n Humildemente lo traigo a Ti. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:188,title:"  188.- MI AMOR Y VIDA  ",anthem:"        188.- MI AMOR Y VIDA \n     Mi amor y vida doy a Ti \n Jess; pues en la cruz por m \n Vertiste sangre carmes, \n Mi Dios y Salvador. \n     CORO \n Mi amor y vida doy a Ti, \n Que fuiste a la cruz por m; \n Mi amor y vida doy a Ti, \n Jess, mi Salvador. \n     -2- \n Que t me salvas, esto s, \n He puesto en Ti, mi dbil fe. \n Feliz entonces vivir, \n Contigo, mi Jess. \n     -3- \n T, que moriste en la cruz, \n Concdeme, Seor Jess, \n Que ande siempre en tu luz, \n Mi Dios y Salvador. \n ",track:"1NtuqhtFL3We0mhBPI7mGt7AUVKNUj2ET",demo:"1A3pEimKQQNywO5iuAQJ0Dm_4Nmm9o--l"},
{id:189,title:"  189.- MI DULCE HOGAR  ",anthem:"        189.- MI DULCE HOGAR \n \n     Si en este mundo luchamos \n Por la obra de Jehov; \n Hay un lugar en el cielo que Cristo, nos dar. \n     CORO \n Mi dulce hogar donde reina la luz, \n Libre de pruebas do mora Jess. \n A veces, tristes, deseamos estar \n En el hogar, en las playas del cristalino mar. \n (Mi dulce hogar, precioso hogar, \n All ir a descansar, \n Libre de pruebas y temor, \n Cristo est all, mi Salvador, \n En este mundo hay pesar \n All ir a descansar; \n En el hogar, en las playas del cristalino mar \n     -2- \n S, un descanso espera a los hijos del Seor, \n Donde no habr ms dolores, \n Ni luchas, ni temor. \n     -3- \n Muy pronto all estaremos, \n En presencia de Jess, \n Y entonces gozaremos de su divina luz. \n ",track:"1r-Dpl8HYQa706AEI8X3FwNI7EU7RYA2V",demo:"1PF9-DUJOiGauHqm7OvLdoAomIRd3LuFT"},
{id:190,title:"  190.- MIO ES EL  ",anthem:"        190.- MO ES L \n \n     Hay un Pastor, el cual vela por m \n Y mo es l. \n Nada soy yo, en el trono l es Rey, \n Mas mo es l. \n Tan pecador como soy, mas me am, \n Y mo es l. \n Nunca podr entender el, por qu, \n Mas, mo es l. \n  CORO \n Mo es l. . . mo es l, \n Maravillosa verdad cantar, \n Que mo es l. \n     -2- \n l vino desde el cielo por m \n Y mo es l; \n Para salvarme, muri en la cruz, \n Mas, mo es l. \n Aunque indigno, l mora en m \n Y mo es l; \n Y de su lado yo nunca me ir \n Pues mo es l. \n     -3- \n De all tambin vino el Consolador, \n Y mo es l; \n   l me revela el amor de Jess, \n Y mo es l; \n Me guardar del pecado mortal, \n Y mo es l; \n Me llevar al hogar celestial \n Pues mo es l. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:191,title:"  191.- MUERTO AL MUNDO  ",anthem:"        191.- MUERTO AL MUNDO \n \n     Yo quiero ser fiel \n A mi Rey y Seor, \n En la senda trazada \n Por su inmenso amor, \n Con sangre y espinas, \n Que sobre su frente \n //: Por darnos vida eterna, :// \n Soport valiente. \n     CORO \n A mis peticiones vi contestacin, \n Cuando Cristo, del cielo, \n Me dio su bendicin, \n Hoy hay en mi alma \n Un lindo concierto; \n //: No importa que el mundo, :// \n Me tenga por muerto. \n    -2- \n A ese manso Cordero \n Que inmolado fue, \n Es al que le pido \n Me aumente la fe; \n Pues servirle siempre \n Ha sido mi anhelo \n //: Y, en mis horas tristes, ://\n  El, es mi consuelo. \n     -3- \n Mientras sirva a Cristo, \n Que es mi Salvador, \n Y de su palabra \n Tome su dulzor, \n No importan desprecios; \n De aquel que yo amo, \n //: Imitando al Santo, :// \n Digo que es mi hermano. \n Muerto al mundo, vida en Cristo. \n ",track:"1XfnfBWNli3d-MeH13S2tIUP_tgQxyElT",demo:"1ImfId2MNT5UqM1MHfmFj6zWg8_hlNfGX"},
{id:192,title:"  192.- NADA DE SOMBRAS  ",anthem:"                 192.- NADA DE SOMBRAS \n \n     No habr sombras en el valle \n De la muerte, \n Cuando cese de la vida el batallar; \n Y escuchemos del Seor el llamamiento, \n Ya llevndonos con l, a descansar. \n     CORO \n Sombras, nada de sombras \n Al dejar el mundo de dolor; \n Sombras, nada de sombras, \n Cuando al cielo llegue vencedor. \n     -2- \n Al dejarnos los que amamos, \n No habr sombras \n Si su fe depositaron en Jess, \n Porque irn a vivir por las edades \n Con quien quiso redimirlos en la cruz. \n     -3- \n Cuando venga por los suyos \n No habr sombras, \n Pues su gloria y majestad las destruirn, \n Y las huestes redimidas con su jefe, \n A las clicas mansiones entrarn. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:193,title:"  193.- NADA INMUNDO  ",anthem:"        193.- NADA INMUNDO \n    Es el cielo un lugar \n Limpio, puro y sin maldad; \n El pecado no entra all \n Por sus puertas pasarn, \n Los que dejan de pecar, \n El pecado no entrar. \n     CORO \n Nada inmundo entrar, \n Nada inmundo entrar; \n Los que all entrar podrn, \n Limpios, puros estarn, \n Nada inmundo entrar. \n     -2- \n Necesita el pecador \n El poder del Salvador, \n Para all poder entrar; \n Si tan slo por la fe, \n En Jess el Cristo, cree, \n Nada inmundo entrar. \n     -3- \n En la sangre de Jess, \n Derramada en la cruz, \n Hay limpieza para ti; \n En aquel feliz hogar, \n T, con l, podrs morar, \n Nada inmundo entrar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:194,title:"  194.- NI EN LA TIERRA ",anthem:"           194.- NI EN LA TIERRA \n \n     Ni en la tierra, ni en el cielo, \n Hay un nombre cual Jess; \n Sobre todo, l reina, \n l es slo eterna luz. \n     -2- \n Es Jess, mi gran riqueza, \n Hallo en l, mi solo bien; \n Valen ms que todo el oro \n Los tesoros de su Edn. \n     -3- \n Es Jess, mi gran sustento, \n Mi pan suave y celestial; \n De mis dichas y gozo, \n Es el rico manantial. \n    -4- \n Infinita es su ternura. \n Quin la puede sondear? \n Con los ngeles hoy quiero \n Su grandeza pregonar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:195,title:"   195.- NIVEAS ROPAS  ",anthem:"        195.- NVEAS ROPAS \n \n     Nveas ropas sin igual \n En la patria celestial, \n Nos dar Jehov, nuestro Seor; \n Donde luz del sol no habr, \n Pues sublime brillar \n La sonriente faz del Salvador. \n     CORO \n Nvea ser, nvea ser \n La vestidura celestial, \n Traje de luz, nos da Jess, \n Nveo vestido de luz, \n Sublime don celestial. \n     -2- \n Nveas ropas sin igual, \n De los santos, noble ideal, \n Llevaremos ante nuestro Rey; \n En completa comunin, \n Cantaremos la cancin \n De la redimida y santa grey. \n      -3- \n Nveas ropas sin igual \n Del Cordero celestial, \n La redenta grey ostentar; \n Pues la sangre de Jess \n Derramada en la cruz \n Nuestras manchas todas lavar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:196,title:"   196.- NOCHE DE PAZ  ",anthem:"        196.- NOCHE DE PAZ \n \n     Noche de paz! Noche de amor! \n Todo duerme en derredor, \n Entre los astros que esparcen su luz \n Bella, anunciando al niito Jess. \n Brilla la estrella de paz, \n Brilla la estrella de amor. \n     -2- \n Noche de paz! Noche de amor! \n Oye humilde, el fiel pastor, \n Coros celestes que anuncian salud; \n Gracias y glorias en gran plenitud. \n Por nuestro buen Redentor, \n Por nuestro buen Redentor. \n     -3- \n Noche de paz ! Noche de amor! \n Ved, qu bello resplandor \n Luce en el rostro del nio Jess, \n En el pesebre, del mundo la luz. \n Astro de eterno fulgor, \n Astro de eterno fulgor. \n ",track:"1jONgeSphlhZ8P8tt6i74mAe8oGaxou5q",demo:"1tKY3orIi8tlTk5mVDXE4MEMtDvtye75d"},
{id:197,title:"  197.- NO HAY TRISTEZA EN EL CIELO",anthem:"     197.- NO HAY TRISTEZA EN EL CIELO \n \n     No hay tristeza en el cielo, \n Ni llanto, ni amargo dolor. \n No hay corazn angustiado, \n Do reina el Dios de amor. \n Las nubes de nuestro horizonte, \n Jams aparecen all. \n El sol en su gloria esplendente, \n Derrama su luz celestial. \n     CORO \n Yo voy a la patria del alma, \n Do Cristo prepara mi hogar, \n Do todos los santificados \n Irn para siempre a morar; \n El da feliz ya se acerca, \n En que el sol para m se pondr, \n Oh! qu gozo ser cuando mire al Seor, \n En aquella hermosa ciudad. \n     -2- \n No hay afliccin en el cielo, \n Ni pruebas existen all; \n El alma que en Cristo reposa, \n Segura en su seno estar; \n No hay tentacin en el cielo, \n Ni sombra de muerte atroz, \n El rbol de vida florece \n Do fluye el ro de Dios. \n     -3- \n Cun dulce ser en el cielo! \n Pasadas las penas aqu, \n Volvernos a ver reunidos \n Con nuestros amados all; \n Por todos los siglos eternos, \n Qu dicha inefable ha de ser! \n Estar en presencia de Cristo, \n Gozando de eterno placer. \n ",track:"16wkAn7ML5xvsljkEzU2oBFS8nW7SG4FA",demo:"18HVWCmP37TXPiUpsY6qvPB-G90_JHJVp"},
{id:198,title:"  198.- NOS VEREMOS EN EL RIO?",anthem:"        198.- NOS VEREMOS EN EL RO? \n \n     Nos veremos en el ro \n Cuyas aguas cristalinas \n Nacen puras, cristalinas, \n Bajo el trono del Seor? \n     CORO \n Oh! s, nos congregaremos \n De ese ro a la ribera, \n De la vida verdadera \n Que nace del trono de Dios \n     -2- \n En las mrgenes del ro \n Que frecuentan serafines, \n Y embellecen querubines, \n Da la dicha eterna, Dios. \n     -3- \n El vergel que riega el ro, \n De Jess es la morada; \n El mal nunca tiene entrada\n  All, slo reina Dios. \n ",track:"1LDXV2HrUDyqWJhJuxv25Xb4L1J-lxs8w",demo:"1CaR8Yc0zBxCnNdT49JIsfF-zPnvp1DcD"},
{id:199,title:"  199.- NO TE DE TEMOR  ",anthem:"        199.- NO TE D TEMOR \n \n     No te d temor hablar por Cristo, \n Haz que brille en ti su luz; \n Al que te salv confiesa siempre, \n Todo debes a Jess. \n     CORO \n No te d temor, no te d temor \n Nunca, nunca, nunca; \n Es tu amante salvador, \n Nunca, pues, te d temor. \n     -2- \n No te d temor, hacer por Cristo, \n Cuanto de tu parte est; \n Obra con amor, con fe y constancia; \n Tus trabajos premiar. \n     -3- \n No te d temor, sufrir por Cristo, \n Los reproches o el dolor, \n Sufre con amor tus pruebas todas, \n Cual sufri tu salvador. \n     -4- \n No te d temor vivir por Cristo, \n Esta vida que te da; \n Si tan slo en l, por siempre fiares, \n l, con bien te sacar. \n  -5- \n No te d temor morir por Cristo, \n Va, verdad y vida es l; \n l te llevar con su ternura \n A su clico vergel. \n ",track:"11hhDO80tO0is00FFJuXwnzFR9f0MWaf8",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:200,title:"  200.- NO TENGO CUIDADO  ",anthem:"        200.- NO TENGO CUIDADO \n \n     No tengo cuidado, ni tengo temor, \n De lo que me espera aqu; \n Confo tan slo en mi buen Salvador \n El cual cuida siempre de m. \n     CORO \n Vivo por fe, en mi Salvador, \n No temer, es fiel mi Seor; \n En toda lid nunca me dejar \n Yo vivo por fe, l me guardar. \n     -2- \n Vendrn tempestades, lo s yo muy bien; \n La fuerza del sol faltar; \n Amigos, dejarme lo pueden tambin, \n Jess, siempre me sostendr. \n     -3- \n El Dios que an viste el lirio aqu, \n Sustenta tambin al gorrin; \n Jams puede desampararme a m, \n Su hijo soy por adopcin. \n     -4- \n Un da vendr otra vez el Seor, \n Al cielo l nos llevar, \n All cesarn el afn y el temor, \n Y Dios todo dominar. \n ",track:"1UoNQAPiQqA358AZ8UAABilLGarREYG1i",demo:"14fDuukmmrklr2QeFgMYjdSY8l1EdT7Bf"},
{id:201,title:"   201.- NOVENTA Y NUEVE ",anthem:"        201.- NOVENTA Y NUEVE \n \n     Noventa y nueve ovejas, s, \n En el aprisco estn, \n Mas, una sola sin pastor, \n Por la montaa va. \n La puerta de oro traspas \n //:Y vaga en triste soledad:// \n     -2- \n Seor, No bastan para Ti \n Las que encontraste ya? \n Oh, no! No bastan, el Pastor \n Responde con afn. \n Y al tenebroso bosque voy \n //: Mi pobre oveja a rescatar:// \n     -3- \n No sabe el redimido bien, \n Que amargo y hondo mar \n Atraves su buen pastor, \n Llorando de ansiedad. \n Por su ovejuela a quien hall \n //: A punto casi de expirar :// \n     -4- \n De quin, Seor, la sangre es \n Que sealando va \n Al indeciso viador \n La senda celestial? \n Por una oveja la vert \n //: Y as podrla recobrar:// \n     -5- \n Mis manos hoy el bosque hiri \n Con harta crueldad, \n Mas, yo mi pobre oveja en l \n Busqu sin descansar; \n Oveja que hoy ests aqu \n //: Ven a mis hombros sin tardar:// \n     -6- \n La tierra toda de una voz \n El eco alegre da: \n Mi cara oveja, al fin hall, \n Mi gozo celebrad \n Y en tanto el cielo dice as: \n //: Lo suyo, Dios restaura ya :// \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:202,title:"  202.- OH. ALMA  ",anthem:"        202.- OH ALMA! \n \n     Oh alma, querida! A dnde t vas? \n Por tu negligencia te vas a perder. \n Si no te preocupas por tu salvacin \n No tendrs excusa ni tendrs perdn. \n Es la salvacin un tesoro que hay, \n Que con oro y plata no puedes comprar, \n Que con oro y plata no puedes comprar. \n     -2- \n Reuniendo del mundo los mercados mil \n Cristo no encontr precio justo para ti; \n Pues eres tesoro de precio sin par, \n Eres lo ms grande que tiene un mortal, \n Y siendo tu precio tan alto a pagar, \n Slo Jesucristo te puede salvar, \n Pues slo su sangre te puede comprar. \n     -3- \n Si tres o ms vidas, pudiera tener, \n Feliz yo las diera si era menester; \n Y as salvacin para ti obtener, \n Pues vale la pena por ti padecer. \n No hay ya temor a la muerte o dolor, \n Si a cambio, el cielo promete el Seor. \n Cielo es tu precio. Cielo es tu nombre, \n Cielo eres t. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:203,title:"  203.- OH. AMOR QUE NO ME DEJARAS",anthem:"        203.- OH AMOR QUE NO ME DEJARS! \n \n     Oh amor que no me dejars! \n Descansa mi alma siempre en Ti; \n Es tuya y t la guardars \n Y en el ocano de tu amor \n Ms rica al fin ser. \n     -2- \n Oh luz que en mi sendero vas! \n Mi antorcha dbil rindo a Ti; \n Su luz apaga el corazn, \n Seguro de encontrar en Ti \n Ms bello resplandor. \n     -3- \n Oh gozo que a buscarme a m \n Viniste con mortal dolor; \n Tras la tormenta, el arco vi, \n Y ya el maana yo lo s, \n Sin lgrimas ser! \n     -4- \n Oh cruz que miro sin cesar! \n Mi orgullo, gloria y vanidad; \n Al polvo dejo, por hallar \n La vida que en su sangre dio \n Jess mi salvador. \n ",track:"1uQiM1H6491MIiAy9y1w2xurdq_FEL_3w",demo:"1dzgMytG3OI7ihj5UIe6kisZ7FT-CVDFE"},
{id:204,title:"  204.- OH. BONDAD TAN INFINITA  ",anthem:"        204.- OH, BONDAD TAN INFINITA! \n \n     Oh, bondad tan infinita, \n Hacia el mundo pecador; \n Dios, en Cristo revelando \n Su eternal y santo amor! \n     CORO \n Es Jess para m, \n La esperanza de salud. \n Slo en l, hallar \n La divina plenitud. \n     -2- \n Como el vasto firmamento, \n Como el insondable mar, \n Es la gracia salvadora \n Que Jess, al alma da. \n     -3- \n Aunque fueren tus pecados \n Rojos como el carmes; \n En el ro del calvario \n Hay limpieza para ti. \n ",track:"1g3ZdtQARZNuUS5nJ9rUt9QS-UKn-LsuR",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:205,title:"  205.- OH. CRISTO. TU AYUDA YO QUIERO",anthem:"       205.- OH CRISTO, TU AYUDA YO QUIERO! \n \n     Oh Cristo! tu ayuda yo quiero tener, \n En todas las luchas que agitan mi ser, \n Tan slo T puedes la vida salvar, \n T slo la fuerza le puedes prestar. \n    -2- \n Oh Cristo! la gloria del mundo busqu, \n Y ansioso, mi vida y afn le entregu; \n Y en cambio, mi pecho tan slo encontr, \n Torturas sin cuento que el alma apur. \n     -3- \n Oh Cristo! ya quiero llegar a vivir, \n De aquellos alientos que t haces sentir; \n Al alma que huyendo del mal tentador, \n Se vuelve anhelante, se vuelve a tu amor. \n     -4- \n Oh Cristo! ya quiero tus huellas seguir, \n Y gracia constante de Ti recibir; \n Hallar en mis noches, contigo la luz, \n Y alivio a mis penas al pie de la cruz. \n ",track:"1LXZ2iNYO4vMWiTMKkA9IRx_4TAoPr9sJ",demo:"1qIjex25B1L-mn44SADr1DTA_8CfsDthL"},
{id:206,title:"  206.- OH. CUAN DULCE  ",anthem:"        206.- OH, CUN DULCE! \n     Oh, cun dulce es fiar en Cristo, \n Y entregarse todo a l \n Esperar en sus promesas \n Y en sus sendas serle fiel! \n     CORO \n Jesucristo, Jesucristo, \n Ya tu amor probaste en m, \n Jesucristo, Jesucristo, \n Siempre quiero fiar en Ti. \n     -2- \n Es muy dulce fiar en Cristo \n Y cumplir su voluntad, \n No dudando su Palabra, \n Que es la luz y la verdad. \n     -3- \n Siempre es grato fiar en Cristo \n Cuando busca el corazn, \n Los tesoros celestiales \n De la paz y del perdn. \n     -4- \n Siempre en Ti, confiar yo quiero, \n Mi precioso salvador; \n En la vida y en la muerte, \n Proteccin me d tu amor. \n ",track:"1CuJWaORH26On5kR4fknbj5vtZdlXaTDf",demo:"1dIkTMEBl7FOQh9vUq7WmWrFlYwy-waDd"},
{id:207,title:"  207.- OH. JOVENES VENID  ",anthem:"        207.- OH, JVENES, VENID! \n \n     Oh, jvenes, venid! su brillante pabelln \n Cristo ha desplegado ante la nacin; \n A todos en sus filas, l quiere recibir, \n Y con l a la pelea os har salir. \n     CORO \n Vamos a Jess, alistados sin temor, \n Vamos a la lid inflamados de valor! \n Jvenes, luchemos todos contra el mal; \n En Jess llevamos nuestro general. \n     -2- \n Oh, jvenes, venid! el caudillo salvador \n Quiere recibiros en su derredor; \n Con l a la batalla salid sin vacilar, \n Vamos pronto, compaeros, vamos a luchar. \n     -3- \n Las armas invencibles del Jefe guiador, \n Son el evangelio y su grande amor; \n Con ellas, revestidos y llenos de poder \n Compaeros, acudamos, vamos a vencer. \n     -4- \n Los fieros enemigos, engendros de Satn, \n Se hallan sostenidos por su capitn; \n Oh! Jvenes, vosotros poneos sin temor, \n A la diestra del caudillo, nuestro salvador. \n     -5- \n Quien venga a la pelea, su voz escuchar; \n Cristo, la victoria le conceder; \n Salgamos, compaeros, luchemos bien por l, \n Con Jess, conquistaremos inmortal laurel. \n ",track:"1BKoW9eXAjSm86aEiGV-bEHuTiYriV5ha",demo:"1MarMKvHFLvsFXi-XJrNjPjhtkcaL_VKf"},
{id:208,title:"  208.- OH. MI CORAZON REBOZA  ",anthem:"        208.- OH, MI CORAZN REBOSA! \n \n     Oh, mi corazn reboza de gran gozo! \n Porque Dios, me perdon en su amor; \n Al servirle a l estoy ya tan dichoso, \n Voy al cielo do est mi salvador. \n     CORO \n Voy subiendo al cielo en urea escalera, \n Voy subiendo do corona ya me espera, \n Voy subiendo en la luz, \n Voy subiendo con Jess; \n   Alabanzas cantar a Dios all. \n Voy subiendo al cielo en urea escalera, \n Voy subiendo do corona ya me espera, \n Voy subiendo en la luz, \n Voy subiendo con Jess, \n Voy subiendo al celeste hogar. \n  -2- \n Cada da quiero amarle ms constante, \n Cada da quiero serle yo ms leal, \n Ayudado por su gracia abundante, \n Que procede de mi padre celestial. \n    -3- \n Hallo gran placer llevando pecadores, \n Pobres, ricos de cualquiera condicin; \n A aquel que en la cruz sufri dolores, \n Para dar a todos plena salvacin. \n ",track:"1Om62OCKx_vhZjXMPo2vuLjdOMPczGlGd",demo:"1Cdy1iCMR1jDGvmw96dg196tMnxyzwsgj"},
{id:209,title:"  209.- OH. QUE AMIGO ",anthem:"        209.- OH, QU AMIGO! \n \n    Oh, qu amigo nos es Cristo! \n El llev nuestro dolor \n Y nos manda que llevemos \n Todo a Dios en oracin. \n Vive el hombre desprovisto \n De paz, gozo y santo amor? \n Esto es porque no llevamos \n Todo a Dios en oracin. \n     -2- \n Vives dbil y cargado \n De cuidados y temor? \n A Jess, refugio eterno, \n Dile todo en oracin. \n Te desprecian tus amigos? \n Cuntaselo en oracin, \n En sus brazos de amor tierno, \n Paz tendr tu corazn. \n     -3- \n Jesucristo es nuestro amigo, \n De esto, pruebas l nos dio; \n Al sufrir el cruel castigo \n Que el culpable mereci. \n Y su pueblo redimido \n Hallar seguridad, \n Fiando en este amigo eterno \n Y esperando en su bondad. \n ",track:"1H_EOyIaeqvZ0c-9Gh_iskG2xg6-RJMS1",demo:"1EEq9hW3bwGpvT8t4ZRGOUjcqxgM7n0JG"},
{id:210,title:"  210.- OH. SI QUIERO VERLE  ",anthem:"        210.- OH, S, QUIERO VERLE! \n \n     Voy feliz al dulce hogar, por fe en Jess,\n  Y luchando a traer almas a la luz; \n Dardos encendidos mil, vienen contra m; \n Mas, yo s por la fe, vencer aqu. \n     CORO \n Oh, s, quiero verle! ver al Salvador, \n Quiero ver su rostro lleno de amor; \n En aquel da yo he de cantar: \n Ya pas todo afn, todo mi pesar. \n     -2- \n En las olas del turbin, Cristo guardar \n Mi barquilla, y guiar hasta el puerto all; \n Yo tranquilo puedo estar, mi piloto es l, \n Es mi Rey, tengo fe, se que l es fiel. \n     -3- \n En servir al Salvador por los valles voy, \n Donde muchas sombras hay, ms seguro estoy; \n Muchos triunfos obtendr, nunca faltar \n Mi Jess, es la luz, l me sostendr. \n ",track:"1BztVrum3AGUiOKWiIPWaGzwD5BtkIoMv",demo:"1qMK9WptzR9RisQYWtd_dlYBXSpVp7XA7"},
{id:211,title:"  211.- OH. VEN SIN TARDAR  ",anthem:"        211.- OH, VEN SIN TARDAR! \n \n     Oh, no rechaces la verdad! \n Tus ojos hoy abre a la luz, \n Renuncia a toda la maldad \n Y ven a Jess. \n     CORO \n Oh! ven sin tardar! \n Oh! ven sin tardar! \n Acepta a Jess y salvo sers. \n     -2- \n Tus ojos ya tal vez el sol \n No ms aqu contemplarn; \n Hoy es el da de salvacin \n Oh, ven a Jess! \n     -3- \n Jess te tiene compasin; \n Con qu su amor le pagars? \n Hoy trae a l tu corazn \n Y salvo sers. \n     -4- \n Jess recibe al pecador, \n Que quiere a l su alma unir. \n Ya no desprecies ms su amor, \n Oh, ven a Jess! \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:212,title:"  212.- OYE PECADOR LA VOZ DE CRISTO",anthem:"        212.- OYE, PECADOR LA VOZ DE CRISTO \n \n     Oye, pecador, la voz de Cristo; \n Deja tus pecados, ven en pos de m, \n Borrar tus muchas rebeliones, \n Ven, y hallars descanso en m. \n Hallars perdn y vida eterna, \n Libertad de todo llanto y dolor, \n Quieres descansar de tus pecados? \n Ven a m, oh pobre pecador. \n     -2- \n Mrale en el huerto suplicando \n En su rostro gotas hay de carmes; \n Ora a Cristo en su agona, \n Pidiendo vida para ti. \n Luego en la cruz cruel, colgado, \n Coronado con espinas, qu amor! \n Su perdn nos da la paz perfecta, \n Dale hoy tu vida, pecador. \n     -3- \n Oh! por qu no quieres aceptarlo? \n Aceptar a Cristo, como Salvador? \n Entregar a El tu vida entera? \n Ser guiado por tu Redentor? \n Esperando con amor tan grande, \n Sigue t mis huellas, clama con fervor, \n No hay otro, Yo soy el camino, \n Ven a m, oh pobre pecador. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:213,title:"  213.- PADRE. OH. PADRE  ",anthem:"         213.- PADRE, OH PADRE \n     Coro: \n  Padre, oh Padre, la bella luz \n Del da, se fue. \n //: Las sombras se declinan ya :// \n Solo: \n Mas, mientras prolongan \n Oye nuestra voz. \n Oye Cristo, oye Cristo. \n Coro: \n Y oyendo bendcenos, oh Seor, \n Una bendicin, una bendicin. \n Do: \n Llnanos con tu preciosa verdad, \n Danos tu gracia y poder. \n Coro: \n Benignos seamos, benignos seamos. \n Concdenos esta plegaria, Seor! \n Solo: \n Amparo y gua s t, Oh Dios! \n Coro: \n Amparo y gua s t, Oh Dios! \n Amparo y gua s t, Oh Dios! \n    CORO \n Oye, s oye. Oye, s oye,\n Merced pedimos, Seor. \n Oye, s oye, tu grey te clama Oh Dios! \n Imparte tu gracia y bondad. \n Oye, s oye, tu grey te clama Oh Dios! \n Imparte tu gracia, tu gracia y bondad. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:214,title:"   214.- PADRE. TU PALABRA ES  ",anthem:"        214.- PADRE, TU PALABRA ES \n \n     Padre, tu palabra es \n Mi delicia y mi solaz; \n Gue siempre aqu mis pies, \n Y a mi pecho traiga paz. \n     CORO \n Es tu ley, Seor, \n Faro celestial; \n Que en perenne resplandor \n Norte y gua da al mortal. \n     -2- \n Si obediente o tu voz, \n En tu gracia fuerza hall; \n Y con firme pie y veloz, \n Por tus sendas camin. \n      -3- \n Tu verdad es mi sostn \n Contra duda y tentacin. \n Y destila calma y bien \n Cuando asalta la afliccin. \n        -4- \n Son tus dichos para m \n Prendas fieles de salud; \n Dame, pues que te oiga a Ti \n Con filial solicitud. \n ",track:"1a3Z6tIsRd6gQ3bCQVV6dtrIcrFEUT4Kg",demo:"15vyySEL8209bzHCKmWa7UaZayAirLH5m"},
{id:215,title:"  215.- PAZ. PAZ. CUAN DULCE PAZ",anthem:"        215.- PAZ, PAZ, CUN DULCE PAZ \n \n     En el seno de mi alma, una dulce quietud \n Se difunde, embargando mi ser. \n Una calma infinita, que slo podrn \n Los amados de Dios, comprender. \n     CORO \n Paz, paz, cun dulce paz, \n Es aquella que el Padre me da! \n Yo le pido que inunde por siempre mi ser, \n En sus ondas de amor celestial. \n     -2- \n Qu tesoro yo tengo en la paz que me dio, \n Y en el fondo del alma ha de estar; \n Tan segura, que nadie quitarla podr \n Mientras miro los aos pasar. \n     -3- \n Esta paz inefable, consuelo me da, \n Descansando tan slo en Jess. \n Y ningunos peligros mi vida tendr, \n Si me siento inundado en su luz. \n     -4- \n Sin cesar, yo medito en aquella ciudad, \n Do al autor de la paz he de ver \n Y en que el himno ms dulce \n Que all he de cantar, \n Al estar con Jess, ha de ser. \n     -5- \n Alma triste, que en rudo conflicto te ves. \n Sola y dbil, tu senda al seguir, \n Haz de Cristo el amigo que fiel siempre es, \n Y su paz, t podrs recibir. \n ",track:"1Yn_TtaDPjkrKgHg-D2taB5sdiN8zUXH6",demo:"1MmguNQbAzsSyP6DsC5IAFQ_kNFbdRbLW"},
{id:216,title:"  216.- PECADOR. VEN AL DULCE JESUS",anthem:"             216.- PECADOR, VEN AL DULCE JESS \n \n     Pecador, ven al dulce Jess,\n  Y feliz para siempre sers; \n Si en verdad le quisieres tener, \n Al divino Seor, hallars. \n     CORO \n Ven a l, ven a l, \n Que te espera tu buen Salvador, \n Ven a l, ven a l, \n Que te espera tu buen Salvador. \n     -2- \n Si cual hijo que necio pec, \n Vas buscando a sus pies compasin, \n Tierno padre, en Jess hallars, \n Y tendrs en sus brazos perdn. \n     -3- \n Si enfermo te sientes morir, \n El ser tu doctor celestial; \n Y hallars en su sangre tambin, \n Medicina que cure tu mal. \n ",track:"1c8lmh-QOlfZOFMHaH8a-E6nY-u-X-J8z",demo:"16hku9kb238kxKrI8lk_O-o6nxO0XYN-K"},
{id:217,title:"   217.- PERDERME. OH! QUE SERA?",anthem:"        217.- PERDERME OH! QU SER? \n \n     El tiempo vuela y la eternidad, \n Muy pronto me sorprender; \n No puedo olvidar ni apartar el temor, \n Mirando el futuro sin fin. \n     CORO \n Perderme, Oh! Qu ser? \n La ira de Dios a gustar \n Con ngeles malos morada hacer, \n En el lago de fuego eternal. \n     -2- \n Delante hay tinieblas, pero en la oscuridad, \n Diviso espectro subir, \n Borrando el arco de fe para m, \n Que brilla en la eternidad. \n     -3- \n Cuando el tiempo termine y la tierra tambin,\n  Abierto el cielo ser, \n El juicio del gran da comenzar,\n  De mi alma, oh! qu ser? \n     -4- \n La gracia de Dios, pronto terminar,\n  En el cielo lugar no habr, \n Por ser negligente perdido ser, \n De mi alma oh! qu ser? \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:218,title:"  218.- PODER PENTECOSTAL  ",anthem:"        218.- PODER PENTECOSTAL \n \n     Unnimes junto a la cruz \n Pedimos con fervor; \n Segn tu dicho, oh Jess, \n Manda el Consolador. \n     CORO \n S, manda otra lluvia, \n Oh, buen Salvador! \n Y con tu gran fuego, \n Avvanos, Seor, \n Oh! danos la lluvia \n Del Consolador, \n Aviva tu Iglesia \n Con poder celestial. \n     -2- \n Cual vivo fuego vendaval, \n Oh! hazlo descender, \n Y pueda hoy en cada cual \n Su templo establecer. \n     -3- \n Mediante fe y oracin, \n El cielo, T, abrirs, \n El Santo fuego hars bajar \n Y nos avivars. \n     -4- \n Destruye el egosmo, s, \n Y quema todo mal; \n Ven, vivifcame aqu, \n Con fuego celestial. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:219,title:"  219.- POR CRISTO ES NUESTRO ESFUERZO",anthem:"           219.- POR CRISTO ES NUESTRO ESFUERZO \n \n     Por Cristo es nuestro esfuerzo, \n l es nuestro Seor, \n Su rostro nos anima, \n Gozamos de su amor. \n Estemos a su lado, \n Sumisos a su voz. \n Cifrando nuestra gloria \n En ir por l a Dios \n     -2- \n Fundados en su gracia \n Podemos trabajar; \n Su nombre confesando \n Luchando sin cesar; \n Jams nos abandona \n l es nuestro sostn. \n Nos lleva a su morada \n Nos guarda el sumo bien. \n     -3- \n Gozosos y entusiastas \n Formemos su legin; \n Siguiendo su bandera \n Con todo el corazn. \n Por l es nuestro esfuerzo, \n Le damos nuestro ser; \n Y vamos adelante \n Seguros de vencer. \n ",track:"15CIaBE7hjqPR4oLD15FD9hSlBntDAyRX",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:220,title:"  220.- POR EL MENSAJERO  ",anthem:"        220.- POR EL MENSAJERO \n \n     Por el Mensajero es nuestro afn, \n Silencioso y fiel predicador, \n Con l habremos de luchar hasta vencer, \n Las huestes que se oponen contra el Seor. \n Maana, tarde y noche con fervor, \n Vamos propagando por amor, \n El Mensajero firme y alto levantad, \n Y a la victoria, juventud, marchad. \n     -2- \n Por el Mensajero es nuestro afn, \n Quin se esfuerza ms por su extensin? \n Es nuestra juventud que lucha sin cesar. \n Por sustituto, a todo el mundo predicad. \n Maana, tarde y noche, con fervor \n Fieles lucharemos hasta el fin, \n El Mensajero firme y alto levantad. \n Y a la victoria, juventud, marchad. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:221,title:"  221.- POR EL MENSAJERO LUCHEN",anthem:"        221.- POR EL MENSAJERO LUCHEN \n \n     Por el Mensajero luchen soldados de la cruz; \n Alzad su gran esfuerzo en nombre de Jess. \n De victoria en victoria, sus triunfos anunciad, \n Con Dios a vuestro lado sus nuevas proclamad. \n     CORO \n Estaos firmes, soldados de la Cruz, \n Al mensajero levantad en nombre, \n En nombre de Jess. \n     -2- \n Por el Mensajero luchen, os llama a la lid, \n Con l pues a la lucha, soldados, todos id, \n Probad que sois valientes, \n Luchando contra el mal, \n Y al Mensajero ayuden, es un amigo fiel. \n     -3- \n Por el Mensajero luchen, la lucha es tenaz,\n  Hoy da de batalla, maana es de cancin; \n Todo el que venciere, corona obtendr, \n Y con el Rey de gloria, por siempre reinar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:222,title:"  222.- POR LA SANGRE ",anthem:"        222.- POR LA SANGRE \n \n     Por el mundo brilla luz desde que muri Jess, \n Por nosotros en la cruz del Calvario; \n Los pecados l llev, de la culpa nos libr,\n  Con la sangre que man del Calvario. \n     CORO \n Por la sangre, por la sangre, \n Somos redimidos, s, por la sangre carmes; \n Por la sangre, por la sangre, \n Por la sangre de Jess, del Calvario. \n     -2- \n Antes, todo fue temor, ms ahora es amor, \n Porque comprend el valor del Calvario; \n Yo viv en perdicin, hoy poseo salvacin, \n Por la grande redencin del Calvario. \n     -3- \n Eres grande pecador? he aqu tu salvador; \n Tema del predicador: El Calvario. \n Salvacin a cada cual que padece por su mal, \n Dios ofrece gracia tal por la sangre. \n ",track:"1H-onbR-8Bu7Z2A4WpCZ8WQ9eRcG_OOT4",demo:"1vesMgupQ8Im6pstibn_qGWJKVClWEmv9"},
{id:223,title:"  223.- PRESTO ID  ",anthem:"        223.- PRESTO ID! \n \n     Presto id! presto id! con mensaje de luz, \n A oscuros pases que estn sin Jess.\n  Es mandato de l, en su nombre andad, \n Hoy el santo evangelio de Dios, proclamad! \n Oh! hoy mismo a l, vuestras vidas rendid, \n Presto id! presto id! presto id! \n     -2- \n Presto id! presto id! con palabras de Dios,\n A las tierras que nunca oyeron su voz; \n Que temprano vayis, s, por tierra y mar, \n En la fuerza de Cristo al perdido a salvar,\n  l os manda an, al momento salid, \n Presto id! presto id ! presto id! \n     -3- \n Presto id ! presto id! de descanso hablad, \n A las almas cansadas de tanta maldad; \n Pues por ellas Jess, con su muerte compr, \n Del pecado el rescate y descanso gan; \n El mensaje llevad, predicad: oh venid, \n Presto id! presto id! presto id! \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:224,title:"  224.- QUIEN ESTA POR CRISTO? ",anthem:"        224.- QUIN EST POR CRISTO? \n \n     Quin est por Cristo? Quin le servir? \n A salvar a otros quin le ayudar? \n Quin dejando el mundo, contra el error \n Luchar por siempre al lado del Seor? \n     CORO \n Por su magna gracia, su profundo amor, \n Yo estoy por Cristo, es mi Rey y Seor. \n        -2- \n No por la corona, ni el galardn, \n Entro en la lucha y alzo el pendn; \n Es por el perdido por quien l muri. \n En llevarlo al Salvador, me gozo yo. \n     -3- \n No con oro o plata, Cristo nos compr, \n Sino con la sangre que en la cruz verti; \n Los que a l acuden, bendecidos son, \n Libertad reciben, limpio corazn. \n     -4- \n Rudo el conflicto sigue con Satn, \n Mas, lo venceremos, Cristo es capitn; \n Su verdad eterna nuestro pabelln, \n Su presencia aviva todo corazn. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:225,title:"  225.- QUIEN SERA MI FIADOR?  ",anthem:"        225.- QUIEN SER MI FIADOR? \n \n     Quin ser mi fiador? \n Preguntme, al dejar \n Para siempre, \n La senda torcida del mal; \n Cuando el Juez sin igual \n Ante m contempl, \n A quin, pues, por defensa \n Al fin yo tendr? \n     CORO \n Cuando el Juez sin igual, \n Vino mi alma a juzgar, \n Y un fiador me pidieron, \n Por mi alma inmortal; \n All mi Jess, \n Se volvi en pro de m, \n Y exclam con amor: \n Yo respondo por ti. \n     -2- \n Cun oscuro el pasado \n En la cruz contempl, \n Cunto yerro y pecado \n En la senda que holl; \n Mas all bien no hay, \n Que aparezca en mi haber. \n Oh! quin, pues, por mi alma \n Querr responder? \n  -3- \n La justicia yo vi, \n Su balanza ostentar; \n Y tembl y gem \n La balanza al pisar, \n Y pesme yo all \n Y muy falto me hall, \n Oh! quin, pues, por mi alma \n Querr responder? \n     -4- \n Ven, Jess, en tu seno \n Yo quiero morar, \n Tus mandatos yo quiero \n Por siempre acatar; \n Mientras viva yo aqu, \n Quiero ser fiel a Ti. \n Y con gozo exclamar: \n T respondes por m. \n ",track:"1aImLOCMHAUXoS9JNcZmxzRoWdL0a-Xty",demo:"1aWE-0uMalZbqwhkKZAR_hZTJ2XJopfGG"},
{id:226,title:"  226.- QUIERO A CRISTO  ",anthem:"        226.- QUIERO A CRISTO \n \n     Pompa y fama y placer. \n Oro y plata por doquier, \n No los quiero yo tener, \n Quiero a Cristo, slo Cristo. \n     CORO \n Quiero a Cristo, mi Seor. \n Quiero a Cristo y su amor, \n l es mo, suyo soy. \n Quiero a Cristo, slo Cristo. \n     -2- \n Muchos guas hay doquier, \n Y maestros del saber; \n Que ensean su querer, \n Quiero a Cristo, slo Cristo. \n     -3- \n Se acaba la salud, \n El dinero y su virtud; \n La riqueza y su lad, \n Quiero a Cristo, slo Cristo. \n     -4- \n Cuando en la eternidad, \n Vistas de gran majestad, \n Dir con felicidad, \n Quiero a Cristo, slo Cristo. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:227,title:"  227.- QUIERO SEGUIR TUS PASOS ",anthem:"        227.- QUIERO SEGUIR TUS PASOS \n \n     Quiero seguirte fiel, mi Jess, \n Llevar tu cruz, llevar tu cruz. \n Yo contigo quiero andar, \n Sin vacilar, sin vacilar. \n     CORO \n En tus pasos quiero seguir \n Cerca de Ti, cerca de Ti; \n Y si encuentro pruebas aqu, \n Dame confianza en Ti. \n     -2- \n Con Jess, yo quiero andar, \n Slo con l, slo con l;\n  Paz y gozo yo tendr, \n Al serle fiel, al serle fiel. \n     -3- \n Dame ardiente corazn, \n Lleno de amor, lleno de amor; \n Y tu Espritu, Seor, \n Como guiador, como guiador. \n     -4- \n Cada da quiero cumplir \n Tu voluntad, tu voluntad; \n Y servirte a Ti, Seor, \n En humildad, en humildad. \n ",track:"1mv7hFXSUwtNvZH4C9nsPLDLaupN9ze5h",demo:"1DZLIFxwLHRuRjLeZLFvhq7OrdFATaBkM"},
{id:228,title:"  228.- RESPUESTA NO HAY?  ",anthem:"        228.- RESPUESTA NO HAY? \n      Respuesta no hay, del ruego, que en tu pecho \n Con ansiedad alzaste en tu dolor? \n Tu fe vacila ya y tu esperanza, \n Creyendo vano el ruego a tu Seor? \n No digas nunca que El no oy tu voz, \n Tu anhelo, cumplir despus, tu Dios, \n Tu anhelo, cumplir despus, tu Dios. \n     -2- \n Respuesta no hay? quiz cuando elevaste \n Tu ansiosa voz al trono celestial, \n Temiste no sufrir tan larga espera; \n Tan ruda fue la lucha con el mal! \n Mas, t vers que el tiempo ir veloz, \n Y te responder despus, tu Dios, \n Y te responder despus, tu Dios. \n     -3- \n Respuesta no hay? no digas que te olvida, \n Quiz tu parte no cumplida vio; \n Cuando tu ansioso ruego a Dios alzaste, \n De fe, la lucha en tu alma comenz. \n Si de su ley tan slo vas en pos, \n Respuesta, te dar despus, tu Dios, \n Respuesta, te dar despus, tu Dios. \n     -4- \n Respuesta no hay? la fe tenerla debes \n Si en Cristo, Roca eterna, firme est; \n Segura siempre queda en la tormenta \n Ni al rayo ni a los vientos temer. \n Mas, sabe bien que Dios oir su voz \n Y clama: lo ha de hacer despus mi Dios! \n Y clama: lo ha de hacer despus mi Dios! \n ",track:"1IOFR7jHFgaKAUm4XAYGlFv5iP4yYu5O2",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:229,title:"  229.- ROCA DE LA ETERNIDAD  ",anthem:"        229.- ROCA DE LA ETERNIDAD \n \n     Roca, de la eternidad, \n Fuiste abierta para m, \n S mi escondedero fiel, \n Slo encuentro paz en Ti. \n Rico, limpio manantial, \n En el cual, lavado fui. \n     -2- \n Aunque fuese siempre fiel, \n Aunque llore sin cesar, \n Del pecado no podr \n Justificacin lograr, \n Slo en Ti teniendo fe, \n Deuda tal podr pagar. \n     -3- \n Mientras tenga que vivir \n Mi postrer suspiro al dar, \n Cuando vaya a responder \n En tu augusto tribunal, \n S mi escondedero fiel \n Roca, de la eternidad. \n ",track:"16WjWRUn719EYwsTF6u3TdsPXRDS84CtU",demo:"1iFl5G2mvkjOUlRNO5wW3Ysbjt1z2EvgX"},
{id:230,title:"  230.- SAGRADO ES EL AMOR  ",anthem:"   230.- SAGRADO ES EL AMOR \n \n     Sagrado es el amor, \n Que nos ha unido aqu. \n A los que creemos, del Seor \n La voz que llama a s. \n     -2- \n A nuestro Padre Dios, \n Roguemos con fervor; \n Almbrenos la misma luz, \n Nos una el mismo amor. \n     -3- \n Nos vamos a ausentar, \n Mas nuestra firme unin, \n Jams podrse quebrantar \n Por la separacin. \n     -4- \n Un da, en la eternidad, \n Nos hemos de reunir; \n Que Dios nos lo conceda, har \n El frvido pedir. \n ",track:"189TFb4tPn7VkVYBmY59dqtIo-h5rgwfr",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:231,title:"  231.- SALID EMBAJADORES DEL SEOR",anthem:"        231.- SALID, EMBAJADORES DEL SEOR \n \n     Od, od! lo que nos manda el Seor; \n Marchad, marchad! hablando de mi amor. \n Y he aqu, yo con vosotros estar, \n Los das todos, hasta el fin, os guardar. \n     CORO \n Id, id, por el mundo, \n Id, id, y predicad el Evangelio; \n Id, id, va adelante \n El Todopoderoso Salvador; \n Gloria! gloria! aleluya a Jess! \n Gloria! gloria! aleluya a Jess! \n Nuestras almas l salv \n Nuestras manchas l lav, \n Proclamemos, pues a todos su amor. \n     -2- \n Pensad, pensad! la condicin del pecador; \n Qu triste es, qu llena de dolor! \n Sin luz, sin paz, camina a la eternidad,\n Y no conoce el peligro en que est. \n     -3- \n Salid, salid! embajadores del Seor; \n Buscad, buscad! al pobre pecador, \n Aprovechad el tiempo que el Seor os da, \n Pues, pronto el da de salud, acabar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:232,title:"  232.- SALIO TRIUNFANTE  ",anthem:"        232.- SALI TRIUNFANTE \n     Resucit! la nueva dad \n Al mundo que su muerte vio; \n Subi a la cruz en mi lugar, \n Mas del sepulcro, revivi. \n     CORO \n Por qu buscis al Cristo aqu, \n Si entre los muertos ya no est? \n No le lloris, cantad, red, \n Y proclamad: El Cristo, vive y reina ya. \n     -2- \n Resucito! ya no tendr \n Sombras, la tumba para l; \n El que muriere vivir; \n El que creyere slo en l. \n     -3- \n Vironle, tristes, sepultar, \n Cuantos en l tuvieron fe; \n Toda esperanza muerta est, \n Creyeron sepultar con l. \n     -4- \n Mas el sepulcro no logr \n En sus prisiones, detener, \n Al Cristo Rey, que vencedor \n Fue del infierno y su poder. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:233,title:"  233.- SALVA AL INCREDULO  ",anthem:"        233.- SALVA AL INCRDULO \n \n     Ama a tus prjimos, \n Piensa en sus almas, \n Diles la historia del tierno Seor; \n Cuida del hurfano. \n Hazte su amigo; \n Cristo le es Padre y fiel Salvador. \n     CORO \n Salva al incrdulo, \n Mira el peligro; \n Dios le perdonar, \n Dios lo amar. \n     -2- \n Dentro del corazn \n Triste, abatido, \n Obra el Espritu de salvacin, \n Dndole nimo \n Para salvarse; \n Llvalo al Maestro con abnegacin. \n     -3- \n Salva a tus prjimos, \n Cristo, te ayuda; \n Fuerza de Dios, ser tuya en verdad; \n l te bendecir \n En tus esfuerzos; \n Con l, disfrutars la eternidad. \n ",track:"1QaY2b79UvwqbFhXXikTb4Db7mi59qjoq",demo:"13ecuZW6xknL74ws7bQhldSXLTMZSOa46"},
{id:234,title:"  234.- SALVADOR MI BIEN ETERNO ",anthem:"        234.- SALVADOR, MI BIEN ETERNO \n \n     Salvador, mi bien eterno, \n Ms que vida para m, \n En mi fatigosa senda, \n Cerca siempre, te halle a Ti. \n Junto a Ti, junto a Ti, \n Junto a Ti, junto a Ti; \n En mi fatigosa senda \n Cerca siempre, te halle a Ti. \n     -2- \n No los bienes ni placeres, \n Ni renombre busco aqu, \n En las pruebas, en desdenes, \n Cerca siempre te halle a Ti. \n Junto a Ti, junto a Ti, \n Junto a Ti, junto a Ti; \n En las pruebas, en desdenes, \n Cerca siempre te halle a Ti. \n        -3- \n Yendo por sombro valle, \n En rugiente mar hostil, \n Antes y despus del trance, \n Cerca siempre te halle a Ti. \n Junto a Ti, junto a Ti, \n Junto a Ti, junto a Ti; \n Antes y despus del trance, \n Cerca siempre te halle a Ti. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:235,title:"  235.- SALVO EN JESUS  ",anthem:"        235.- SALVO EN JESS \n \n     Bajo sus alas, seguro descanso, \n Aunque anochezca, y amague el turbin; \n En l confo, su brazo me guarda, \n Hijo soy yo, de eternal redencin. \n     CORO \n Salvo en Jess, salvo en Jess, \n Quin de l podr apartarme? \n Bajo sus alas mi alma estar, \n Salva y segura por siempre. \n     -2- \n Bajo sus alas encuentro refugio, \n Cuando temor hay en mi corazn; \n Si a mis heridas no hay cura en la tierra, \n Cristo me alivia y me da salvacin. \n     -3- \n Bajo sus alas hay paz indecible, \n Me escondo aqu, mientras dure mi afn; \n Fiel proteccin de mi fiero enemigo, \n Paz y salud me proporcionarn. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:236,title:"  236.- SANTA BIBLIA  ",anthem:"        236.- SANTA BIBLIA \n \n     Santa Biblia, para m, \n Eres un tesoro aqu; \n T contienes con verdad \n La divina voluntad; \n T me dices lo que soy, \n De quin vine y a quin voy. \n    -2- \n T reprendes mi dudar, \n T me exhortas sin cesar; \n Eres faro que a mi pie, \n Va guiando por la fe; \n A las fuentes del amor, \n Del bendito Salvador. \n     -3- \n Eres infalible voz, \n Del espritu de Dios; \n Que vigor al alma da, \n Cuando en afliccin est; \n T me enseas a triunfar, \n De la muerte y el pecar. \n     -4- \n Por tu santa letra s, \n Que con Cristo, reinar; \n Yo que tan indigno soy, \n Por tu luz al cielo voy. \n Santa Biblia! para m, \n Eres un tesoro aqu. \n ",track:"1yOM2Lz6zeEH-vC0hJ63RhSfB5d9IUT1B",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:237,title:"  237.- SANTA PAZ Y PERDON  ",anthem:"        237.- SANTA PAZ Y PERDN \n \n     La voz del evangelio, \n Por doquier resuena ya, \n Buenas nuevas para todos, \n Que las quieran aceptar: \n Dios, de tal modo am \n Al perdido pecador, \n Que del cielo dio su Hijo, \n Oh, qu infinito amor! \n     CORO \n Santa paz . . . y perdn, \n Suena el eco celestial. \n Santa paz . . . y perdn, \n Dios concede al mortal. \n     -2- \n La voz del evangelio, \n Ya pregona a todo ser, \n Que hay hartura para todos, \n Si por fe, quieren comer; \n El pan de vida soy, \n Satisfecho quedars, \n Y de tu alma tus pecados \n Quitar y paz tendrs. \n     -3- \n La voz del evangelio, \n Viene ahora a avisar, \n De la destruccin eterna, \n La que hay que evitar; \n Salvaos sin tardar, \n No os demoris as, \n No volvis atrs la vista, \n El peligro est all. \n     -4- \n La voz del evangelio \n Grata y alegre es, \n El amor de Jesucristo, \n Da perdn mediante fe, \n Las nuevas os las doy, \n De haber un salvador, \n Muy amante y poderoso, \n Que perdona al pecador. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:238,title:"  238.- SANTIDAD A JEHOVA  ",anthem:"        238.- SANTIDAD A JEHOV \n \n     Llamado a santidad, pueblo de Dios, \n Que con su sangre, Jess, os compr, \n Que de entre el mundo, de mal os sac, \n De esclavitud, de pecado os libr. \n     CORO \n Santidad hoy a Jehov, nuestro canto y loor, \n Santidad hoy a Jehov, \n Mientras vamos a Sin. \n S, cantad, cantad cancin. \n Santidad hoy a Jehov, sea por siempre \n     -2- \n Llamado a santidad, hijos de luz, \n Con Jesucristo debis siempre andar \n Con albo manto, sin mancha o pecar, \n Llena vuestra alma de espritu y luz. \n     -3- \n Llamada a santidad, esposa fiel, \n Espera siempre al Esposo y Seor, \n Al cielo eleva tu rostro y amor, \n Hasta que en gloria aparezca Emmanuel. \n ",track:"1KlHjpktSKgDD7xHFusz6oW0FXmd9mK1z",demo:"1iGTkgxCPzqFEggRMke1mD_CVvF5ESU6w"},
{id:239,title:"  239.- SANTO. SANTO. SANTO  ",anthem:"     239.- SANTO! SANTO! SANTO! \n \n     Santo! Santo! Santo! \n Seor Omnipotente, \n Siempre el labio mo, \n Loores te dar; \n Santo! Santo! Santo! \n Te adoro reverente, \n Dios en tres personas, \n Bendita Trinidad. \n     -2- \n Santo! Santo! Santo! \n En numeroso coro, \n Santos escogidos \n Te adoran con fervor; \n De alegra llenos, \n Y sus coronas de oro, \n Rinden ante el trono \n Glorioso del Seor. \n    -3- \n Santo! Santo! Santo! \n La inmensa muchedumbre, \n De ngeles que cumplen \n Tu santa voluntad; \n Ante Ti se postra, \n Baada por tu lumbre, \n Ante Ti, que has sido, \n Que eres y sers. \n     -4- \n Santo! Santo! Santo! \n Por ms que ests velado, \n E imposible sea \n Tu gloria contemplar; \n Santo t eres slo, \n Y nada hay a tu lado, \n En poder perfecto, \n Pureza y caridad. \n     -5- \n Santo! Santo! Santo! \n La gloria de tu nombre, \n Vemos en tus obras, \n En cielo, tierra y mar,\n  Santo! Santo! Santo! \n Te adorar todo hombre \n Dios en tres personas, \n Bendita Trinidad! \n ",track:"1hbsEfIOw3yyyPat3xs29zx6U_Rsz7tcR",demo:"10qf8v3k7OG_vWWz5lv-qEGo3ukkQQeVw"},
{id:240,title:"  240.- SANTIDAD AL SEOR ",anthem:"        240.- SANTIDAD AL SEOR \n \n     Ya resuena alegre la cancin: \n Para siempre santidad! \n Nos anuncia plena salvacin, \n Para siempre santidad! \n     CORO \n Para siempre, santidad! \n Para siempre, santidad! \n Alabemos todos al Rey de gloria! \n Para siempre santidad! \n     -2- \n Del pecado innato nos limpi, \n Para siempre santidad! \n De su amor perfecto nos llen, \n Para siempre santidad! \n     -3- \n Alzaremos fieles el pendn, \n Para siempre santidad! \n Lucharemos sin vacilacin, \n Para siempre santidad! \n     -4- \n Alabanzas demos al Seor, \n Para siempre santidad! \n Entonemos todos con fervor, \n Para siempre santidad! \n ",track:"1uw_gzjl7CkzfEULRvN_YbIi77BvG4V0A",demo:"1ce5pJluMhJUqtOxhT6trnGf87Xa0JLOP"},
{id:241,title:"  241.- SANTO. SANTO. SEOR JEHOVA",anthem:"        241.- SANTO, SANTO, SANTO, SEOR JEHOV \n \n     Nuestro sol, se pone ya; \n Todo en calma quedar, \n La plegaria levantad, \n Que bendiga la bondad \n De nuestro Dios. \n     CORO \n Santo, Santo, Santo, \n Seor Jehov! \n Cielo y tierra, de tu amor \n Llenos hoy estn, Seor, \n Loor a Ti! \n     -2- \n Oh, Seor! tu proteccin. \n Dale hoy al corazn; \n Dale aquella dulce paz. \n Que a los tuyos siempre das, \n Con plenitud. \n     -3- \n Oh, Seor! que al descansar \n Pueda en Ti, seguro estar, \n Y maana mi deber, \n Pueda siempre fiel hacer; \n En tu loor. \n ",track:"1swIxdyZJ3AUOjta1lRbzcQ0w0HM4L40t",demo:"17v3v9EtHtpdZjzyjakQbA4AVzYRCur4x"},
{id:242,title:"  242.- SEOR REPOSAMOS  ",anthem:"        242.- SEOR, REPOSAMOS \n \n     Seor, reposamos en tu santo da, \n Cumpliendo el mandato legado por Ti; \n Reposo buscamos, Dios nuestro, en tu seno, \n Que as ordenaste en el Sina, \n Que as ordenaste en el Sina. \n     -2- \n Tus hijos, se acercan, oh Dios, a tu trono, \n En santa, ferviente, sincera oracin; \n Pidindote, escuches su humilde plegaria, \n Y gocen por siempre, de tu proteccin, \n Y gocen por siempre, de tu proteccin. \n     -3- \n Alienta a tus hijos, que obtengan victoria, \n Que puedan fielmente, tus leyes cumplir; \n Que cuando vinieres, Seor, en tu gloria, \n Pudieren por siempre, contigo vivir, \n Pudieren por siempre, contigo vivir. \n ",track:"1rAmsaH_C5PHxaeMajcBR3eZQ1RrFtOAT",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:243,title:"  243.- SIEMPRE ORAD  ",anthem:"        243.- SIEMPRE ORAD \n \n     Siempre orad, muy pronto viene Cristo, \n Guarda bien tu corazn; \n Retn firme todo lo que tienes, \n Tus trabajos premiar. \n     CORO \n Siempre orad, nos manda Cristo, \n Y velad... en oracin; \n Pronto vendr... l en las nubes \n Y nos llevar con l. \n      -2- \n Siempre orad, que si hoy viniere Cristo, \n l te halle en la lid, \n Con la espada suya bien empleada, \n Defendiendo la verdad. \n     -3- \n Siempre orad, no seas imprudente, \n Sin aceite por tu luz; \n Sin demora toma la promesa, \n De divina plenitud. \n     -4- \n Siempre orad, con celo y con constancia, \n Ejerctate en la fe; \n Ten el nimo de Jesucristo, \n Y sus huellas sigue fiel. \n ",track:"1XpXQM0CP8vpO5qVVTBpabEgLOh7Cebn6",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:244,title:"  244.- SI HAY VALOR Y FE  ",anthem:"        244.- SI HAY VALOR Y FE \n \n     Si en tu senda las nubes \n Agolparse ves, \n No vaciles por ello, \n Ni flaqueen tus pies; \n Cada nube que venga, \n No podr traer \n Ms que pruebas que pasan, \n Si hay valor y fe. \n     CORO \n Si hay valor y fe, \n Si hay valor y fe, \n En la ms oscura noche \n Siempre hay luz. \n Si hay valor y fe, \n Si hay valor y fe, \n Gozo y paz traer la lucha \n Si hay valor y fe. \n     -2- \n Si es tu vida una carga, \n De cuidados mil, \n Olvidado de todo, \n Te podrs sentir. \n Si tu ayuda acudieres \n A llevar doquier; \n Esto endulza la vida, \n Si hay valor y fe \n     -3- \n Pon en alto tus ojos, \n Sin dudar jams. \n Que en las lides del mundo, \n Vencedor saldrs; \n Que si hay flores y encantos, \n Tras invierno cruel, \n Trae encantos la vida, \n Si hay valor y fe. \n ",track:"1a7Q6GdM2Q_d5JqKVdVlvbWGVJQ3Fddr4",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:245,title:"  245.- SI HE OFENDIDO A ALGUIEN ",anthem:"        245.- SI HE OFENDIDO A ALGUIEN \n \n     Si he ofendido a alguien hoy, Seor, \n Si un alma he apartado de tu amor, \n Si he caminado en senda egosta hoy: \n Imploro perdn. \n     -2- \n Si he conversado ocioso, en vez de orar, \n Si en tu redil quera descansar, \n Y el puesto en dura lid abandonar, \n Imploro perdn. \n     -3- \n Si en su afliccin, alguno abandon, \n Si tu cario tierno no mostr; \n No quiero yo, perder tu amor y fe, \n Imploro perdn. \n     -4- \n Perdona lo que he confesado aqu \n Y lo secreto que se hallara en m, \n Mi ayuda y gua s, confo en Ti, \n Seor, Amn. \n ",track:"1woErXzCyJPvTIQBxA5v0Z9-nyO6EGPOc",demo:"1dyJcZPYzY0Zva7MmqjLpNAGkfX-nd_Bp"},
{id:246,title:"  246.- SIGUE ORANDO  ",anthem:"        246.- SIGUE ORANDO \n \n     De pesar te ves rodeado? \n Tienes pruebas en verdad? \n De sufrir ests cansado? \n Ves que viene tempestad? \n     CORO \n Sigue orando, sigue orando, \n Aunque ruja tempestad: \n El Seor est velando, \n Te dar tranquilidad. \n     -2- \n En las penas, en el duelo, \n Que tu alma sufrir; \n En Jess, tendrs consuelo, \n l, tus penas calmar. \n     -3- \n Al Seor tu voz eleva, \n Ve a Dios con gran fervor; \n l, de pena te releva, \n Y mitiga tu dolor. \n    -4- \n Tarda Dios en contestarte? \n Sigue orando sin cesar; \n Pronto ayuda puede darte, \n Tu oracin al contestar. \n ",track:"1_13ZBDuKUM-_DtEro33ttyCiLgbHaZIs",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:247,title:"  247.- SIN FRUTO HE DE LLEGAR ",anthem:"        247.- SIN FRUTO HE DE LLEGAR? \n \n     Oh! Sin fruto he de llegar \n Al cruzar el ancho mar? \n Cuando llegue ante el trono del Seor \n Me dar vergenza cruel, \n Al llamar mi nombre l, \n Por las faltas de las obras del amor. \n     -2- \n Qu amargura he de sentir, \n Si mis cuentas al rendir, \n Hallo que no he sido fiel a mi Seor, \n Por talento que enterr, \n Y ni un alma rescat \n Porque nunca habl de Cristo y de su amor. \n     -3- \n La cosecha al concluir, \n Le podr or decir: \n Ven, obrero, a gozar de tu mansin, \n Mis espigas contar, \n Y en gavillas llevar; \n Esa mies, a recibir el galardn. \n     -4- \n Al abrir el libro aquel, \n Estar mi nombre en l, \n En sus hojas blancas, nveas cual la luz? \n Mi carrera al concluir, \n Le podr a Jess or: \n Al descanso ven, y deja ya tu cruz. \n     -5- \n Mostrar mi gratitud, \n A mi Salvador Jess; \n Trabajando con ardor hasta el final, \n Y alguna ofrenda de amor, \n Pondr a los pies del Seor. \n Cuando venga la llamada celestial. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:248,title:"  248.- SI YO MIS PECADOS PUDIERA LAVAR",anthem:"        248.- SI YO MIS PECADOS PUDIERA LAVAR \n \n     Si yo mis pecados pudiera lavar, \n Ni sangre en pago pudiera negar; \n Si obras valieran en pago del mal, \n No fuera calvario el solo caudal; \n No fuera calvario el solo caudal. \n     -2- \n Mas, s que no tengo, de m, santidad; \n No puedo limpiarme de tanta maldad \n Ni santo, ni ngel ofrece, audaz, \n Quitar mis pecados y darme solaz, \n Quitar mis pecados y darme solaz. \n     -3- \n Oh Cristo bendito! en Ti es mi paz; \n Tus obras, tu sangre, caudal eficaz, \n T llamas las almas muriendo de sed: \n En m, es la fuente, venid y bebed, \n En m, es la fuente, venid y bebed. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:249,title:"  249.- SOLDADOS DE CRISTO  ",anthem:"        249.- SOLDADOS DE CRISTO \n \n     Soldados de Cristo, que estis en la lid; \n Luchad sin desmayo, pelead con valor; \n Seguid adelante y luchando, decid: \n Rendos a Cristo, l es el Seor! \n     CORO \n Oh, jvenes, nios y ancianos, marchad, \n Llevando en las manos las armas de luz; \n Las almas perdidas con celo buscad; \n Y presto llevadlas a Cristo Jess. \n     -2- \n Pelead oh cristianos! la causa es de Dios, \n Seguid adelante, luchad por Jess, \n Sed siempre valientes y alzad vuestra voz, \n Diciendo que Cristo muri en una cruz. \n     -3- \n La aurora se acerca del da final, \n En que han de premiarse la fe y el valor, \n Entonces Jess, galardn celestial, \n Dar a los que al mundo anunciaron su amor. \n ",track:"1LTvmpH-XwTSDrLndkcDkVhSy9iXCTwOc",demo:"1y7j1rIrANgx8fn4hmVMRay8ysgTg0V5p"},
{id:250,title:"  250.- SOLO CRISTO  ",anthem:"        250.- SLO CRISTO \n \n    Yo hall un fiel amigo para siempre, \n Ms que hermano, padre o madre es l; \n Ms que plata, o que oro, o que cobre, \n Es de todos, Jesucristo, el ms fiel. \n        CORO \n Slo Cristo, slo Cristo, \n Es el lirio de los valles mi Jess; \n Slo Cristo, Slo Cristo, \n Es el lirio de los valles mi Jess. \n     -2- \n De Sarn l es la Rosa tan fragante, \n Y del valle, el lirio puro es l; \n En el alma quebrantada y tan triste, \n Sus olores de amor exhala l. \n     -3- \n Es Jess amante, tierno, compasivo; \n Lento en ira, grande en perdonar. \n l levanta con amor al abatido \n Slo sabe Jesucristo, cmo amar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:251,title:"  251.- SOMOS HIJOS DE UN REY ",anthem:"        251.- SOMOS HIJOS DE UN REY \n \n     Somos hijos de un rey soberano; \n Que los tronos temen ante l, \n El que dice: sea esto y se hace, \n Y la mar le obedece tambin. \n El que limpia de todo pecado, \n Porque el Padre le dio ese poder; \n Y convierte al hombre ms malo, \n En oveja de un rebao fiel. \n     CORO \n Si t quieres conocer sus grandezas, \n Las que el mundo jams te brind; \n Las que quitan pesares y tristezas, \n Las que el padre por amor nos concedi. \n     -2- \n No hay amor ms sublime y ms puro, \n Que el que mana del hijo de Dios, \n Que an habiendo corazones tan duros, \n l muri por darnos salvacin. \n En su fuente de vida han bebido. \n Muchos hombres buscando perdn; \n Y su sangre poder ha tenido, \n l te limpia y te brinda su amor. \n    -3- \n Si en tu vida, jams has tenido, \n Un instante de consolacin; \n Slo Cristo levanta al cado, \n l muri por darnos salvacin. \n Fue su rostro tan santo escupido, \n Maltratado sin ms compasin, \n En la cruz l llev, suspendido, \n Tus pecados y tu rebelin. \n ",track:"1pbub__B5BI_Fqgb6qgsTkFnVY3Qv1IM0",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:252,title:"  252.- SOY FELIZ EN EL SERVICIO DEL SEOR ",anthem:"        252.- SOY FELIZ SIRVIENDO AL SEOR \n     Soy feliz en el servicio del seor; \n Muy alegre, tan alegre; \n Tengo paz, contentamiento y amor, \n En servir al Salvador. \n     CORO \n En servir al Salvador, \n En servirle con amor; \n Cun alegre yo me siento \n En servir a mi Seor! \n     -2- \n Soy feliz en el servicio del Seor, \n Muy alegre, tan alegre; \n Hoy dedico mis talentos al Seor, \n Servir al Salvador. \n     -3- \n Soy feliz en el servicio del Seor, \n Muy alegre, tan alegre; \n En la lucha nunca faltar el valor, \n Que me da el Salvador. \n ",track:"1u6Kn21KoSpQ7MIKio--9muUpKgIoe7xy",demo:"1g6W3EJRxNkYYqW1SoRZgRUB_VRvfp1BV"},
{id:253,title:"  253.- SOY YO SOLDADO DE JESUS?",anthem:"        253.- SOY YO SOLDADO DE JESS? \n \n     Soy yo soldado de la cruz, \n Y siervo del Seor? \n No temer llevar su cruz, \n Sufriendo por su amor. \n        CORO \n Despus de la batalla, nos coronar, \n Dios nos coronar, Dios nos coronar, \n Despus de la batalla, nos coronar, \n En aquella santa Sin. \n Ms all, ms all, en aquella santa Sin; \n Despus de la batalla nos coronar, \n En aquella santa Sin. \n     -2- \n Lucharon otros por la fe; \n Cobarde habr de ser? \n Por mi seor yo pelear, \n Confiando en su poder. \n     -3- \n Es menester que sea fiel, \n Que nunca vuelva atrs, \n Que siga siempre en pos de l, \n Y me guiar en paz. \n ",track:"11-6JjaOHWUIq5G5ZXh7PVOpyktgkZw55",demo:"1rmlNU3AZCJryAjzqGpaMdo0KMFP-9pac"},
{id:254,title:"  254.- SU AMOR ME LEVANTO  ",anthem:"          254.- SU AMOR ME LEVANT \n     Lejos de mi dulce hogar, \n Vagaba yo sin Dios, \n A travs de tierra y mar, \n Sin esperanza y paz; \n Mas, el tierno Salvador \n Vindome en afliccin, \n Por su infinito amor \n Me levant. \n     CORO \n Su grande amor me levant, \n De densa oscuridad me libert; \n Su grande amor me levant, \n De densa oscuridad me libert. \n     -2- \n Todo entrego a mi Jess, \n Siempre le seguir, \n He tomado ya su cruz \n Y el mundo atrs, dej. \n Tan excelso y grande amor \n Requiere la cancin, \n Y el servicio fiel \n De cada corazn. \n     -3- \n Ven a l, oh pecador, \n No te rechazar; \n Con ternura, el buen pastor, \n Hoy te recibir; \n Tus pecados borrar, \n Gozo tendrs sin par; \n Gracia y fuerza te dar \n Para triunfar. \n ",track:"1rxDFL2OtVrw7PDK9gugiBEPGygKp3rCP",demo:"1NBxakCiL2r6hDjgwyTdibpHAIzDSss8C"},
{id:255,title:"  255.- SUENAN LAS TROMPETAS  ",anthem:"        255.- SUENAN LAS TROMPETAS \n \n     Suenan las trompetas en el tercer cielo, \n Y la multitudes van cantando hoy; \n Es que van pasando todos los valientes, \n Que en la tierra, amaron a Cristo Jess. \n     CORO \n No, no, no, no, no, no, no; \n No quiero separarme; \n No, no, no, no, no, no, no; \n No he de dejar de amarle. \n Su grande amor me mostr \n Cuando l vino a salvarme, \n No, no, no, no, no, no, no; \n Nunca podrn de l apartarme. \n     -2- \n Otros que muy tristes, caminan llorando, \n Estn lamentando un grande dolor; \n Es que el Juez divino, con dedo de fuego, \n Les neg la entrada a la hermosa Sin. \n     -3- \n T de cules eres? De los que se salvan, \n O de los que buscan la condenacin? \n Si quieres salvarte tienes que esforzarte,\n  Trabajando siempre por el Salvador. \n ",track:"1tBrCTzfDwh8RZeyvonyXyqqhEour8t11",demo:"1B_F5JiXk0jvYrAFJRyJGHCqkMkfzNfkl"},
{id:256,title:"  256.- SUENEN DULCES HIMNOS  ",anthem:"        256.- SUENEN DULCES HIMNOS \n \n   Suenen dulces himnos gratos al Seor, \n Y iganse en concierto universal! \n Desde el alto cielo baja el Salvador, \n Para beneficio del mortal. \n        CORO \n Gloria! Gloria sea a nuestro Dios! \n Gloria! s, cantemos a una voz. \n Y el cantar de gloria que se oy en Beln \n Sea nuestro cntico tambin. \n     -2- \n Montes y collados, fluyan leche y miel, \n Y abundancia esparzan y solaz, \n Dios se compadece, viendo su afliccin; \n Y les muestra buena voluntad. \n     -3- \n Lata en nuestro pecho noble gratitud,\n  Hacia quien nos brinda redencin. \n Y a Jess el Cristo, que nos da salud, \n Tributemos nuestra adoracin. \n ",track:"1omFAFITshqMPm_YqPi-BuP0s4LG_xO1y",demo:"1Avb5RYd5QLEHR3-tiHeSg5vQKmcOxKDQ"},
{id:257,title:"  257.- TAL COMO SOY ",anthem:"                 257.- TAL COMO SOY \n \n     Tal como soy, sin ms decir, \n Que a otro yo no puedo ir, \n Y t me invitas a venir; \n Bendito Cristo, heme aqu. \n     -2- \n Tal como soy, sin demorar \n Del mal querindome librar; \n T slo puedes perdonar, \n Bendito Cristo, heme aqu. \n     -3- \n Tal como soy, en afliccin, \n Expuesto a muerte y perdicin; \n Buscando vida y perdn, \n Bendito Cristo, heme aqu. \n     -4- \n Tal como soy, tu gran poder; \n Me vence, y busco tu favor, \n Servirte, quiero con valor, \n Bendito Cristo, heme aqu. \n ",track:"1x3pqLB4NQgCwxn9XJn3njrHRCKn0x6Bk",demo:"11idbqqedO3JZKL0uelie58NjZqNJE2Gx"},
{id:258,title:"  258.- TE LOAMOS. OH DIOS   ",anthem:"        258.- TE LOAMOS, OH DIOS! \n \n     Te loamos, Oh Dios! \n Con unnime voz, \n Que en Cristo, tu Hijo, \n Nos diste perdn \n     CORO \n Aleluya! te alabamos. \n Cun grande es tu amor! \n Aleluya! te adoramos, \n Bendito Seor. \n      -2- \n Te loamos, Jess, \n Que tu trono de luz \n Has dejado, por darnos \n Salud, en la cruz. \n     -3- \n Te damos loor, \n Santo Consolador, \n Que nos llenas de gozo, \n Y santo valor. \n     -4- \n Unidos load, \n A la gran Trinidad, \n Que es la fuente de gracia, \n Salud, y verdad. \n ",track:"1pRp5L9qWXpgZziScEH0_YjDPy-qJps5X",demo:"1RaMgvFUWy6-ov4X8MeUo-jJQEWI9p3lv"},
{id:259,title:"  259.- TENDRAS QUE RENACER   ",anthem:"        259.- TENDRS QUE RENACER \n \n     Un hombre, de noche, llegse a Jess, \n Buscando la senda de vida y de luz. \n Y Cristo le dijo: si a Dios quieres ver, \n Tendrs que renacer. \n     CORO \n Tendrs que renacer, \n Tendrs que renacer; \n De cierto, de cierto, te digo a ti \n Tendrs que renacer. \n     -2- \n Si acaso a los cielos t quieres entrar, \n Y all con los santos poder descansar, \n Si quieres la vida eternal obtener, \n Tendrs que renacer. \n     -3- \n Amigo, no debes jams desechar, \n Palabras que Cristo dignse hablar \n Si tu alma no quieres llegar a perder, \n Tendrs que renacer. \n     -4- \n A muchos, Jess, ha de resucitar \n A quienes entonces querrs encontrar, \n Pues esta proclama hoy debes creer, \n Tendrs que renacer. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:260,title:"  260.- TEMPLOS DE DIOS SOIS   ",anthem:"        260.- TEMPLOS DE DIOS, SOIS \n     Templos de Dios, sois, \n As est escrito. \n Templos de su Espritu de verdad;\n A l es dada libre entrada? \n Franca es la puerta de tu voluntad? \n  CORO \n Deja entrar, para morar... \n Al Santo Espritu... \n l ha venido, pacto cumplido,\n  Bendito Espritu de Dios. \n     -2- \n El que perdona, \n Quiere limpiarte, \n Toda la escoria de tu alma quitar; \n Purificado, muerto al pecado, \n El Santo Espritu podr entrar. \n     -3- \n Oh! peregrino, \n En el desierto, \n Entra en la tierra de leche y miel; \n Santificado, de Dios llenado \n Guiado sers por su Espritu fiel. \n ",track:"1K7CenepTNdmTu_eNIT0JVbqtjnwF6USW",demo:"1gXAhiDjLnJZedo8UJEUd_tiUhv4NJIOO"},
{id:261,title:"  261.- TE QUIERO MI SEOR  ",anthem:"        261.- TE QUIERO, MI SEOR \n \n     Te quiero, mi Seor, habita en m! \n Y vencedor ser por fe en Ti. \n     CORO \n Te quiero, s, te quiero, \n Siempre te anhelo, \n Bendceme ahora, acudo a Ti. \n     -2- \n Te quiero, oh Jess, mi Salvador, \n Oh hazme en verdad, tu servidor. \n     -3- \n Tu voluntad, Seor, ensame; \n Y de tu gran amor, oh! clmame. \n     -4- \n Oh, mi gran Bienhechor, en tentacin \n Concdeme valor y proteccin. \n ",track:"1hALmodayN3TTjwUh_ZcLSrZSXQRAHJF4",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:262,title:"  262.- TE SIENTES CASI?  ",anthem:"        262.- TE SIENTES CASI? \n \n     Te sientes casi resuelto ya? \n Te falta poco para creer? \n //:Por qu, pues, dices a Jesucristo: \n Hoy no, maana te seguir?:// \n     -2- \n Te sientes casi resuelto ya? \n Pues vence el casi, a Cristo ven. \n //:Que hoy es tiempo, pero maana \n Sobrado tarde pudiera ser:// \n     -3- \n Sabes que el casi no es de valor \n En la presencia del justo Juez. \n //:Ay, del que muere casi creyendo! \n Completamente perdido es!:// \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:263,title:"  263.- TIERNAS CANCIONES  ",anthem:"        263.- TIERNAS CANCIONES \n \n     Tiernas canciones alzad al Seor, \n Himnos que lleven del alma la fe; \n Y hablen muy alto del frvido amor, \n Que hay en el pecho del hombre que cree. \n Vengan trayendo ferviente oracin, \n Nios y ancianos de Dios al altar; \n Traigan a l, su corazn, \n nico don que podr aceptar. \n     CORO \n //:Cielo y tierra canten \n Al Seor de las naciones!:// \n Y los hombres todos \n Con alegres corazones, \n Sirvan al Seor que vida \n Y paz siempre les da. \n     -2- \n l es la fuente de toda bondad, \n Es de la vida la luz y el calor; \n Slo l nos libra de cruel ansiedad, \n Slo l aleja del alma el dolor; \n Digno es por tanto, que el hombre le d, \n Gloria y honor que resuenen doquier. \n Vamos a l, llenos de fe, \n Nos salvar con su gran poder. \n ",track:"1ANHsVGFzJhKcYlxJRdIA-4xK39zAKZwl",demo:"1wm7dEf_kyraI6tkAj5adFuIJytSOPjdX"},
{id:264,title:"  264.- TODO PODER   ",anthem:"          264.- TODO PODER \n     Lejos de aqu, sin Dios, sin esperanza, \n Millones de almas pueden perecer; \n Quines irn a dar las buenas nuevas \n De salvacin, por Cristo el Redentor? \n     CORO \n Todo poder es dado a M; \n Todo poder es dado a M. \n Id al mundo y proclamad el evangelio, \n Y estoy con vosotros, siempre. \n     -2- \n Las puertas ya abiertas os invitan. \n Siervos de Cristo, entrad en ellas hoy; \n La libertad, los pueblos necesitan. \n Romped el yugo del pecado atroz. \n     -3- \n La tierna voz de Cristo, os convida \n Almas perdidas, Por qu moriris? \n Venid a M, y el agua de la vida, \n En abundancia siempre la tendris. \n     -4- \n Pronto vendr el da, cuando todos, \n Gloria a Dios triunfantes cantarn. \n Con gozo a Sin vendrn los redimidos, \n Y a Jesucristo Rey coronarn. \n ",track:"1IkpDvWuwfmPyMHqYAooGUEWsg11HW18_",demo:"1DuOej68GmZm39DGBI2l-eJPwmw3JDmtN"},
{id:265,title:"  265.- TRABAJAD! TRABAJAD!  ",anthem:"        265.- TRABAJAD, TRABAJAD! \n \n     Trabajad! Trabajad! Somos siervos de Dios, \n Seguiremos la senda que el Maestro traz. \n Renovando las fuerzas con bienes que da, \n El deber que nos toca, cumplido ser. \n     CORO \n Trabajad! Trabajad! \n Esperad y velad! \n Confiad! Siempre orad! \n Que el Maestro pronto volver! \n     -2- \n Trabajad! Trabajad! hay que dar de comer, \n Al que pan de la vida quisiera tener; \n Hay enfermos que irn a los pies del Seor, \n Al saber que de balde los sana su amor. \n     -3- \n Trabajad! Trabajad! Fortaleza pedid, \n El reinado del mal, con valor combatid; \n Conducid los cautivos al Libertador, \n Y decid que de balde redime su amor. \n ",track:"1wK0Xe94EzY-dj1uotF4lqm_65NfM1LgZ",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:266,title:"   266.- TU AMARAS A CRISTO  ",anthem:"        266.- T AMARS A CRISTO \n \n     T que vagas en las tinieblas, \n Lejos de Cristo y su redil; \n Ven y ve cun amante es Cristo, \n Ven, contempla a tu Salvador. \n     CORO \n  Oh, cunto t amars a Cristo, \n Al mirar la gloria del Seor! \n Su corazn fue quebrantado, \n En la cruz por ti y por m. \n     -2- \n Ven, acepta perdn completo, \n Y paz y gozo podrs sentir; \n Alma triste, Jess te llama, \n l te espera con grande amor. \n     -3- \n Este amor tan sublime y tierno, \n Y tan profundo cual hondo mar; \n Brota libre del pecho herido, \n De Jess quien nos da salud. \n     -4- \n Y por siglos interminables, \n Ha de quedar en el corazn; \n Y en el cielo comprenderemos \n Solamente Su magnitud. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:267,title:"  267.- UN DIA CRISTO VOLVERA  ",anthem:"        267.- UN DA CRISTO VOLVER \n \n     Un da, Cristo volver, \n Al ascender lo prometi; \n Del mismo modo volver \n Y su pueblo ha de ver al Rey Jess. \n     CORO \n Muy pronto, s, Jess vendr \n Y alegre lo ver su pueblo; \n En vela todos debern estar \n Para poderle mirar. \n     -2- \n Los mensajeros del Seor, \n Afirman que Jess vendr; \n Y el Espritu de Dios, \n Las promesas de la Biblia, nos abri. \n     -3- \n Oh, gloria sin comparacin \n Ser mirar a nuestro Rey! \n Su rostro hermoso, brillar \n Eclipsando el fulgor del mismo sol. \n     -4- \n Oh, bienvenido Rey Jess! \n Tu Iglesia, ya esperando est; \n Y vana no ser la fe; \n Nuestra dicha es, Seor, que T vendrs. \n ",track:"1gYSRirDJs0q-4veHaeHQOe49p97sJEjt",demo:"1rICtnx5Y89NEIboBqe1IngMENnXfO_Jk"},
{id:268,title:"  268.- UN MENSAJE MUNDIAL  ",anthem:"           268.- UN MENSAJE MUNDIAL \n \n      La Iglesia se levanta airosa, con poder; \n Reclama los derechos santos de su Dios, \n La Biblia no puede cambiar, \n La Biblia ha de permanecer. \n     CORO \n El que quitare a este libro, \n El que aadiere a este libro, \n Dios raer su nombre de aquel; \n Libro que en la gloria est. \n   -2- \n Concilios que procuran la Biblia cambiar, \n Tratando a la salvacin fcil llegar, \n Consumidos ante Dios sern \n Si persisten en su pecar. \n     -3- \n Su ley es la misma, siempre as ser, \n Ni una tilde aunque el mundo pase, faltar; \n El descanso de la creacin, \n Tambin es el da del Seor. \n \n     Juan Barrera \n ",track:"1ThWVL1ellDMS0ZQM-oEA9UG3AjJhWLzr",demo:"1dT5fuIcGBVThQs46oxn1cISq2RjA6NDv"},
{id:269,title:"  269.- UN MUNDO ANCHO OS AGUARDA",anthem:"        269.- UN MUNDO ANCHO OS AGUARDA \n     Juventud, un mundo ancho aguarda, \n Por vuestra prdica de salvacin, \n Lleva tu luz donde slo hay tinieblas, \n Para guiar al que anda en perdicin. \n     CORO \n Sal ya, joven misionero; \n Ve ms all del mar; \n El testimonio es grande, \n No debes vacilar. \n     -2- \n Cuntas almas hoy da perecen \n Sin esperanza de salvacin, \n T eres culpable si no les enseas \n El buen camino de la salvacin. \n     -3- \n Levantemos de Cristo en alto \n Su evangelio de paz y amor, \n Hay recompensa para los que luchan \n Tendrn el cielo como galardn. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:270,title:"  270.- UN NUEVO DIA  ",anthem:"        270.- UN NUEVO DA \n \n     Al despertar, la maana hermosa \n De un nuevo da, que espero yo, \n Cuando en las nubes, esplendoroso \n Y victorioso vendr el Seor, \n Los que le esperan, suben invictos \n Al cielo santo, para gozar, \n De las delicias y la presencia \n Del Rey de gloria, del Rey de paz. \n     -2- \n Cuntos ignoran las cercanas \n Del nuevo da que llegar; \n Indiferentes, despreocupados, \n Viven ajenos del da final, \n Y sorprendidos y atormentados, \n Con gran lamento all vern; \n Que ya la puerta la habr cerrado\n  El Rey de gloria, el Rey de paz. \n     -3- \n Si has escuchado el gran mensaje, \n Y no deseas la perdicin; \n Cristo te ofrece descanso y gozo, \n Y vida eterna en su mansin. \n No te detengas, despierta pronto \n Del sueo insano de la maldad, \n Si t no quieres que te sorprenda, \n El Rey de gloria, el Rey de paz. \n     Letra y Msica: Armando Rodrguez \n ",track:"1PKBNWams5ZPBqYi94kLPWjiy0c26ExxR",demo:"1Z-qPQ-CT6it3Xk9LRgcYfrRAxhD5yOzi"},
{id:271,title:"  271.- UNOS SI. OTROS NO  ",anthem:"      271.- UNOS S, OTROS NO \n \n     Francas las puertas encontrarn; \n Unos s, otros no. \n De alguien las glorias sin fin sern, \n //: Y t? Y yo?:// \n Calles de oro, mar de cristal, \n Pleno reposo, perfecto amor; \n Unos tendrn celestial hogar, \n //: Y t? Y yo?:// \n     -2- \n Fieles discpulos de Jess, \n Unos s, otros no; \n Logran coronas en vez de cruz; \n //: Y t? Y yo?:// \n Mora el Rey en gloriosa luz, \n Con l no puede haber dolor, \n De alguien es esta beatitud, \n //: Y t? Y yo?:// \n     -3- \n Llegan a tiempo, pasando bien, \n Unos s, otros no; \n Estos las puertas cerradas ven; \n //: Y t? Y yo?:// \n Ciegos y sordos hoy nada creen,\n  Tarde lamentarn tal error; \n Al que desdean ser su Juez;\n  //: Y t? Y yo?:// \n  -4- \n Son herederos del porvenir, \n Unos s, otros no; \n Los que procuran con Dios vivir; \n //: Y t? Y yo?:// \n Cuando concluya la dura lid, \n En compaa del Salvador; \n Alguien ser sin cesar, feliz; \n //: Y t? Y yo?:// \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:272,title:"  272.- USA MI VIDA ",anthem:"        272.- USA MI VIDA \n \n     Muchos, que viven en tu derredor, \n Solos y tristes estn; \n T, por tu vida les puedes llevar, \n Gozo, paz y bendicin. \n     CORO \n Usa mi vida, usa mi vida\n  Para tu gloria, mi Jess; \n Todos los das y hoy quiero ser \n Un siervo tuyo, Seor, por doquier. \n     -2- \n Di a los tristes, que Dios es amor; \n l quiere dar salvacin. \n A los que vienen a Cristo Jess, \n Buscando paz y perdn. \n     -3- \n Toda tu vida hoy rinde al Seor, \n Para serle siempre fiel; \n Otros que vean en ti su amor, \n Pronto se rindan a El. \n ",track:"1qCPnFIoEtPXxFg-vhMJxufttUGDqVLEs",demo:"1kOhpzxDC7raZ5bQW8paqLM_rR-fUIQob"},
{id:273,title:"  273.- VAMOS A LA ESCUELA  ",anthem:"        273.- VAMOS A LA ESCUELA \n \n     Hoy es santo sbado, venimos Seor, \n Va a empezar la escuela, danos fe y amor. \n     CORO \n Gloria, gloria a Cristo, por la santa Biblia, \n El libro bendito, nuestra norma y fe. \n     -2- \n  Cual Mara, estamos a los pies de Cristo, \n S, aprenderemos como ella aprendi. \n   -3- \n A nuestros maestros dales luz, saber, \n A nosotros danos, deseos de aprender. \n     -4- \n  Termin la escuela, vamos a partir, \n Que Dios nos conceda otra vez venir. \n ",track:"14nkUCONT2y0r3dnpfbZk72hgeARVepmz",demo:"18NVZNW4Ogf4MbTA-v3aFBrZVtFNEuA3J"},
{id:274,title:"  274.- VAGAS TRISTE?   ",anthem:"        274.- VAGAS TRISTE? \n \n     Vagas triste y angustiado? \n Buscas tu solaz? \n Ven a M, te dice Cristo. \n Y halla paz. \n     -2- \n Hay seales que me indican \n Que mi gua l es? \n Las heridas de sus manos \n Y sus pies. \n     -3- \n Hay corona que le adorna \n Si es Rey para m? \n S, corona, mas de espinas \n Hay all \n      -4- \n Si le busco, si le sigo, \n Cul ser mi don? \n Del pecado, del infierno, \n Redencin. \n     -5- \n Si le pido que me salve, \n Me recibir? \n Puesto que por ti l ha muerto, \n Salvar. \n     -6- \n Hoy por fe, la vida eterna \n Me ha de dar a m? \n Su palabra te responde: \n S, hoy s. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:275,title:"  275.- VED QUE ACERCANDOSE  ",anthem:"              275.- VED QUE ACERCNDOSE \n \n     Ved que acercndose el da va, \n En que el Seor en su gloria vendr, \n A recoger en su alfol, \n Lo que sembramos en tanto aqu; \n l su fruto recoger, \n S, su fruto recoger. \n     CORO \n Vamos sembrando con vivo amor, \n Dulces palabras del buen Salvador; \n Obrando siempre, con celo y con fe, \n Para que rica cosecha nos d. \n     -2- \n Entre zarzales podr caer \n Cierta semilla y no florecer; \n Mas, el Seor a segar vendr, \n Y su buen fruto recoger, \n l, su fruto recoger, \n S, su fruto recoger. \n     -3- \n Muy largo tiempo podr pasar \n Antes de ver la semilla brotar; \n Mas ciertamente el Seor vendr, \n Y su buen fruto recoger, \n l, su fruto recoger, \n S, su fruto recoger. \n     -4- \n En todo tiempo, sin desmayar, \n Vamos sembrando y orando a la par; \n Pues prestamente el Seor vendr, \n Y su buen fruto recoger \n l, su fruto recoger, \n S, su fruto recoger. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:276,title:"  276.- VEN A CRISTO  ",anthem:"        276.- VEN A CRISTO \n \n     Mientras oro y mientras ruego, \n Mientras sientes conviccin, \n Mientras Dios derrama el fuego, \n Ven amigo, a Cristo, ven. \n        CORO \n Ven a l, Ven l, \n Dale hoy tu corazn; \n Ven a l, ven a l, \n Dale hoy tu corazn. \n     -2- \n Has vagado en este mundo \n Sin tranquilidad, sin paz? \n Vuelve a Dios, y en l, confiando. \n Salvo y feliz sers. \n     -3- \n Si en tu vida has fracasado, \n Y tu alma triste est, \n Cree en Cristo, y tu pecado \n Hoy l mismo borrar. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:277,title:"  277.- VEN A EL. VEN A EL  ",anthem:"        277.- VEN A EL, VEN A EL! \n \n     De Jess, no escuchas tierno llamamiento? \n Ven a M, pecador. \n Quiere darte su perdn, paz y contento, \n Ven a l, pecador. \n     CORO \n Te llama con un tierno acento, \n Tu vida quiere redimir. \n Oye del Seor el tierno llamamiento; \n Ven a M, ven a M. \n     -2- \n De tus penas, pronto puedes olvidarte; \n Ven a l, ven a l; \n Porque de ellas, Cristo, alivio puede darte, \n Ven a l, ven a l; \n     -3- \n Slo l, puede pleno gozo concederte, \n Ven a l, ven a l; \n En odiosa cruz, por ello, vio la muerte, \n Ven a l, ven a l; \n     -4- \n No su voz de amor, escuches con desprecio; \n Ven a l, ven l. \n Por tu salvacin pag divino precio, \n Ven a l, ven a l. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:278,title:"  278.- VEN ALMA QUE LLORAS  ",anthem:"  278.- VEN, ALMA QUE LLORAS \n \n     Ven, alma que lloras, ven al Salvador, \n En tus tristes horas, dile tu dolor; \n Ven, dile tu duelo; ven tal como ests, \n Habla sin recelo y no llores ms. \n     -2- \n Tu pena y tristura, dile a tu Seor; \n Toda desventura, engao y error, \n En su tierno seno, descanso hallars; \n Ven, porque l es bueno, y no llores ms. \n     -3- \n Ven, y di al cansado, que acepte la cruz, \n Gua al angustiado, hacia el buen Jess; \n La bendita nueva de celeste paz, \n A los tristes lleva, y no llores ms. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:279,title:"  279.- VENID PASTORCILLOS ",anthem:"        279.- VENID, PASTORCILLOS \n \n     Venid, pastorcillos, venid a adorar \n Al Rey de los cielos, que nace en Jud; \n Sin ricas ofrendas podemos llegar, \n Que el Nio prefiere la fe y la bondad. \n     -2- \n Un rstico techo, abrigo le da, \n Por cuna un pesebre, por templo un portal; \n En lecho de paja incgnito est, \n Quien quiso a los astros, su gloria prestar. \n     -3- \n Hermoso lucero, le vino a anunciar, \n Y magos de Oriente, buscndole van, \n Delante se postran, del Rey de Jud; \n De incienso, oro y mirra, tributo le dan. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:280,title:"  280.- VE. SEMBRADOR  ",anthem:"        280.- VE, SEMBRADOR \n \n     Sal a sembrar, sembrador de paz, \n Sigue las huellas del buen Jess; \n Muy ricos frutos tendrs, si fiel, \n Sigues la senda de paz y luz. \n       CORO \n Ve, ve, ve sembrador; \n Ve, ve, siembra la paz, \n Habla doquiera del Seor, \n Y de su santa paz. \n     -2- \n No desperdicies el tiempo, ve, \n Siembra palabras de vida y paz; \n Semilla eterna, que d su mies, \n Rica semilla, que no es fugaz. \n     -3- \n Dios lo ha mandado, sal a sembrar; \n Nuevas de vida, de amor y paz, \n Tal vez te cueste dolores mil, \n Mas en los cielos tendrs solaz. \n ",track:"1487ImTrP1Ig7siv8TkxinBjvpBXQSmw9",demo:"1YejOWEzwyUdrhXgijr4RuDi_w3H3MKcN"},
{id:281,title:"  281.- VIBRE UN HIMNO TRIUNFAL  ",anthem:"        281.- VIBRE UN HIMNO TRIUNFAL \n \n     Vibre un himno triunfal y que Cuba, \n Las cadenas arroje por fin, \n De los vicios y errores siniestros, \n Que por los siglos la han hecho infeliz! \n Vibre el himno que d a las conciencias, \n La suprema, final redencin. \n Esparciendo las nclitas notas, \n Del glorioso evangelio de amor! \n     CORO \n Del glorioso evangelio de amor, \n Del glorioso evangelio de amor, \n Bajo el cual, venturosa la patria, \n Alce libre su egregio pendn. \n     -2- \n Del pasado, las densas tinieblas, \n Slo dieron infiel ceguedad, \n Mas, doquiera la luz resplandece, \n Va infundiendo pavor a Satn, \n Y ya en Cuba del orto al ocaso, \n Hoy resuena de Cristo la voz, \n Fulgurando en las vivas antorchas, \n Del glorioso evangelio de amor. \n     -3- \n Oh, cristianos de Cuba, alcemos \n Nuestras armas: La Biblia y la cruz \n Y a la patria brindaremos por siempre, \n Los laureles de paz y salud! \n Y los vicios y errores sucumban, \n Mientras van los soldados de Dios, \n Tremolando las ureas banderas; \n Del glorioso evangelio de amor. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:282,title:"  282.- VOLUNTARIOS DE JESUS  ",anthem:"        282.- VOLUNTARIOS DE JESS \n \n     Jess est buscando voluntarios hoy, \n Que a la ruda lucha luego puedan ir; \n Quin est dispuesto a escuchar su voz, \n Siendo voluntario listo a combatir? \n        CORO \n  De Cristo, voluntario t puedes ser; \n Otros ya se alistan, hazlo t, \n Cristo es nuestro jefe, no hay por qu temer, \n Quieres ser un voluntario de Jess? \n     -2- \n Nos cercan las tinieblas densas del error, \n Vamos sobre abismos hondos de maldad, \n Y para destruirlos llama el Salvador \n Muchos voluntarios que amen la verdad. \n     -3- \n La lucha es contra el vicio, la pereza, el mal, \n Contra la ignorancia de la ley de Dios; \n Es una campaa que no tiene igual, \n Quieres ir a ella de Jess en pos? \n     -4- \n El triunfo significa que domine el bien, \n Que los hombres se amen, y que la verdad, \n Reine en las conciencias, siendo su sostn, \n Y ha de ser, si ayudas, una realidad. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:283,title:"  283.- VOZ DE GRATITUD   ",anthem:"        283.- VOZ DE GRATITUD \n \n     La gloria de Cristo el Seor, cantar, \n Pues llena mi vida de gozo y de paz; \n Callar los favores que de l alcanc, \n Mi labio no puede jams. \n  CORO \n Es todo bondad para m, \n Con l, nada puedo desear; \n Pues todos mis altos anhelos aqu, \n Tan slo l, los puede llenar. \n     -2- \n En horas de angustia conmigo l est, \n Y puedo escuchar su dulcsima voz, \n Que me habla, y su paz inefable me da, \n La paz infinita de Dios. \n     -3- \n Si a rudos conflictos me mira que voy, \n Me deja hasta el fin, a m solo luchar, \n Mas pronto si ve que cediendo, yo estoy,\n  Socorro me viene a prestar. \n     -4- \n Tambin cuando gozo, lo miro llegar, \n Y entonces mi dicha, la aumenta el Seor; \n Ya llena mi copa, la veo rebosar \n Con todos sus dones de amor. \n ",track:"1GXfAu7ilO4H62KBAPeE4O1N6Pd_Zacjr",demo:"1Nj5aOLern0ahPEWNMIoXG8uJK6Pett1l"},
{id:284,title:"  284.- YA NO ESTOY SOLO  ",anthem:"        284.- YA NO ESTOY SOLO \n \n     Cmo puedo yo sentirme solo, \n Si est conmigo, Jess mi Rey? \n Fiando en l, disfruto paz, consuelo, \n Porque soy suyo y mo es l. \n     CORO \n No, nunca puedo sentirme solo, \n Si Cristo es mi constante amigo fiel; \n No, nunca puedo sentirme solo, \n Si Cristo es mi constante amigo fiel. \n     -2- \n Cmo puedo yo sentirme solo \n En mis afanes y en mi dolor? \n A su seno protector, yo vuelvo, \n Y all me ampara con tierno amor. \n     -3- \n Al cruzar el valle de la muerte, \n Las negras sombras, no temer; \n Pues me sostendr su brazo fuerte, \n Con l en gloria, yo reinar. \n ",track:"1tAlS3by7q_THwzI4WZgVT70vOiYKyJw6",demo:"1PYzqUi3R9iW1hHwbxA3YcbioRzZdUWFA"},
{id:285,title:"  285.- YA SALVO SOY  ",anthem:"     285.- YA SALVO SOY! \n \n     Mis culpas todas borr Jess, \n Con infinito amor; \n Carg con ellas en cruenta cruz, \n En medio del mortal dolor. \n     CORO \n Salvo por l, yo soy. \n Salvo por su poder, \n A vida nueva, Jess me ha llevado, \n Ya salvo soy! \n     -2- \n En densas sombras, anduve yo; \n Cuando en maldad viv; \n Y mi alma nunca la paz hall, \n Ni gozo alguno yo sent. \n     -3- \n Gloriosa vida de libertad,\n Disfruto yo por l; \n Ya no hay temores, no hay ansiedad, \n Porque l, me guarda siempre fiel. \n     -4- \n Eterno canto en mi corazn, \n Elevo al Redentor; \n Negar no puedo su salvacin, \n Negar no puedo, no, su amor. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:286,title:"  286.- YA TODO DEJE   ",anthem:"        286.- YA TODO DEJ \n \n     Tan triste y tan lejos de Dios me sent, \n Y sin el perdn de Jess, \n Mas, cuando su voz amorosa o, \n Que dijo: Oh, ven a la luz. \n     CORO \n Ya todo dej para andar en la luz; \n No moro en tinieblas ya ms; \n Ya todo dej por seguir a Jess, \n Y vivo en la luz de su faz. \n     -2- \n Qu amigo tan dulce, es el tierno Jess; \n Tan lleno de paz y de amor! \n De todo este mundo es la flgida luz, \n El nombre del buen Salvador. \n     -3- \n De mi alma el anhelo por siempre ser, \n Ms cerca vivir de la cruz; \n Do santo poder y pureza me da, \n La sangre de Cristo Jess.\n      -4- \n Oh! ven a Jess, infeliz pecador: \n No vagues a ciegas ya ms; \n S, ven a Jess, nuestro gran Salvador, \n Pues en l salvacin hallars. \n ",track:"1P0IpVKjDAhOpfAjFaviTn7MoXL6g4zKS",demo:"1v5EpSvKOrOvhEcm5fvr2HnW9j1zmlcUn"},
{id:287,title:"  287.- YO ANDO CON CRISTO  ",anthem:"        287.- YO ANDO CON CRISTO \n \n Muy triste en pecado y en noche me vi, \n Mas ya vivo alegre, a Cristo volv, \n Y todos los das yo canto a Jess; \n Yo ando con Cristo y tengo su luz. \n     CORO \n Yo voy con el Rey, Aleluya! \n Yo ando con Cristo, el Rey; \n No vago ya ms, al cielo me voy, \n Tan slo de Cristo yo soy. \n     -2- \n Por aos y aos, sin Cristo viv, \n Y nunca en la vida, feliz yo me vi; \n Mas hoy, con las aves elevo mi voz, \n Y canto feliz, pues del Rey voy en pos \n     -3- \n Oh, alma que ests en el valle sin luz, \n Si miras arriba vers a Jess! \nVen alto, ms cerca, ven pronto al Seor! \n Es Cristo tu luz y tu fiel Salvador. \n ",track:"1UoQjz8c8zMRw_i075M98rNzJ6xqjoVDq",demo:"1Y9nS4wh_nDR_BdqpmM5St8MgtPt7DUgO"},
{id:288,title:"  288.- YO DARE MIS DIEZMOS  ",anthem:"        288.- YO DAR MIS DIEZMOS \n \n     Para la causa de mi Seor, \n Para su Iglesia salva por l, \n Para su via cristiana y fiel, \n Dar mis diezmos, con gran amor. \n     CORO \n Mis diezmos todos yo voy a dar. \n Y mis ofrendas de gratitud; \n Por su potencia, por su virtud, \n Mi Dios harlos multiplicar. \n     -2- \n En mis pobrezas y en mi afliccin, \n En mi abundancia y prosperidad, \n Solemnemente, con gran lealtad, \n Dar mis diezmos con devocin. \n     -3- \n Soy responsable de cooperar, \n Para la causa de mi Seor; \n Y por lo mismo, con gran amor, \n Diezmos y ofrendas prometo dar. \n     -4- \n A contribuir, me invita el Seor; \n Segn mis fuerzas, segn mi fe, \n Mis diezmos, a Cristo yo le dar, \n l ama a todo feliz dador. \n ",track:"1OaNKqTUjfpz2N56OUX-QzL0TpcwXt6kF",demo:"1Rt0FlD3le1PXFM5udjRq5x1oQiW4HvlJ"},
{id:289,title:"  289.- YO QUIERO SER LIMPIO",anthem:"        289.- YO QUIERO SER LIMPIO \n \n  Yo quiero ser limpio y completo, Jess; \n Deseo por siempre andar en tu luz, \n Mis dolos rompe, los que ayer am, \n Lavado ms blanco que nieve, ser. \n     CORO \n Ms blanco, s, que la nieve ser; \n Lavado en la Sangre y limpio por fe. \n     -2- \n Oh! mrame desde tu trono de amor, \n Haz mi sacrificio perfecto, Seor, \n Te quiero rendir cuanto soy, cuanto s; \n Lavado y ms blanco que nieve ser. \n     -3- \n Jess, te suplico postrado a tus pies, \n Tu propia perfecta justicia me des, \n Tu sangre expiatoria la veo por fe \n Lavado y ms blanco que nieve ser. \n     -4- \n Por esta pureza doy gracias a Ti, \n Que santificado por tu gracia fui; \n Tu sangre, limpindome vi por la fe; \n Lavado y ms blanco que nieve qued. \n     CORO \n Ms blanco, s, que la nieve qued; \n Lavado en la sangre soy limpio por fe. \n ",track:"1sqmTYABrE0I0swWb9UNkBXODbDtN0njJ",demo:"1QuPnK3kKrpop-62oBo6io72h1x7B18mp"},
{id:290,title:"  290.- YO SE ",anthem:"        290.- YO S \n \n     Yo s en quien yo tengo fe, \n En quien confo yo. \n Yo s que Cristo habita en m, \n Y todo en l, es paz. \n     CORO \n Yo s en quien mi fe est, \n Tu sangre me libert; \n Yo s que l me guardar, \n Y su amor, me basta a m. \n     -2- \n Las burlas pueden continuar, \n Del santo amor de Dios. \n Pero jams podrn robar, \n La paz del corazn. \n     -3- \n No pueden revivir el mal \n Que la sangre lav; \n Ni hacer de nuevo un corazn \n De odio y de maldad. \n     -4- \n Yo s, que lo que prometi, \n No fallar jams; \n Por tanto el mundo pecador, \n Jams me vencer. \n ",track:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:291,title:"  291.- YO TENGO UNA ESPERANZA ",anthem:"        291.- YO TENGO UNA ESPERANZA \n \n     // :Yo tengo una esperanza \n Muy grande y muy firme, \n Que all en las mansiones \n Nos hemos de ver : // \n El da ya se acerca, seales ya se ven; \n Que Cristo est a las puertas, \n Diciendo: Ven y ve. \n     CORO \n Cristo bendito del cielo, \n Es el que me ha de ayudar, \n Para yo ir a la gloria, \n Donde yo quiero morar; \n Yo soy embajador \n De Cristo, el Salvador, \n Me voy a preparar, \n A predicar su amor. \n Que Cristo est a las puertas \n Diciendo: Ven y ve. \n     -2- \n // : Pero, si algn da \n Me llegara mi cita, \n Que Dios me necesita, \n Yo tengo que ir :// \n Por eso he confiado \n Como un fiel soldado, \n Que all en las mansiones \n Nos hemos de ver. \n     -3- \n // : Cuando yo me vaya \n De este triste mundo, \n Con gozo profundo \n Lo voy a dejar : // \n Y un grato recuerdo \n De mis aflicciones, \n Que all en la mansiones \n Nos hemos de ver. \n     -4- \n // : Doy gracias a Cristo, \n Por esta esperanza, \n Que slo la alcanzan \n Los que creen en l : // \n Y henchidos de gozo \n Nuestros corazones, \n All en las mansiones \n Nos hemos de ver. \n ",track:"16xmIQf7m5MLHJHjw3Tr-7r7e7YePAn8Q",demo:"1k45x0vfPtA1TW6ALVy5W0CDtAHF5WxHN"},
{id:292,title:"  292.- YO TE SEGUIRE  ",anthem:"            292.- YO TE SEGUIR \n \n     Yo te seguir, Maestro, \n Donde quieras que gues, \n Do T vayas, yo te sigo, \n S, Jess, te seguir. \n     CORO \n Yo te seguir, Maestro. \n Pues tu sangre me salv; \n Y aunque todos te desprecien, \n Siempre, fiel, yo te ser. \n     -2- \n Do la senda sea dura, \n Yo te seguir, Jess, \n T marcaste el camino, \n Y feliz lo seguir. \n     -3- \n Aunque sea muy oscuro, \n El camino a seguir, \n Si tu voz escucho guiando \n Sin temor avanzar. \n     -4- \n Y si el mundo me desprecia, \n No me importa, Salvador, \n Pues recuerdo el desprecio \n Que tuviste, T, Seor. \n ",track:"1z1cBzgtWW2V1leM_VPc3IbrZb4VsxOXL",demo:"1g1gYYMJaSGeDEx2dRy-0caXjr_RvWeCe"},
{id:293,title:"  293.- YO VAGABA EN EL PECADO ",anthem:"        293.- YO VAGABA EN EL PECADO \n \n     Yo vagaba en el pecado, \n Sin esperanza y sin Dios; \n Mas, Jess, me ha salvado \n De este mundo tan atroz. \n     CORO \n Y yo s, s, yo s \n Mi Jess acoge, al ms vil pecador. \n Y yo s, s, yo s \n Mi Jess acoge, al ms vil pecador. \n     -2- \n Pecador, Jess te llama, \n l te quiere perdonar; \n Con amor su voz te invita: \n Ven, te har yo descansar. \n  -3- \n Aunque seas t muy malo, \n Salvacin hay para ti; \n Pues salv al ms malvado, \n Cuando me salvo a m. \n     -4- \n Hallars perdn en Cristo, \n l es vida; l es luz. \n Pecador, no te detengas, \n Ven al buen tierno Jess. \n",track:"1tZAFTC6mgUhXxnewwKdov8OI8xIMf3sQ",demo:"1e2o08al--6fpadcTXQJHCN5gMBoLRVQM"},
{id:'salida',title:"  SALIDA DEL SOL ",anthem:"        SALIDA DEL SOL \n \n     Yo vagaba en el pecado, \n Sin esperanza y sin Dios; \n Mas, Jess, me ha salvado \n De este mundo tan atroz. \n     CORO \n Y yo s, s, yo s \n Mi Jess acoge, al ms vil pecador. \n Y yo s, s, yo s \n Mi Jess acoge, al ms vil pecador. \n     -2- \n Pecador, Jess te llama, \n l te quiere perdonar; \n Con amor su voz te invita: \n Ven, te har yo descansar. \n  -3- \n Aunque seas t muy malo, \n Salvacin hay para ti; \n Pues salv al ms malvado, \n Cuando me salvo a m. \n     -4- \n Hallars perdn en Cristo, \n l es vida; l es luz. \n Pecador, no te detengas, \n Ven al buen tierno Jess. \n",track:"1tZAFTC6mgUhXxnewwKdov8OI8xIMf3sQ",demo:"1e2o08al--6fpadcTXQJHCN5gMBoLRVQM"}]
